import { ChangeEventHandler, useCallback, useReducer, useState } from 'react';
import { TextField, Button } from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { FormattedMessage, useIntl } from 'react-intl';
import { useSelector } from 'react-redux';
import lockIcon from '../../../assets/icons/icons8-lock.svg';
import padlockIcon from '../../../assets/icons/icons8-padlock.svg';
import ConfigurationLock from '../../../protobuf/ConfigurationLock';
import type Proxy from '../../../protobuf/Proxy';
import { NavigationItemType } from '../NavigationItem';
import { AlertDialog } from './AlertDialog';
import { TakeoverDialog } from './TakeoverDialog';

const formReducer = (state: any, event: any) => {
  return {
    ...state,
    [event.name]: event.value,
  };
};

export const useLockStateHandlerNavigation = (): NavigationItemType => {
  const intl = useIntl();
  const proxy = useSelector<any>((state) => state.app.proxy) as Proxy;
  const configLock = useSelector<any>((state) => state.app.configurationLock) as ConfigurationLock;
  const loggedUser = useSelector<any>((state) => state.app.user);
  const [formData, setFormData] = useReducer(formReducer, { scope: 1 });

  const [openFormDialog, setOpenFormDialog] = useState(false);

  const getLabel = useCallback(() => {
    if (configLock.isLockedBySameUser(loggedUser)) {
      return intl.messages['topNavigation.release'];
    } else if (configLock.isLockedByOtherUser(loggedUser)) {
      return intl.messages['topNavigation.takeover'];
    } else {
      return intl.messages['topNavigation.lock'];
    }
  }, [configLock, intl.messages, loggedUser]);

  const handleChange: ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement> = (event) => {
    setFormData({
      name: event.target.name,
      value: event.target.value,
    });
  };

  const handleClose: React.MouseEventHandler<HTMLButtonElement> = (event) => {
    event.stopPropagation();
    setOpenFormDialog(false);
  };

  const handleSubmit: React.MouseEventHandler<HTMLButtonElement> = (event) => {
    event.preventDefault();
    event.stopPropagation();
    setOpenFormDialog(false);
    formData.requestTakeover = configLock.isLockedByOtherUser(loggedUser);
    proxy.subscribeToLockAcquisitionStream(formData);
  };

  return {
    id: 'lock-state-handler',
    icon: configLock.isLockedBySameUser(loggedUser) ? padlockIcon : lockIcon,
    enabled: true,
    content: (
      <>
        <div className="lock-state-handler">
          <p>{getLabel()}</p>
        </div>
        <Dialog open={openFormDialog} onClose={handleClose} aria-labelledby="form-dialog-title">
          <DialogTitle id="form-dialog-title">
            {configLock.isLockedByOtherUser(loggedUser) ? (
              <FormattedMessage id="lockStateHandler.formDialog.takeoverConfigRequestTitle" />
            ) : (
              <FormattedMessage id="lockStateHandler.formDialog.lockConfigRequestTitle" />
            )}
          </DialogTitle>
          <DialogContent>
            <DialogContentText>
              <FormattedMessage id="lockStateHandler.formDialog.lockConfigRequestContent" />
            </DialogContentText>
            <TextField
              InputLabelProps={{ shrink: true }}
              autoFocus
              margin="dense"
              id="lock-scope-id"
              name="scope"
              label={<FormattedMessage id="lockStateHandler.formDialog.lockScope" />}
              type="number"
              defaultValue="1"
              disabled={true}
              onChange={handleChange}
              fullWidth
            />
            <TextField
              InputLabelProps={{ shrink: true }}
              margin="dense"
              id="lock-description-id"
              name="description"
              label={<FormattedMessage id="lockStateHandler.formDialog.lockDescription" />}
              type="text"
              onChange={handleChange}
              fullWidth
            />
            {configLock.isLockedByOtherUser(loggedUser) && (
              <>
                <TextField
                  InputLabelProps={{ shrink: true }}
                  margin="dense"
                  id="takeover-message-id"
                  name="takeoverMessage"
                  label={<FormattedMessage id="lockStateHandler.formDialog.takeoverMessage" />}
                  type="text"
                  onChange={handleChange}
                  fullWidth
                />
              </>
            )}
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              <FormattedMessage id="cancel" />
            </Button>
            <Button onClick={handleSubmit} color="primary">
              <FormattedMessage id="submit" />
            </Button>
          </DialogActions>
        </Dialog>
        <AlertDialog
          open={configLock.isConfigAlreadyLocked()}
          title={
            <FormattedMessage
              id="lockStateHandler.alertDialog.lockAlreadyLocked"
              values={{ user: configLock.owner }}
            />
          }
          contentText={
            <FormattedMessage
              id="lockStateHandler.alertDialog.lockDescription"
              values={{ description: configLock.lockDescription }}
            />
          }
        >
          <Button
            onClick={() => {
              proxy.closeAlertDialogs();
            }}
            color="primary"
          >
            <FormattedMessage id="close" />
          </Button>
        </AlertDialog>
        <AlertDialog
          open={configLock.isOwnerRejectedTakeover()}
          title={
            <FormattedMessage
              id="lockStateHandler.alertDialog.ownerRejectedTakeover"
              values={{ user: configLock.owner }}
            />
          }
          contentText={
            <FormattedMessage
              id="lockStateHandler.alertDialog.lockDescription"
              values={{ description: configLock.lockDescription }}
            />
          }
        >
          <Button
            onClick={() => {
              proxy.closeAlertDialogs();
            }}
            color="primary"
          >
            <FormattedMessage id="close" />
          </Button>
        </AlertDialog>
        <AlertDialog
          open={configLock.isWaitingForTakeoverResponse()}
          title={
            <FormattedMessage
              id="lockStateHandler.alertDialog.waitingForTakeoverResponse"
              values={{ user: configLock.owner }}
            />
          }
          contentText={
            <FormattedMessage id="lockStateHandler.alertDialog.waitingForResponseInfo" />
          }
        />
        <TakeoverDialog
          open={configLock.isTakeoverAttemptFromOtherUser()}
          title={
            <FormattedMessage
              id="lockStateHandler.alertDialog.takeoverAttempt"
              values={{ user: configLock.takeoverUser }}
            />
          }
          contentText={
            <FormattedMessage
              id="lockStateHandler.alertDialog.takeoverMessage"
              values={{ message: configLock.takeoverMessage }}
            />
          }
          onAccept={() => {
            proxy.passConfigLock({
              lockId: configLock.lockId,
              takeoverId: configLock.takeoverId,
            });
          }}
          onReject={() => {
            proxy.retainConfigLock({ takeoverId: configLock.takeoverId });
          }}
        />
      </>
    ),
    onClick: () => {
      if (
        !openFormDialog &&
        !configLock.isConfigAlreadyLocked() &&
        !configLock.isOwnerRejectedTakeover() &&
        !configLock.isWaitingForTakeoverResponse() &&
        !configLock.isTakeoverAttemptFromOtherUser()
      ) {
        if (configLock.isLockedBySameUser(loggedUser)) {
          proxy.unsubscribeFromLockAcquisitionStream();
        } else {
          setOpenFormDialog(true);
        }
      }
    },
  };
};
