import { useMemo } from 'react';
import { FormattedMessage } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import type Proxy from '../../../protobuf/Proxy';
import { setCurrentMode } from '../../../redux/actions';
import modes from '../../../utils/modes';
import { canEditSVOMode, canReadSVOMode } from '../../../utils/permissions';
import { NavigationItemType } from '../NavigationItem';

const selectableModes = Object.keys(modes).filter(
  (m) => m !== 'unknownMode',
) as (keyof typeof modes)[];

export const useModeSwitchNavigation = (): NavigationItemType | undefined => {
  const dispatch = useDispatch();

  const isSVOConfigLoaded = useSelector<any>((state) => state.viewSet.isSVOConfigLoaded);
  const { myPermissions, currentMode, proxy } = useSelector<any>((state) => state.app) as {
    myPermissions: Set<string>;
    currentMode: string;
    proxy: Proxy;
  };

  const canRead = canReadSVOMode(myPermissions);
  const canEdit = canEditSVOMode(myPermissions);

  const currentModeKey = useMemo(() => {
    const modeId = currentMode ?? proxy.mode ?? modes.unknownMode.id;
    return Object.keys(modes).find(
      (modeKey) => modes[modeKey as typeof selectableModes[number]].id === modeId,
    ) as keyof typeof modes;
  }, [currentMode, proxy.mode]);

  const children = useMemo(() => {
    const changeMode = (_modeKey: string, modeId: string) => {
      dispatch(setCurrentMode(modeId));
    };

    return selectableModes.map<NavigationItemType>((key) => ({
      id: key,
      onClick: () => {
        changeMode(key, modes[key].id);
      },
      content: <FormattedMessage id={key} />,
      icon: modes[key].iconSrc,
      enabled: true,
    }));
  }, [dispatch]);

  if (!isSVOConfigLoaded || (!canRead && !canEdit)) {
    return undefined;
  }

  return {
    id: 'mode-switch',
    icon: modes[currentModeKey]?.iconSrc,
    content: <FormattedMessage id={currentModeKey} />,
    enabled: canEdit,
    children,
  };
};
