import LZString from 'lz-string';
import { reviverJsonToMap, replacerMapToJson } from '../utils/jsonMapper';
import {
  CONFIG_INFO,
  CONFIGURATION_CHUNK_TEMPLATE,
  CONFIGURATION_CHUNK_LIST,
} from '../utils/localStorage';
import logger from '../utils/logger';
import { isObjectValid } from '../utils/objectValidator';
import { chunkSubstr } from '../utils/stringChunker';
import Protobuf from './Protobuf';

const extractConfigName = (filename) => {
  if (!filename) {
    return '';
  }
  const slashIdx = filename.lastIndexOf('\\');
  if (slashIdx !== -1) {
    filename = filename.substr(slashIdx + 1);
  }
  const dotIdx = filename.lastIndexOf('.');
  if (dotIdx !== -1) {
    filename = filename.substr(0, dotIdx);
  }
  return filename;
};

const getConfigName = async (protobuf) => {
  const request = Protobuf.SvPb.GetConfigurationInfoRequest.create();
  const { fileName } = await protobuf.request(request);
  return extractConfigName(fileName);
};

const getInspections = async (protobuf) => {
  try {
    const inspectionsReq = Protobuf.SvPb.GetInspectionsRequest.create({});
    const inspectionsRes = await protobuf.request(inspectionsReq);
    return (inspectionsRes.inspections || []).map((inspection) => ({
      id: inspection.id,
      name: inspection.name,
    }));
  } catch (e) {
    // ignore error. will fall back to empty inspection name list.
    logger.error('Error while fetching inspection name list:', e);
    return [];
  }
};

const getInspectionIds = (inspections) => {
  return (inspections || []).map((inspection) => inspection.id);
};

const getInspectionNames = (inspections) => {
  return (inspections || []).map((inspection) => inspection.name);
};

// objectIDIndex has the format:
//   {#123}
// and sometimes also an index at the end:
//   {#123}[10]
// or this for empty arrays
//   {#123}[ ]
// except of the index at the end, it is a normal id
const parseObjectIdIndex = (str) => {
  if (!str.startsWith('{#')) {
    return;
  }
  const endIdx = str.indexOf('}');
  if (endIdx < 0) {
    return;
  }
  // TODO parse array index
  str = str.substr(2, endIdx);
  return parseInt(str, 10);
};

const cleanObjectSelectorNodeRecursively = (node) => {
  const { name, type, location, objectIDIndex, children, selected } = node;
  const cleanedChildren = (children || []).map(cleanObjectSelectorNodeRecursively);
  const id = (objectIDIndex && parseObjectIdIndex(objectIDIndex)) || null;
  return {
    id,
    name,
    type,
    location,
    selected,
    children: cleanedChildren,
  };
};

const getIdNameMappingForInspection = async (protobuf, inspectionId, inspectionName) => {
  const req = Protobuf.SvPb.GetObjectSelectorItemsRequest.create({
    areas: [Protobuf.SvPb.SearchArea.toolsetItems],
    inspectionID: inspectionId,
    instanceID: 0, // InvalidObjectId
    attribute: Protobuf.SvPb.ObjectAttributes.viewable,
    wholeArray: true,
    type: Protobuf.SvPb.ObjectSelectorType.allValueObjects,
    attributesAllowed: 1,
  });
  const res = await protobuf.request(req);
  if (!res.tree) {
    logger.error('GetObjectSelectorItemsRequest response does not contain a tree');
    return null;
  }

  const tree = cleanObjectSelectorNodeRecursively(res.tree);

  const entries = new Map();
  const queue = (tree && tree.children && tree.children.slice(0)) || [];
  while (queue.length > 0) {
    const head = queue.shift();
    const { id, name, type, location, children } = head;
    if (id) {
      entries.set(id, {
        id,
        name,
        type,
        location,
        fullLocation: 'Inspections.' + inspectionName + '.' + location,
      });
    }
    if (children && children.length) {
      for (let i = 0; i < children.length; ++i) {
        queue.push(children[i]);
      }
    }
  }

  return entries;
};

const getObjectSelectorItemsRequest = async (protobuf, inspectionId, requestParameters) => {
  const req = Protobuf.SvPb.GetObjectSelectorItemsRequest.create(requestParameters);
  let res;
  try {
    res = await protobuf.request(req);
  } catch (error) {
    logger.error(error);
    return null;
  }

  if (!res.tree) {
    logger.error('GetObjectSelectorItemsRequest response does not contain a tree');
    return null;
  }

  const tree = cleanObjectSelectorNodeRecursively(res.tree);
  return { tree };
};

const getObjectSelectorItemsForInspection = async (protobuf, inspectionId) => {
  return getObjectSelectorItemsRequest(protobuf, inspectionId, {
    areas: [Protobuf.SvPb.SearchArea.toolsetItems],
    inspectionID: inspectionId,
    instanceID: 0, // InvalidObjectId
    attribute: Protobuf.SvPb.ObjectAttributes.viewable,
    wholeArray: true,
    type: Protobuf.SvPb.ObjectSelectorType.allValueObjects,
    attributesAllowed: 1,
  });
};

const getRemotelySettableObjectSelectorItemsForInspection = async (protobuf, inspectionId) => {
  return getObjectSelectorItemsRequest(protobuf, inspectionId, {
    areas: [Protobuf.SvPb.SearchArea.toolsetItems],
    inspectionID: inspectionId,
    instanceID: 0, // InvalidObjectId
    attribute: Protobuf.SvPb.ObjectAttributes.remotelySettable,
    wholeArray: true,
    type: Protobuf.SvPb.ObjectSelectorType.allValueObjects,
    attributesAllowed: 1,
  });
};

const getObjectSelectorImagesForInspection = async (protobuf, inspectionId) => {
  return getObjectSelectorItemsRequest(protobuf, inspectionId, {
    areas: [Protobuf.SvPb.SearchArea.toolsetItems],
    inspectionID: inspectionId,
    type: Protobuf.SvPb.ObjectSelectorType.allImageObjects,
    attribute: Protobuf.SvPb.ObjectAttributes.archivableImage,
    attributesAllowed: 1,
  });
};

const getVersion = async (protobuf, requestClass) => {
  const request = requestClass.create();
  const response = await protobuf.request(request);
  if (!response.version) {
    logger.error(`Error while reading version via ${requestClass.name}`);
    return 'Unknown Version';
  }
  return response.version;
};

const getPerInspection = async (protobuf, inspectionIds, inspectionNames, fn) => {
  const itemsByInspectionId = new Map();
  for (let i = 0; i < inspectionIds.length; ++i) {
    const inspectionId = inspectionIds[i];
    const inspectionName = inspectionNames[i];
    const item = await fn(protobuf, inspectionId, inspectionName);
    if (item) {
      itemsByInspectionId.set(inspectionId, item);
    }
  }
  return itemsByInspectionId;
};

const getConfigurationTree = async (protobuf) => {
  const request = Protobuf.SvPb.GetConfigurationTreeRequest.create();
  const response = await protobuf.request(request);
  if (!response || !response.tree) {
    logger.error('Cannot get config tree');
    return false;
  }
  return response;
};

const mapInspectionIdtoToolset = async (protobuf) => {
  const inspectionToolsetMappings = new Map();

  const configurationTree = await getConfigurationTree(protobuf);
  if (!isObjectValid(() => configurationTree.tree.children)) {
    logger.error('configTree is not valid');
    return inspectionToolsetMappings;
  }

  await Promise.all(
    configurationTree.tree.children.map(async (inspection) => {
      const inspectionId = inspection.objectID;
      if (!inspectionId) {
        logger.error(`Inspection without valid id: ${inspectionId}`);
        return;
      }

      let toolSetId = 0;
      try {
        toolSetId = await getToolSetId(protobuf, inspectionId, inspection.name);
      } catch (e) {
        logger.error(`Could not load tool set id for ${inspection.name} (${inspectionId})`);
        return;
      }

      inspectionToolsetMappings.set(inspectionId, { ...inspection, toolSetId });
    }),
  );

  return inspectionToolsetMappings;
};

const getToolSetId = async (protobuf, inspectionId, inspectionName) => {
  const request = Protobuf.SvPb.InspectionCmdRequest.create({
    inspectionID: inspectionId || undefined,
    getObjectIdRequest: {
      name: `${inspectionName}.Tool Set`,
    },
  });

  try {
    const response = await protobuf.request(request);
    return response?.getObjectIdResponse?.objectId;
  } catch (error) {
    logger.error(error);
  }
};
const getGlobalConstants = async (protobuf, inspectionIds) => {
  const constants = [];
  if (!inspectionIds || inspectionIds.length === 0) {
    return constants;
  }
  const reqObj = {
    areas: [Protobuf.SvPb.SearchArea.globalConstantItems],
    inspectionID: inspectionIds[0],
    type: Protobuf.SvPb.ObjectSelectorType.allValueObjects,
    attribute: Protobuf.SvPb.ObjectAttributes.viewable,
    attributesAllowed: 1,
  };

  const req = Protobuf.SvPb.GetObjectSelectorItemsRequest.create(reqObj);
  const res = await protobuf.request(req);

  (res?.tree?.children || []).forEach((childLevel1) => {
    if (childLevel1.location === 'Global') {
      (childLevel1.children || []).forEach((childLevel2) => {
        constants.push(childLevel2.location);
      });
    }
  });

  return constants;
};
export default class Configuration {
  static async loadFromCache() {
    try {
      let configString = '';
      const chunksString = localStorage.getItem(CONFIGURATION_CHUNK_LIST);
      const chunkList = chunksString.split(',');

      configString = chunkList.map((key) => localStorage.getItem(key)).join();
      const decompressedConfig = LZString.decompress(configString);
      const config = JSON.parse(decompressedConfig, reviverJsonToMap);

      return new Configuration({
        ...config,
      });
    } catch (err) {
      logger.error({ err }, 'Error loading configuration');
      return null;
    }
  }

  static async saveToCache(configuration, configInfo) {
    const stringifiedConfig = LZString.compress(JSON.stringify(configuration, replacerMapToJson));
    const configChunks = chunkSubstr(stringifiedConfig, 1000000); // 1m chars = 1MB chunks
    let configChunkNames = [];

    for (let i = 0; i < configChunks.length; i++) {
      const chunk = configChunks[i];
      const chunkName = `${CONFIGURATION_CHUNK_TEMPLATE}${i}`;
      localStorage.setItem(chunkName, chunk);
      configChunkNames.push(chunkName);
    }

    localStorage.setItem(CONFIGURATION_CHUNK_LIST, configChunkNames.join(','));
    localStorage.setItem(CONFIG_INFO, JSON.stringify(configInfo));
  }

  static extractConfigNameFromFileName(filename) {
    return extractConfigName(filename);
  }

  async discoverInspectionsWithConfigName(protobuf) {
    if (!protobuf || !protobuf.isConnected()) {
      logger.error('Protobuf is invalid!');
      return;
    }

    try {
      const promises = await Promise.all([getConfigName(protobuf), getInspections(protobuf)]);

      const [configName, inspections] = promises;

      this.name = configName;
      this.inspections = inspections;
      this.inspectionIds = getInspectionIds(inspections);
      this.inspectionNames = getInspectionNames(inspections);
    } catch (err) {
      logger.error({ err }, 'Error while discovering configuration');
      return;
    }
  }

  async discoverPartsInBackground(protobuf) {
    if (!protobuf || !protobuf.isConnected()) {
      logger.error('Protobuf is invalid!');
      return;
    }

    try {
      const promises = await Promise.all([
        getVersion(protobuf, Protobuf.SvPb.GetSVObserverVersionRequest),
        getVersion(protobuf, Protobuf.SvPb.GetGatewayVersionRequest),
        getVersion(protobuf, Protobuf.SvPb.GetWebAppVersionRequest),
        getPerInspection(
          protobuf,
          this.inspectionIds,
          this.inspectionNames,
          getIdNameMappingForInspection,
        ),
        getPerInspection(
          protobuf,
          this.inspectionIds,
          this.inspectionNames,
          getObjectSelectorItemsForInspection,
        ),
        getPerInspection(
          protobuf,
          this.inspectionIds,
          this.inspectionNames,
          getRemotelySettableObjectSelectorItemsForInspection,
        ),
        getPerInspection(
          protobuf,
          this.inspectionIds,
          this.inspectionNames,
          getObjectSelectorImagesForInspection,
        ),
        mapInspectionIdtoToolset(protobuf),
        getGlobalConstants(protobuf, this.inspectionIds),
      ]);
      const [
        svObserverVersion,
        gatewayVersion,
        webAppVersion,
        idNameMappings,
        objectSelectorItems,
        remotelySettableObjectSelectorItems,
        objectSelectorImages,
        inspectionToolsetMappings,
        globalConstants,
      ] = promises;

      this.versions = { svObserverVersion, gatewayVersion, webAppVersion };
      this.idNameMappings = idNameMappings;
      this.objectSelectorItems = objectSelectorItems;
      this.remotelySettableObjectSelectorItems = remotelySettableObjectSelectorItems;
      this.objectSelectorImages = objectSelectorImages;
      this.inspectionToolsetMappings = inspectionToolsetMappings;
      this.globalConstants = globalConstants;
    } catch (err) {
      logger.error({ err }, 'Error while discovering configuration');
      return;
    }
  }

  constructor(config) {
    this.name = '';
    this.inspections = [];
    this.inspectionIds = [];
    this.inspectionNames = [];
    this.idNameMappings = new Map();
    this.objectSelectorItems = new Map();
    this.remotelySettableObjectSelectorItems = new Map();
    this.objectSelectorImages = new Map();
    this.versions = {};
    this.inspectionToolsetMappings = new Map();
    this.globalConstants = [];

    if (Object.keys(config).length !== 0) {
      // copy all provided fields into this.
      // this allows treating the configuration instance like a
      // normal json object, but with additional helper functions.
      Object.assign(this, config);
    }
  }

  getSvObserverVersion() {
    return this.versions && this.versions.svObserverVersion;
  }

  getGatewayVersion() {
    return this.versions && this.versions.gatewayVersion;
  }

  getWebAppVersion() {
    return this.versions && this.versions.webAppVersion;
  }

  getInspections() {
    return this.inspections;
  }

  getInspectionName(inspectionId) {
    const inspection = (this.inspections || []).find((i) => i.id === inspectionId);
    return inspection && inspection.name;
  }

  getValueNameForId(inspectionId, id) {
    if (typeof inspectionId === 'string') {
      inspectionId = parseInt(inspectionId, 10);
    }
    const mappings = this.idNameMappings.get(inspectionId);
    if (mappings) {
      if (typeof id === 'string') {
        id = parseInt(id, 10);
      }
      const mapping = mappings.get(id);
      if (mapping) {
        return mapping.fullLocation;
      }
    }
    return null;
  }

  async setIdNameMappings(protobuf) {
    if (!protobuf || !protobuf.isConnected()) {
      logger.error('Protobuf is invalid!');
      return;
    }

    if (
      this.idNameMappings.size === 0 &&
      this.inspectionIds.length !== 0 &&
      this.inspectionNames.length !== 0
    ) {
      const mappings = await getPerInspection(
        protobuf,
        this.inspectionIds,
        this.inspectionNames,
        getIdNameMappingForInspection,
      );
      this.idNameMappings = mappings;
    } else {
      logger.warn('setIdNameMappings: id name mappings already set');
    }
  }

  getObjectSelectorItemsForInspection(inspectionId) {
    return this.objectSelectorItems.get(inspectionId);
  }

  async setObjectSelectorItems(protobuf) {
    if (!protobuf || !protobuf.isConnected()) {
      logger.error('Protobuf is invalid!');
      return;
    }

    if (
      this.objectSelectorItems.size === 0 &&
      this.inspectionIds.length !== 0 &&
      this.inspectionNames.length !== 0
    ) {
      const objectSelectorItems = await getPerInspection(
        protobuf,
        this.inspectionIds,
        this.inspectionNames,
        getObjectSelectorItemsForInspection,
      );
      this.objectSelectorItems = objectSelectorItems;
    } else {
      logger.warn('setObjectSelectorItems: object selector items already set');
    }
  }

  getRemotelySettableObjectSelectorItemsForInspection(inspectionId) {
    return this.remotelySettableObjectSelectorItems.get(inspectionId);
  }

  getObjectSelectorImagesForInspection(inspectionId) {
    return this.objectSelectorImages.get(inspectionId);
  }

  async setObjectSelectorImages(protobuf) {
    if (!protobuf || !protobuf.isConnected()) {
      logger.error('Protobuf is invalid!');
      return;
    }

    if (
      this.objectSelectorImages.size === 0 &&
      this.inspectionIds.length !== 0 &&
      this.inspectionNames.length !== 0
    ) {
      const objectSelectorImages = await getPerInspection(
        protobuf,
        this.inspectionIds,
        this.inspectionNames,
        getObjectSelectorImagesForInspection,
      );
      this.objectSelectorImages = objectSelectorImages;
    } else {
      logger.warn('setObjectSelectorImages: object selector images already set');
    }
  }

  async setInspectionIdToToolsetMappings(protobuf) {
    if (this.inspectionToolsetMappings.size === 0) {
      this.resetInspectionIdToToolsetMappings(protobuf);
    } else {
      logger.warn('setInspectionIdToToolsetMappings: mappings already done');
    }
  }

  async resetInspectionIdToToolsetMappings(protobuf) {
    if (!protobuf || !protobuf.isConnected()) {
      logger.error('Protobuf is invalid!');
      return;
    }

    const mappings = await mapInspectionIdtoToolset(protobuf);
    this.inspectionToolsetMappings = mappings;
  }

  async updateInspectionIdToToolsetMapping(protobuf, inspectionId) {
    if (!protobuf || !protobuf.isConnected()) {
      logger.error('Protobuf is invalid!');
      return;
    }

    const configurationTree = await getConfigurationTree(protobuf);
    if (!isObjectValid(() => configurationTree.tree.children)) {
      logger.error('configTree is not valid');
      return;
    }

    for (const inspection of configurationTree.tree.children) {
      const id = inspection.objectID;
      if (id === inspectionId) {
        let toolSetId = 0;
        try {
          toolSetId = await getToolSetId(protobuf, id, inspection.name);
        } catch (e) {
          logger.error(`Could not load tool set id for ${inspection.name} (${id})`);
          return;
        }

        this.inspectionToolsetMappings.set(inspectionId, { ...inspection, toolSetId });
        return;
      }
    }
  }

  // Currently filters array data from values
  // TODO: array data should be shown in tree, but id must be changed (all have same id atm)
  filterArrayValuesFromTree = (tree) => {
    let stack = [],
      node,
      ii;
    stack.push(tree);
    while (stack.length > 0) {
      node = stack.pop();
      if (!node?.id && node?.children?.length) {
        let firstChildId = '';
        for (ii = 0; ii < node.children.length; ii += 1) {
          if (firstChildId === node.children[ii].id) {
            node.children = [];
            node.id = firstChildId;
            break;
          }
          if (node.children[ii].id) {
            firstChildId = node.children[ii].id;
          }
        }
        for (ii = 0; ii < node.children.length; ii += 1) {
          stack.push(node.children[ii]);
        }
      }
    }
    return tree;
  };
}
