import { useSelector } from 'react-redux';

import ActionBar, { ActionBarType } from '../../components/ActionBar';
import Container from '../../components/layout/Container';

import { canReadParameterView, canEditParameterView } from '../../utils/permissions';
import { Overlay } from './components/Overlay';

import './style.scss';

interface ParameterViewProps {
  viewActions?: ActionBarType[];

  id: string;
  inspectionId: string;
}

const ParameterView = ({ id, inspectionId, viewActions = [] }: ParameterViewProps) => {
  const myPermissions = useSelector<any>((state) => state.app?.myPermissions) as Set<string>;
  const parameter = useSelector<any>((state) => state.toolSet?.parameter) as
    | { component: React.ReactNode }
    | undefined;

  if (canReadParameterView(myPermissions) || canEditParameterView(myPermissions)) {
    return (
      <div className="parameter-view-wrapper">
        <Container style={!viewActions?.length ? { marginBottom: 0 } : {}}>
          {parameter?.component || <Overlay inspectionId={inspectionId} id={id} />}
        </Container>
        {!!viewActions?.length && <ActionBar isViewAction actions={viewActions} />}
      </div>
    );
  }
  return <div></div>;
};

export default ParameterView;
