import { useMemo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import languageIcon from '../../../assets/icons/language.svg';
import { addUserSwitchedLanguageNotification, setLocale } from '../../../redux/actions';
import { languages } from '../../../translations';
import { NavigationItemType } from '../NavigationItem';

export const useLanguageChooserNavigation = (): NavigationItemType => {
  const dispatch = useDispatch();
  const locale = useSelector<any>((state) => state.app.locale) as string;

  const children = useMemo<NavigationItemType[]>(
    () =>
      languages.map((language) => ({
        id: language.locale,
        content: language.name,
        onClick: () => {
          dispatch(setLocale(language.locale));
          dispatch(addUserSwitchedLanguageNotification(language.locale));
        },
        enabled: true,
      })),
    [dispatch],
  );

  return {
    id: 'language-chooser',
    icon: languageIcon,
    content: locale ?? '',
    enabled: true,
    children,
  };
};
