import React, { ReactNode, useState } from 'react';
import Button from '@material-ui/core/Button';
import { FormattedMessage } from 'react-intl';
import infoIcon from '../../../../assets/icons/info.svg';
import { AlertDialog } from '../../../../views/TopNavigation/navigation/AlertDialog';
import IconButton from '../../../buttons/IconButton';

export interface InformationBoxProps {
  onClose?: () => void;
  open?: boolean;
  informationText: string | ReactNode;
  title?: string;
}

const InformationBox: React.FC<InformationBoxProps> = ({
  onClose,
  informationText,
  open,
  title,
}) => {
  const [showingInfo, setShowingInfo] = useState(open || false);
  return (
    <>
      <div>
        <IconButton
          tooltip="Show information"
          onClick={() => {
            setShowingInfo(true);
          }}
          iconUrl={infoIcon}
        />
      </div>
      <AlertDialog open={showingInfo} contentText={informationText} title={title}>
        <Button
          onClick={() => {
            setShowingInfo(false);
            onClose && onClose();
          }}
          color="primary"
        >
          <FormattedMessage id="close" />
        </Button>
      </AlertDialog>
    </>
  );
};

export default InformationBox;
