import { useEffect } from 'react';
import { TextField, InputAdornment, IconButton } from '@material-ui/core';
import { MuiThemeProvider, createTheme, makeStyles } from '@material-ui/core/styles';
import { useDispatch, useSelector } from 'react-redux';
import ClearIcon from '../../../../assets/icons/clear.svg';
import { OutlinedSelection } from '../../../../components/OutlinedSelection';
import Configuration from '../../../../protobuf/Configuration';
import type Proxy from '../../../../protobuf/Proxy';
import { setSaveFlag } from '../../../../redux/actions';
import { canEditParameterView } from '../../../../utils/permissions';
import { OverlaySchema } from '../../../../utils/reduxHelper';

const useStyles = makeStyles(() => ({
  header: {
    fontSize: '1.8rem',
    fontWeight: 'bold',
    marginBottom: 30,
  },
  input: {
    height: 37,
    fontSize: '1.4rem',
  },
  label: {
    fontSize: '1.4rem',
    transform: 'translate(14px, 11px) scale(1)',
  },
}));

const theme = createTheme({
  typography: {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    //@ts-ignore
    useNextVariants: true,
    htmlFontSize: 10,
  },
});

interface OverlayPropertyListProps {
  inspectionId: string;
  viewId: string;
  currentOverlay: OverlaySchema;
  imageDimensions?: { width: number; height: number };
  onChange: (changedValue: Partial<OverlaySchema>) => void;
  openSelection: (variableName: string, variableId: keyof OverlaySchema) => void;
}

export const OverlayPropertyList = ({
  inspectionId,
  currentOverlay,
  imageDimensions,
  onChange,
  openSelection,
}: OverlayPropertyListProps) => {
  const proxy = useSelector<any>((state) => state.app?.proxy) as Proxy;
  const configuration = useSelector<any>((state) => state.app?.configuration) as Configuration;
  const myPermissions = useSelector<any>((state) => state.app?.myPermissions) as Set<string>;
  const dispatch = useDispatch();

  const classes = useStyles();

  useEffect(() => {
    if (configuration.idNameMappings.size === 0) {
      configuration.setIdNameMappings(proxy.protobuf).then(() => {
        proxy.dispatchCurrentConfiguration(configuration);
      });
    }
  }, [configuration, proxy]);

  const imageWidth = imageDimensions?.width ?? 0;
  const imageHeight = imageDimensions?.height ?? 0;

  const minWidth = Number(-imageWidth * 2);
  const maxWidth = Number(imageWidth * 2);

  const minHeight = Number(-imageHeight * 2);
  const maxHeight = Number(imageHeight * 2);

  const canEdit = canEditParameterView(myPermissions);

  const handleChange = (changedValue: Partial<OverlaySchema>) => {
    dispatch(setSaveFlag(true));
    onChange(changedValue);
  };

  type CommonFieldProps = {
    name: string;
    id: keyof OverlaySchema;
    disabled: boolean;
  };

  const renderTextField = ({ name, id, disabled }: CommonFieldProps) => {
    return (
      <TextField
        label={name}
        style={{ width: '100%' }}
        id={id}
        type="text"
        variant="outlined"
        disabled={disabled || !currentOverlay}
        InputProps={{ classes: { input: classes.input } }}
        InputLabelProps={{ classes: { root: classes.label } }}
        value={(currentOverlay && currentOverlay[id]) || ''}
        onChange={(e) => handleChange({ [id]: e.target.value })}
      />
    );
  };

  const renderNumberField = ({
    name,
    id,
    min,
    max,
    disabled,
  }: CommonFieldProps & { min: number; max: number }) => {
    const value = (currentOverlay && currentOverlay[id]) || '';
    const trimToMinMax = (min: number, max: number, value: string) => {
      let valueNumber = Number(value);
      if (valueNumber > max) {
        valueNumber = max;
      }
      if (valueNumber < min) {
        valueNumber = min;
      }

      return valueNumber;
    };
    return (
      <TextField
        label={name}
        style={{ width: '100%' }}
        id={id}
        type="number"
        variant="outlined"
        disabled={disabled || !currentOverlay}
        InputProps={{
          classes: { input: classes.input },
          inputProps: { min, max },
        }}
        InputLabelProps={{ classes: { root: classes.label } }}
        value={value}
        onChange={(e) => handleChange({ [id]: trimToMinMax(min, max, e.target.value) })}
      />
    );
  };

  const renderSelection = ({
    name,
    id,
    options,
    disabled,
  }: CommonFieldProps & { options: string[] }) => {
    return (
      <OutlinedSelection
        selectedValue={currentOverlay && currentOverlay[id]}
        values={options}
        onSelectionChanged={handleChange}
        name={name}
        id={id}
        disabled={disabled || !currentOverlay}
      />
    );
  };

  const renderObjectSelector = ({ name, id, disabled }: CommonFieldProps) => {
    const value =
      (currentOverlay &&
        currentOverlay[id] &&
        configuration.getValueNameForId(inspectionId, currentOverlay[id])) ||
      '';

    return (
      <TextField
        label={name}
        style={{ width: '100%' }}
        id={id}
        type="text"
        variant="outlined"
        disabled={disabled || !currentOverlay}
        InputProps={{
          classes: { input: classes.input },
          endAdornment: (
            <InputAdornment position="end">
              <IconButton
                edge="end"
                aria-label="clear"
                onClick={() => handleChange({ [id]: '' })}
                title={id}
              >
                <img src={ClearIcon} alt="x" />
              </IconButton>
            </InputAdornment>
          ),
        }}
        InputLabelProps={{ classes: { root: classes.label } }}
        value={value}
        onFocus={() => openSelection(name, id)}
      />
    );
  };

  return (
    <div className="parameter-view-overlay-overlaypropertylist">
      <MuiThemeProvider theme={theme}>
        <label className={classes.header}>Overlay parameters</label>
        {renderTextField({ name: 'Label', id: 'label', disabled: !canEdit })}

        {renderNumberField({
          name: 'X',
          id: 'x',
          min: minWidth,
          max: maxWidth,
          disabled: !canEdit,
        })}
        {renderObjectSelector({ name: 'X Variable', id: 'xVariable', disabled: !canEdit })}

        {renderNumberField({
          name: 'Offset Left',
          id: 'offsetLeft',
          min: minWidth,
          max: maxWidth,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: 'Offset Left Variable',
          id: 'offsetLeftVariable',
          disabled: !canEdit,
        })}

        {renderNumberField({
          name: 'Y',
          id: 'y',
          min: minHeight,
          max: maxHeight,
          disabled: !canEdit,
        })}
        {renderObjectSelector({ name: 'Y Variable', id: 'yVariable', disabled: !canEdit })}

        {renderNumberField({
          name: 'Offset Top',
          id: 'offsetTop',
          min: minHeight,
          max: maxHeight,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: 'Offset Top Variable',
          id: 'offsetTopVariable',
          disabled: !canEdit,
        })}

        {renderNumberField({
          name: 'Width',
          id: 'width',
          min: minWidth,
          max: maxWidth,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: 'Width Variable',
          id: 'widthVariable',
          disabled: !canEdit,
        })}

        {renderNumberField({
          name: 'Height',
          id: 'height',
          min: minHeight,
          max: maxHeight,
          disabled: !canEdit,
        })}
        {renderObjectSelector({
          name: 'Height Variable',
          id: 'heightVariable',
          disabled: !canEdit,
        })}
        {renderTextField({ name: 'Color', id: 'color', disabled: !canEdit })}
        {renderObjectSelector({
          name: 'Color Variable',
          id: 'colorVariable',
          disabled: !canEdit,
        })}

        {renderSelection({
          name: 'Line Style',
          id: 'lineStyle',
          options: ['solid', 'dotted', 'dashed'],
          disabled: !canEdit,
        })}
      </MuiThemeProvider>
    </div>
  );
};
