import React from 'react';
import Button from '@material-ui/core/Button';

import './style.scss';

interface ListSelectionProps {
  headline?: React.ReactNode;
  selectionItems?: {
    id: number | string;
    text: React.ReactNode;
    icon: string;
    enabled: boolean;
  }[];
  showCancel?: boolean;
  onSelection: (itemId: string | number) => void;
  cancelButtonProps?: {
    text?: React.ReactNode;
    icon?: string;
    goBack?: () => void;
  };
}

const ListSelection = ({
  headline = '',
  selectionItems = [],
  onSelection,
  cancelButtonProps = {},
  showCancel = false,
}: ListSelectionProps) => (
  <div>
    <h3>{headline}</h3>
    <ul className="list-wrapper">
      {selectionItems.map((item) => (
        <li
          className="selection-item"
          key={`li${item.id}`}
          style={{
            pointerEvents: item.enabled ? 'auto' : 'none',
            opacity: item.enabled ? '1.0' : '0.3',
          }}
        >
          <Button variant="outlined" key={`button${item.id}`} onClick={() => onSelection(item.id)}>
            {item.icon && <img src={item.icon} alt="inspection.name" className="image-item" />}
            {item.text}
          </Button>
        </li>
      ))}
      {showCancel ? (
        <li className="selection-item" key="liCancel">
          <Button variant="contained" color="primary" onClick={cancelButtonProps.goBack}>
            {cancelButtonProps.icon && (
              <img src={cancelButtonProps.icon} alt="type.id" className="image-item" />
            )}
            {cancelButtonProps.text}
          </Button>
        </li>
      ) : null}
    </ul>
  </div>
);

export default ListSelection;
