import { ReactNode, useRef, useState } from 'react';
import { ClickAwayListener, Paper, Popper } from '@material-ui/core';
import clsx from 'clsx';

import './NavigationItem.scss';

export interface NavigationItemType {
  id: string;
  icon?: string;
  className?: string;
  content: ReactNode;
  onClick?: () => void;
  enabled: boolean;
  children?: (NavigationItemType | null)[];
}

interface NavigationItemProps {
  item: NavigationItemType;
  nested?: boolean;
}

export const NavigationItem = ({ item, nested }: NavigationItemProps) => {
  const itemRef = useRef<HTMLDivElement>(null);

  const [openMenu, setOpenMenu] = useState(false);

  const hasChildren = !!item.children?.length;

  return (
    <>
      <div
        className={clsx('navigation-item', item.className)}
        ref={itemRef}
        style={{
          pointerEvents: item.enabled ? 'auto' : 'none',
          opacity: item.enabled ? '1.0' : '0.3',
        }}
      >
        <button
          className={clsx(
            hasChildren && 'show-arrow',
            nested && 'nested arrow-right',
            openMenu && 'active',
            (hasChildren || item.onClick) && 'clickable',
          )}
          onClick={() => {
            if (item.onClick) {
              item.onClick();
            } else if (hasChildren) {
              setOpenMenu(true);
            }
          }}
        >
          {item.icon && <img src={item.icon} />}
          <span>{item.content}</span>
        </button>
      </div>
      <Popper
        open={openMenu}
        anchorEl={itemRef.current}
        placement={nested ? 'right-start' : 'bottom'}
        style={{ zIndex: 9 }}
      >
        <ClickAwayListener onClickAway={() => setOpenMenu(false)}>
          <Paper
            elevation={4}
            className="navigation-item-children-list"
            style={{ minWidth: itemRef.current?.getBoundingClientRect().width ?? 0 }}
          >
            {(item.children ?? []).map(
              (child) => child && <NavigationItem key={child.id} item={child} nested />,
            )}
          </Paper>
        </ClickAwayListener>
      </Popper>
    </>
  );
};
