import { makeStyles } from '@material-ui/core';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import { CommonEditorProps } from './common';
import './BooleanEditor.scss';

const useStyles = makeStyles({
  disabled: {
    color: 'darkblue !important',
    fontWeight: 'bold',
  },
});

export const BooleanEditor = ({ value, onChange }: CommonEditorProps<boolean>) => {
  const classes = useStyles();

  return (
    <div className="boolean-editor">
      <ToggleButtonGroup
        color="primary"
        value={!!value}
        exclusive
        onChange={(_, value) => onChange(!!value)}
      >
        <ToggleButton disabled={value} value={true} classes={classes}>
          TRUE
        </ToggleButton>
        <ToggleButton disabled={!value} value={false} classes={classes}>
          FALSE
        </ToggleButton>
      </ToggleButtonGroup>
    </div>
  );
};
