import React, { ReactNode, useState } from 'react';
import Button from '@material-ui/core/Button';
import { useSelector, useDispatch } from 'react-redux';
import deleteIcon from '../../assets/icons/delete.svg';
import { saveDatabaseGatewayConfiguration } from '../../redux/actions';
import { SvPb } from '../../types/protobuf';
import ModalNotification from '../ModalNotification.js';
import { OutlinedSelection } from '../OutlinedSelection';
import IconButton from '../buttons/IconButton';
import BatchSettings from './components/BatchSettings';
import TriggerSettings from './components/TriggerSettings';

import './style.scss';

export interface AppConfiguration {
  globalConstants: Record<string, any>;
  inspectionSubscriptions: Array<InspectionSubscriptionConfig>;
  inspections: Array<Inspection>;
  objectSelectorItems: Map<Inspection['id'], SvPb.GetObjectSelectorItemsResponse>;
  idNameMappings: Map<Inspection['id'], { fullLocation: string }>;
  filterArrayValuesFromTree: <T>(val: T) => T;
  getObjectSelectorImagesForInspection: <T>(inspectionId: Inspection['id']) => T;
}

export interface Inspection {
  name: string;
  id: number;
}

export interface RootState {
  app: {
    databaseGatewayConfig: DatabaseGatewayConfig;
    configuration: AppConfiguration;
    protobuf: Record<string, unknown>;
  };
}

export interface DatabaseGatewayConfig {
  inspectionSubscriptions: Array<InspectionSubscriptionConfig>;
}

export interface InspectionSubscriptionConfig {
  url?: string;
  inspection: string;
  batch: Partial<BatchConfig>;
  trigger: Partial<TriggerConfig>;
}

export interface BatchConfig extends DataTriggerConfig {
  comment: string;
  batchname: string;
  inspectionname: string;
  sectionid: string;
  productionspeed: string;
  timestamp: string;
  headerid: number;
}

interface DataAttributes {
  enabled: boolean;
  name: string;
  value: string;
}

type ResultNames = 'result1' | 'result2' | 'result3';
type ImageNames = 'image1' | 'image2' | 'image3' | 'image4';
type DataNames =
  | 'data1'
  | 'data2'
  | 'data3'
  | 'data4'
  | 'data5'
  | 'data6'
  | 'data7'
  | 'data8'
  | 'data9'
  | 'data10'
  | 'data11'
  | 'data12'
  | 'data13'
  | 'data14'
  | 'data15'
  | 'data16'
  | 'data17'
  | 'data18'
  | 'data19';

type DataTriggerConfig = Partial<Record<DataNames, DataAttributes>>;
type ImageTriggerConfig = Partial<Record<ImageNames, TriggerImageConfig>>;
type ResultTriggerConfig = Partial<Record<ResultNames, DataAttributes>>;
type DataImageTriggerConfig = DataTriggerConfig & ImageTriggerConfig & ResultTriggerConfig;

export interface TriggerConfig extends DataImageTriggerConfig {
  storeCondition: string;
  revolution: string;
}

export interface TriggerImageConfig {
  enabled: boolean;
  samplingRate: number; //  0.10
  image: string; // "${IMAGE:Inspections.C11 Fingergrip.Tool Set.Image}"
  path: string; // "${CONSTANT:Global.Product Name}/${DATE:YYYY-MM-DD}/${CONSTANT:Global.Batch Name}/Reject-${VALUE:Inspections.C11 Fingergrip.Tool Set.1010 M reject condition.Passed}/${trigger}-${name}.bmp"
}

const generateDefaultInspectionSubscription = (inspectionName = '') => ({
  inspection: inspectionName,
  batch: {},
  trigger: {},
});

export type TriggerVariablesType = SvPb.GetObjectSelectorItemsResponse['tree'] | string[] | null;

const DatabaseGatewayEditor = () => {
  const dispatch = useDispatch();
  const configuration = useSelector<RootState, DatabaseGatewayConfig>(
    (state) => state.app.databaseGatewayConfig || { inspectionSubscriptions: [] },
  );

  const webAppConfiguration = useSelector<RootState, AppConfiguration>(
    (state) => state.app.configuration,
  );
  const protobuf = useSelector<RootState>((state) => state.app.protobuf);
  const [update, setUpdate] = useState<boolean>(false);

  const batchVariables: string[] = [];
  if (webAppConfiguration && webAppConfiguration.globalConstants) {
    (webAppConfiguration.globalConstants || []).forEach((constant: string) => {
      batchVariables.push('${CONSTANT:' + constant + '}');
    });
  }

  const allowedImagePathVariables = [...batchVariables];
  allowedImagePathVariables.push('${trigger}');
  allowedImagePathVariables.push('${name}');
  allowedImagePathVariables.push('${DATE:YYYY-MM-DD}');

  const saveConfiguration = () => {
    simplifyConfiguration();
    dispatch(saveDatabaseGatewayConfiguration(protobuf, configuration));
  };

  const simplifyConfiguration = () => {
    configuration.inspectionSubscriptions.forEach((subscription) => {
      if (subscription?.batch) {
        const batch = subscription.batch;
        for (const [key, value] of Object.entries(batch)) {
          if (typeof value === 'object' && value.name === '' && value.enabled !== false) {
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            batch[key] = value.value;
          }
        }
      }
    });
  };

  const getInspectionNames = () => {
    const names: string[] = [];
    (webAppConfiguration?.inspections || []).forEach((inspection) => {
      names.push(inspection.name);
    });
    return names;
  };

  const getInspectionId = (inspectionName: string) => {
    const filteredInspections = webAppConfiguration.inspections.filter(
      (inspection) => inspection.name === inspectionName,
    );
    if (filteredInspections.length > 0) {
      return filteredInspections[0].id;
    }

    return -1;
  };

  const getTriggerVariablesAsTree = (inspectionId: Inspection['id']) => {
    const items = webAppConfiguration?.objectSelectorItems.get(inspectionId) || [];
    let tree: SvPb.GetObjectSelectorItemsResponse['tree'] = 'tree' in items ? items.tree : null;
    tree = webAppConfiguration.filterArrayValuesFromTree<typeof tree>(tree);

    return tree;
  };

  const getTriggerVariables = (inspectionId: Inspection['id']) => {
    const variables: string[] = [];
    const mapping = webAppConfiguration?.idNameMappings.get(inspectionId);

    if (mapping) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      mapping.forEach((value) => {
        variables.push('${VALUE:' + value.fullLocation + '}');
      });
    }
    return variables;
  };

  const getImageVariables = (inspectionId: Inspection['id']) => {
    const { tree } =
      webAppConfiguration?.getObjectSelectorImagesForInspection<SvPb.GetObjectSelectorItemsResponse>(
        inspectionId,
      ) || {
        tree: {},
      };
    return tree;
  };

  const deleteSubscription = (idx: number) => {
    configuration.inspectionSubscriptions.splice(idx, 1);
    setUpdate(!update);
  };

  const inspectionNames = getInspectionNames();

  const renderConfig = () => {
    const subscriptions: ReactNode[] = [];
    configuration.inspectionSubscriptions.forEach((subscription, idx) => {
      let triggerVariablesAsTree: ReturnType<typeof getTriggerVariablesAsTree & string[]> = null;
      let triggerVariables: string[] = [];
      let allowedImageVariables = null;
      if (webAppConfiguration && webAppConfiguration.inspections) {
        const inspectionId = getInspectionId(subscription.inspection);
        triggerVariablesAsTree = getTriggerVariablesAsTree(inspectionId);
        triggerVariables = getTriggerVariables(inspectionId);
        allowedImageVariables = getImageVariables(inspectionId);
      }

      subscriptions.push(
        <div
          key={`subscription_${subscription.inspection}_${idx}`}
          className="database-gateway-fields-container"
        >
          <div>
            <IconButton
              tooltip="Add property"
              onClick={() => deleteSubscription(idx)}
              iconUrl={deleteIcon}
            />
          </div>
          <div className="field-container">
            <div key={`inspection_name_wrapper`} className="header">
              Inspection:
            </div>
            <div className="field-settings">
              <OutlinedSelection
                selectedValue={subscription.inspection}
                values={inspectionNames}
                onSelectionChanged={(e) => {
                  subscription.inspection = e.inspectionSelection as string;
                  setUpdate(!update);
                }}
                id="inspectionSelection"
              />
            </div>
          </div>
          <div className="field-container">
            <div className="header">Batch:</div>
            <BatchSettings subscription={subscription} allowedVariables={batchVariables} />
          </div>
          <div className="field-container">
            <div className="header">Trigger:</div>
            <TriggerSettings
              subscription={subscription}
              allowedVariables={triggerVariablesAsTree}
              allowedAutoCompleteVariables={triggerVariables}
              allowedImageVariables={allowedImageVariables}
              allowedImagePathVariables={allowedImagePathVariables}
            />
          </div>
        </div>,
      );
    });

    return subscriptions;
  };

  const addNewSubscription = () => {
    if (configuration.inspectionSubscriptions) {
      configuration.inspectionSubscriptions.push(
        generateDefaultInspectionSubscription(inspectionNames[0]),
      );
      saveConfiguration();
    }
    setUpdate(!update);
  };

  return (
    <div className="database-gateway-editor-wrapper">
      <ModalNotification visible={!webAppConfiguration} message={<p>Loading...</p>} />
      <Button variant="contained" color="primary" onClick={() => saveConfiguration()}>
        Save
      </Button>
      {renderConfig()}
      <Button variant="contained" color="primary" onClick={() => addNewSubscription()}>
        Add subscription
      </Button>
    </div>
  );
};

export default DatabaseGatewayEditor;
