import { useEffect, useMemo, useState } from 'react';
import { Button, Slider } from '@material-ui/core';
import clsx from 'clsx';
import ChevronDoubleLeftIcon from '../../../assets/icons/chevron-double-left.svg';
import ChevronLeftIcon from '../../../assets/icons/chevron-left.svg';
import IconButton from '../../../components/buttons/IconButton';
import { RawEditor } from './RawEditor';
import { CommonEditorProps } from './common';

import './NumberEditor.scss';

const toggleNegative = (value: string) =>
  value.startsWith('-') ? value.substring(1) : `-${value}`;

interface NumberEditorProps {
  onlyIntegers?: boolean;
  min?: number;
  max?: number;
  smallStep?: number;
  largeStep?: number;
  hideRawEditor?: boolean;
}

export const NumberEditor = ({
  value,
  onChange,
  onlyIntegers,
  min,
  max,
  smallStep = 1,
  largeStep = 10,
  hideRawEditor,
}: CommonEditorProps<number> & NumberEditorProps) => {
  const [rawValue, setRawValue] = useState('');
  const [invalidInternalValue, setInvalidInternalValue] = useState(false);

  useEffect(() => {
    setRawValue(value?.toString() ?? '');
  }, [value]);

  const clamp = (value: number) => {
    if (typeof min === 'number' && value < min) return min;
    if (typeof max === 'number' && value > max) return max;
    return value;
  };

  const parseRawValue = (raw: string, fix = false) => {
    setRawValue(raw);

    const numberValue = onlyIntegers ? parseInt(raw) : parseFloat(raw);
    if (
      !isNaN(numberValue) &&
      (typeof min !== 'number' || numberValue >= min) &&
      (typeof max !== 'number' || numberValue <= max)
    ) {
      setInvalidInternalValue(false);
      onChange(numberValue);
    } else if (fix) {
      setInvalidInternalValue(false);
      const newValue = clamp(numberValue || 0);
      onChange(newValue);
      setRawValue(newValue.toString());
    } else {
      setInvalidInternalValue(true);
    }
  };

  const keys = useMemo(
    () => [...new Array(10).fill(0).map((_, i) => i.toString()), ...(onlyIntegers ? [] : ['.'])],
    [onlyIntegers],
  );

  return (
    <div className={clsx('number-editor', invalidInternalValue && 'invalid')}>
      {!hideRawEditor && (
        <RawEditor
          numeric
          value={rawValue}
          onChange={parseRawValue}
          onBlur={() => {
            setRawValue(value.toString());
            setInvalidInternalValue(false);
          }}
          exitEditModeOnBlur
        />
      )}
      <div className="slider">
        <div style={{ display: 'flex', columnGap: 8 }}>
          <IconButton
            style={{ height: 24, width: 24 }}
            iconUrl={ChevronDoubleLeftIcon}
            onClick={() => onChange(clamp(value - largeStep))}
          />
          <IconButton
            style={{ height: 24, width: 24 }}
            iconUrl={ChevronLeftIcon}
            onClick={() => onChange(clamp(value - smallStep))}
          />
        </div>
        {typeof min === 'number' && typeof max === 'number' && (
          <div className="slider-widget-container">
            <Slider
              color="primary"
              value={value || 0}
              onChange={(_, value) => onChange((Array.isArray(value) ? value[0] : value) || 0)}
              valueLabelDisplay="auto"
              min={min}
              max={max}
              step={1}
            />
          </div>
        )}
        <div style={{ display: 'flex', columnGap: 8 }}>
          <IconButton
            style={{ height: 24, width: 24, transform: 'scaleX(-1)' }}
            iconUrl={ChevronLeftIcon}
            onClick={() => onChange(clamp(value + smallStep))}
          />
          <IconButton
            style={{ height: 24, width: 24, transform: 'scaleX(-1)' }}
            iconUrl={ChevronDoubleLeftIcon}
            onClick={() => onChange(clamp(value + largeStep))}
          />
        </div>
      </div>
      <div className="keys">
        {(typeof min !== 'number' || min < 0) && (
          <Button
            variant="contained"
            disabled={
              (typeof min === 'number' && parseFloat(toggleNegative(rawValue)) < min) ||
              (typeof max === 'number' && parseFloat(toggleNegative(rawValue)) > max)
            }
            onClick={() => parseRawValue(toggleNegative(rawValue), true)}
          >
            -
          </Button>
        )}
        {keys.map((key) => (
          <Button
            key={key}
            variant="contained"
            disabled={
              (key === '.' && rawValue.includes('.')) ||
              (typeof max === 'number' &&
                '1234567890'.includes(key) &&
                parseFloat(rawValue + key) > max) ||
              (typeof min === 'number' &&
                '1234567890'.includes(key) &&
                parseFloat(rawValue + key) < min)
            }
            onClick={() => {
              if (key === '.' && rawValue.includes('.')) {
                return;
              }
              parseRawValue(rawValue + key, true);
            }}
          >
            {key}
          </Button>
        ))}
        <Button
          variant="contained"
          onClick={() =>
            parseRawValue(rawValue.substring(0, Math.max(0, rawValue.length - 1)), true)
          }
        >
          DEL
        </Button>
      </div>
    </div>
  );
};
