import { NavigationItemType } from '../NavigationItem';
import { useInspectionStateNavigation } from './useInspectionStateNavigation';
import { useLanguageChooserNavigation } from './useLanguageChooserNavigation';
import { useLockStateHandlerNavigation } from './useLockStateHandlerNavigation';
import { useModeSwitchNavigation } from './useModeSwitchNavigation';
import { useStatusReportNavigation } from './useStatusReportNavigation';
import { useUserInfoNavigation } from './useUserInfoNavigation';

import './style.scss';

export const useNavigation = () => {
  const userInfoNavigation = useUserInfoNavigation();
  const languageChooserNavigation = useLanguageChooserNavigation();
  const lockStateHandlerNavigation = useLockStateHandlerNavigation();
  const inspectionStateNavigation = useInspectionStateNavigation();
  const statusReportNavigation = useStatusReportNavigation();
  const modeSwitchNavigation = useModeSwitchNavigation();

  const navigation: (NavigationItemType | null)[] = [
    modeSwitchNavigation ?? (undefined as never),
    statusReportNavigation ?? (undefined as never),
    inspectionStateNavigation ?? (undefined as never),
    null,
    lockStateHandlerNavigation,
    userInfoNavigation,
    languageChooserNavigation,
  ].filter((item) => item !== undefined);

  return { navigation };
};
