import { useEffect, useRef } from 'react';
import { Slider } from '@material-ui/core';
import { RawEditor } from './RawEditor';
import { CommonEditorProps } from './common';
import './AngleEditor.scss';

const MIN = 0;
const MAX = 360;

export const AngleEditor = ({ value, onChange }: CommonEditorProps<number>) => {
  const visualizerElement = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!visualizerElement.current) {
      return;
    }

    const visualizer = visualizerElement.current;

    const update = (x: number, y: number) => {
      const bounds = visualizer.getBoundingClientRect();
      const x2 = ((x - bounds.left) / bounds.width) * 2 - 1;
      const y2 = -(((y - bounds.top) / bounds.height) * 2 - 1);
      const angleRad = Math.atan2(y2, x2);
      if (isNaN(angleRad)) {
        return;
      }
      let angleDeg = (angleRad * 180) / Math.PI;
      while (angleDeg < 0) {
        angleDeg += 360;
      }
      onChange(Math.round(angleDeg));
    };

    const onMouseEvent = (event: MouseEvent) => {
      if (event.buttons === 1) {
        update(event.clientX, event.clientY);
      }
    };

    visualizer.addEventListener('mousemove', onMouseEvent);
    visualizer.addEventListener('mousedown', onMouseEvent);

    return () => {
      visualizer.removeEventListener('mousemove', onMouseEvent);
      visualizer.removeEventListener('mousedown', onMouseEvent);
    };
  }, [onChange]);

  return (
    <div className="angle-editor">
      <RawEditor
        numeric
        value={value.toString()}
        onChange={(stringValue) => {
          const numValue = parseFloat(stringValue);
          if (!isNaN(numValue)) {
            onChange(Math.max(MIN, Math.min(MAX, numValue)));
          }
        }}
        exitEditModeOnBlur
      />
      <div className="slider-widget-container">
        <Slider
          color="primary"
          value={value || 0}
          onChange={(_, value) => onChange((Array.isArray(value) ? value[0] : value) || 0)}
          valueLabelDisplay="auto"
          min={MIN}
          max={MAX}
          step={1}
        />
      </div>
      <div className="angle-visualizer" ref={visualizerElement}>
        <div className="inner">
          <span
            style={{
              transform: `rotate(0deg) skew(${
                -90 + Math.max(0, Math.min(MAX / 4, value))
              }deg) scaleY(-1)`,
            }}
          />
          <span
            style={{
              transform: `rotate(-90deg) skew(${
                -90 + Math.max(0, Math.min(MAX / 4, value - MAX / 4))
              }deg) scaleY(-1)`,
            }}
          />
          <span
            style={{
              transform: `rotate(-180deg) skew(${
                -90 + Math.max(0, Math.min(MAX / 4, value - MAX / 2))
              }deg) scaleY(-1)`,
            }}
          />
          <span
            style={{
              transform: `rotate(-270deg) skew(${
                -90 + Math.max(0, Math.min(MAX / 4, value - (MAX * 3) / 4))
              }deg) scaleY(-1)`,
            }}
          />
        </div>
        <div className="x-axis" />
        <div className="angle-axis" style={{ transform: `rotate(${MAX - value}deg)` }} />
      </div>
    </div>
  );
};
