import { useMemo } from 'react';
import classNames from 'classnames';
import { useIntl } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import iconOverlayList from '../../assets/icons/overlay-list.svg';
import KoerberLogo from '../../assets/logo/logo.svg';
import Logo from '../../components/layout/Logo';
import Configuration from '../../protobuf/Configuration';
import { setTopNavHidden } from '../../redux/actions';
import { NavigationItem } from './NavigationItem';
import DateTime from './components/DateTime';
import LockState from './components/LockState';
import { useNavigation } from './navigation/useNavigation';

import './style.scss';

export const TopNavigation = () => {
  const { topNavHidden, isSVOConfigLoaded, unsavedChanges } = useSelector(
    (state: any) => state.viewSet,
  );
  const configuration = useSelector((state: any) => state.app.configuration) as Configuration;
  const dispatch = useDispatch();

  const { navigation } = useNavigation();

  const intl = useIntl();

  const navClass = classNames({
    nav: true,
    'nav-hidden': topNavHidden,
  });

  const configName = useMemo(() => {
    if (!isSVOConfigLoaded) {
      return intl.messages['noConfigName'];
    }
    if (!configuration || !configuration.name) {
      return intl.messages['unknownConfigName'];
    }
    return <span>{configuration.name}</span>;
  }, [configuration, intl.messages, isSVOConfigLoaded]);

  return (
    <>
      <div className={navClass}>
        <button className="hider" onClick={() => dispatch(setTopNavHidden(!topNavHidden))}>
          {topNavHidden ? 'show' : 'hide'}
        </button>
        <div className="config-wrapper">
          <Logo src={KoerberLogo} title="logo" />
          {unsavedChanges ? <div style={{ fontStyle: 'italic' }}>{configName}*</div> : configName}
        </div>
        <div className="flex-container">
          {navigation.map((item, index) =>
            item ? (
              <NavigationItem key={item.id} item={item} />
            ) : (
              <span key={index} className="spacer" />
            ),
          )}
        </div>
        <div className="responsive-menu">
          <NavigationItem
            key="menu"
            item={{
              id: 'responsive-menu',
              content: 'Menu',
              icon: iconOverlayList,
              enabled: true,
              children: navigation,
            }}
          />
        </div>
        <div className="separator">
          <DateTime />
          <LockState />
        </div>
      </div>
    </>
  );
};
