import { Key, useState } from 'react';
import { SortableContainer, SortableElement, arrayMove, SortEndHandler } from 'react-sortable-hoc';

import './style.scss';
import iconDeleteDustbin from '../../../../assets/icons/delete-dustbin.svg';
import iconDown from '../../../../assets/icons/down.svg';
import iconTakeOver from '../../../../assets/icons/take-over.svg';
import iconUp from '../../../../assets/icons/up.svg';

import ActionBar, { ActionBarType } from '../../../../components/ActionBar';

const SortableItem = SortableElement(({ value }: { value: Key }) => (
  <li className="filter-list-row" key={value}>
    <span className="filter-list-row-item">{value}</span>
  </li>
));

const SortableList = SortableContainer(({ items }: { items: { value: Key }[] }) => (
  <ul>
    {items.map((item, index) => (
      <SortableItem key={item.value} index={index} value={item.value} />
    ))}
  </ul>
));

type AvailableFilterListItemsType = {
  id: string;
  value: string;
  onClick: () => void;
}[];

interface FilterProps {
  AvailableFilterListItems: AvailableFilterListItemsType;
  AvailableFilterListActions: ActionBarType[];
  CurrentFilterListItems: AvailableFilterListItemsType;
  CurrentFilterListActions: ActionBarType[];
}

const Filter = ({
  AvailableFilterListItems = [
    { id: '', value: 'Closing Filter', onClick: () => void 0 },
    { id: '', value: 'Custom Filter', onClick: () => void 0 },
    { id: '', value: 'Custom2 Filter', onClick: () => void 0 },
    { id: '', value: 'Dilation Filter', onClick: () => void 0 },
    { id: '', value: 'EdgeDetect Filter', onClick: () => void 0 },
    { id: '', value: 'EdgeDetect2 Filter', onClick: () => void 0 },
    { id: '', value: 'Erosion Filter', onClick: () => void 0 },
    { id: '', value: 'HorizonEdge Filter', onClick: () => void 0 },
    { id: '', value: 'LaplacianEdge Filter', onClick: () => void 0 },
    { id: '', value: 'LaplacianEdge2 Filter', onClick: () => void 0 },
    { id: '', value: 'MAX MIN Filter', onClick: () => void 0 },
    { id: '', value: 'Opening Filter', onClick: () => void 0 },
    { id: '', value: 'Ranking Filter', onClick: () => void 0 },
    { id: '', value: 'Sharpen Filter', onClick: () => void 0 },
    { id: '', value: 'Sharpen2 Filter', onClick: () => void 0 },
    { id: '', value: 'Skeleton Filter', onClick: () => void 0 },
    { id: '', value: 'Smooth Filter', onClick: () => void 0 },
    { id: '', value: 'Thickening Filter', onClick: () => void 0 },
    { id: '', value: 'Thinning Filter', onClick: () => void 0 },
    { id: '', value: 'VertEdge Filter', onClick: () => void 0 },
    { id: '', value: 'Watershed Filter', onClick: () => void 0 },
  ],
  AvailableFilterListActions = [
    { intlId: 'before', iconUrl: iconTakeOver, onClickHandler: () => void 0 },
  ],
  CurrentFilterListItems = [
    { id: '', value: 'Custom Filter', onClick: () => void 0 },
    { id: '', value: 'Dilation Filter', onClick: () => void 0 },
    { id: '', value: 'Closing Filter', onClick: () => void 0 },
  ],
  CurrentFilterListActions = [
    { intlId: 'down', iconUrl: iconDown, onClickHandler: () => void 0 },
    { intlId: 'up', iconUrl: iconUp, onClickHandler: () => void 0 },
    { intlId: 'delete', iconUrl: iconDeleteDustbin, onClickHandler: () => void 0 },
  ],
}: FilterProps) => {
  const [localCurrentFilterListItems, setLocalCurrentFilterListItems] =
    useState(CurrentFilterListItems);

  const onSortEnd: SortEndHandler = ({ oldIndex, newIndex }) => {
    setLocalCurrentFilterListItems(arrayMove(localCurrentFilterListItems, oldIndex, newIndex));
  };

  return (
    <div className="parameter-view-filter-wrapper">
      <div className="available-filters filter-list">
        <div className="filter-header">Available Filters</div>
        <ul className="filter-content">
          {AvailableFilterListItems.map((item) => (
            <li className="filter-list-row" key={item.value}>
              <span className="filter-list-row-item">{item.value}</span>
            </li>
          ))}
        </ul>
        <div className="filter-list-actions">
          <ActionBar actions={AvailableFilterListActions} />
        </div>
      </div>

      <div className="current-filters filter-list">
        <div className="filter-header">Available Filters</div>
        <ul className="filter-content">
          <SortableList
            helperClass="filter-list-row-item-on-drag"
            items={localCurrentFilterListItems}
            onSortEnd={onSortEnd}
          />
        </ul>
        <div className="filter-list-actions">
          <ActionBar actions={CurrentFilterListActions} />
        </div>
      </div>

      <div className="property-container">
        <div className="filter-header">Properties</div>
      </div>
    </div>
  );
};

export default Filter;
