import { FormattedMessage, IntlShape } from 'react-intl';

import de from './de';
import en from './en';
import es from './es';

const translations = {
  de: {
    ...de,
  },
  en: {
    ...en,
  },
  es: {
    ...es,
  },
};

declare module 'react-intl' {
  interface TypedIntlShape extends IntlShape {
    messages: Record<keyof typeof en, string>;
  }

  export function useIntl(): TypedIntlShape;
}

export const languages = [
  {
    locale: 'de',
    name: <FormattedMessage id="topNavigation.language.german" />,
  },
  {
    locale: 'en',
    name: <FormattedMessage id="topNavigation.language.english" />,
  },
  {
    locale: 'es',
    name: <FormattedMessage id="topNavigation.language.spanish" />,
  },
];

export default translations;
