import { useSelector } from 'react-redux';
import NavigationItem from '../../../../components/NavigationItem';
import ConfigurationLock from '../../../../protobuf/ConfigurationLock';

import './style.scss';

const LockState = () => {
  const configLock = useSelector<any>((state) => state.app.configurationLock) as ConfigurationLock;

  return (
    <NavigationItem>
      <div className="lock-status">
        <p>Status: {configLock.isLocked() ? 'Locked' : 'Unlocked'}</p>
        <p>
          Owner: {configLock.isLocked() ? configLock.owner : 'None'}{' '}
          {configLock.isLocked() ? configLock.host : '0.0.0.0'}
        </p>
        {/* <p>Host: {configLock.isLocked() ? configLock.host : '0.0.0.0'}</p> */}
      </div>
    </NavigationItem>
  );
};
export default LockState;
