import React, { Component } from 'react';
import PropTypes from 'prop-types';

import {
  toggleFullscreenMode,
  setEditingFocus,
  setFocusedViewType,
  setSaveFlag,
  openHelpView,
  setParameter,
} from '../redux/actions/index';
import logger from '../utils/logger';
import { IMAGE_VIEW } from '../views/ViewSetFactory';

export default class KeyHandler extends Component {
  componentDidMount() {
    this.node.addEventListener('keydown', this.handleKeyEvent);
    this.node.addEventListener('click', this.handleClick);
  }

  componentWillUnmount() {
    this.node.removeEventListener('keydown', this.handleKeyEvent);
    this.node.removeEventListener('click', this.handleClick);
  }

  handleKeyEvent = (event) => {
    const keyString = `${event.ctrlKey ? 'ctrl+' : ''}${event.key}`;
    switch (keyString) {
      case 'ctrl+f':
        event.preventDefault();
        this.props.dispatch(toggleFullscreenMode(this.props.view));
        break;
      case 'F1':
        event.preventDefault();
        this.props.dispatch(openHelpView());
        break;
      default:
        return;
    }
    event.stopPropagation();
  };

  handleClick = () => {
    const { view, editingViewIds, dispatch, helpSection } = this.props;

    if (view) {
      try {
        const viewId = view.props.id;
        const viewSet = view.props.views.viewSet[viewId];
        if (viewSet) {
          const inspectionId = viewSet.inspectionId;
          const viewType = viewSet.viewComponentClassId;
          if (viewType === IMAGE_VIEW) {
            dispatch(setEditingFocus(inspectionId, viewId));
            dispatch(setParameter(null));

            if (!editingViewIds.includes(viewId)) {
              dispatch(setSaveFlag(true));
            }
          }
          dispatch(setFocusedViewType(viewType));
        }
      } catch (e) {
        logger.error(e instanceof Error ? e.message : String(e));
      }
    } else if (helpSection) {
      dispatch(setFocusedViewType(helpSection));
    }
  };

  render() {
    const { children } = this.props;
    return (
      <div
        tabIndex={0}
        style={{ width: '100%', height: '100%' }}
        ref={(node) => {
          this.node = node;
        }}
      >
        {children && children}
      </div>
    );
  }
}

KeyHandler.defaultProps = {
  children: undefined,
};

KeyHandler.propTypes = {
  children: PropTypes.node,
  dispatch: PropTypes.func.isRequired,
  view: PropTypes.object, // eslint-disable-line
};
