/* eslint-disable @typescript-eslint/ban-ts-comment */
import { createLogger } from 'browser-bunyan';
import browserStdout from 'browser-stdout';
import bunyanFormat from 'bunyan-format';

interface ProxyStreamOptions {
  throttleInterval?: number;
}

interface Message {
  msg: string;
  level: string | number;
  sameMessageCount: number;
}

class ProxyStream {
  private proxy: typeof Proxy | null = null;
  private pendingMessages: Message[] = [];
  private readonly opts: ProxyStreamOptions;
  private currentThrottleTimeout: NodeJS.Timeout | null = null;

  constructor(opts: ProxyStreamOptions = {}) {
    this.opts = opts;
    this.start();
  }

  start() {
    const { throttleInterval } = this.opts;

    const throttleRequests = () => {
      this.currentThrottleTimeout = setTimeout(() => {
        const recs = this.pendingMessages;
        this.pendingMessages = [];

        if (recs.length > 0) {
          if (!this.proxy) {
            this.pendingMessages = recs;
          } else {
            //@ts-expect-error waiting for Proxy class to be written in typescript
            this.proxy.sendLogsToServer(recs);
          }
        }
        throttleRequests();
      }, throttleInterval);
    };

    throttleRequests();
  }

  setProxy(proxy: typeof Proxy) {
    this.proxy = proxy;

    if (this.proxy && this.pendingMessages.length > 0) {
      //@ts-expect-error waiting for Proxy class to be written in typescript
      this.proxy.sendLogsToServer(this.pendingMessages);
      this.pendingMessages = [];
    }
  }

  removeProxy() {
    this.proxy = null;
    this.pendingMessages = [];
  }

  write(rec: Message) {
    if (this.currentThrottleTimeout) {
      const idx = this.pendingMessages.findIndex(
        (message) => message.level === rec.level && message.msg === rec.msg,
      );

      if (idx >= 0) {
        if (!this.pendingMessages[idx].sameMessageCount) {
          this.pendingMessages[idx] = { ...this.pendingMessages[idx], sameMessageCount: 1 };
        }
        this.pendingMessages[idx].sameMessageCount++;
      } else {
        this.pendingMessages.push(rec);
      }
    }
  }
}

class FormatStream {
  private readonly impl: ReturnType<typeof bunyanFormat>;

  constructor() {
    const out = browserStdout({
      label: false,
    });
    this.impl = bunyanFormat(
      {
        outputMode: 'short',
        color: false,
      },
      out,
    );
  }

  write(rec: Message) {
    this.impl.write(JSON.stringify(rec));
  }
}

const proxyStream = new ProxyStream({
  throttleInterval: 3000,
});
const formatStream = new FormatStream();
const logger = createLogger({
  name: 'SvWebApp',
  streams: [
    {
      level: 'warn',
      stream: proxyStream,
      //@ts-ignore
      type: 'raw',
    },
    {
      level: 'debug',
      stream: formatStream,
      //@ts-ignore
      type: 'stream',
    },
  ],
});

//@ts-ignore
logger.setProxy = (proxy: typeof Proxy) => proxyStream.setProxy(proxy);
//@ts-ignore
logger.removeProxy = () => proxyStream.removeProxy();

export default logger;
