import { useRef, useState } from 'react';
import { Portal } from '@material-ui/core';
import { useIntl } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import downloadIcon from '../../../assets/icons/download.svg';
import helpIcon from '../../../assets/icons/help.svg';
import aboutIcon from '../../../assets/icons/info.svg';
import logoutIcon from '../../../assets/icons/logout.svg';
import permissionsIcon from '../../../assets/icons/permissions.svg';
import saveIcon from '../../../assets/icons/save.svg';
import shortcutHelpIcon from '../../../assets/icons/shortcut-help.svg';
import uploadIcon from '../../../assets/icons/upload.svg';
import userIcon from '../../../assets/icons/user.svg';
import type Proxy from '../../../protobuf/Proxy';
import { saveClientChunk } from '../../../redux/actions';
import { forceDownload } from '../../../utils/downloader';
import {
  canEditUserManagement,
  canReadSVOConfiguration,
  canReadUserManagement,
  canSaveConfig,
  canWriteSVOConfiguration,
} from '../../../utils/permissions';
import AboutView from '../../AboutView';
import HelpView from '../../HelpView';
import ShortcutView from '../../ShortcutView';
import { NavigationItemType } from '../NavigationItem';

export const useUserInfoNavigation = (): NavigationItemType => {
  const dispatch = useDispatch();
  const currentState = useSelector((state) => state);
  const { user, protobuf, myPermissions, proxy } = useSelector<any>((state) => state.app) as {
    myPermissions: Set<string>;
    proxy: Proxy;
    [key: string]: any;
  };
  const unsavedChanges = useSelector<any>((state) => state.viewSet.unsavedChanges);
  const intl = useIntl();
  const history = useHistory();
  const inputFile = useRef<HTMLInputElement>(null);

  const [shortcutViewOpen, setShortcutViewOpen] = useState(false);
  const [isHelpViewOpen, setIsHelpViewOpen] = useState(false);
  const [isAboutViewOpen, setIsAboutViewOpen] = useState(false);

  const downloadConfig = async () => {
    const fileData = await proxy.downloadSVOConfig();
    const blob = new Blob([fileData], { type: 'application/octet-stream' });
    const url = URL.createObjectURL(blob);
    forceDownload(url, `SVOConfig_${new Date().toISOString()}.svz`);
  };

  const onFileAdded: React.ChangeEventHandler<HTMLInputElement> = (event) => {
    const file = event.target.files?.[0];
    if (file) {
      const fileReader = new FileReader();
      fileReader.readAsArrayBuffer(file);

      fileReader.onload = (event) => {
        const arrayBuffer = event.target?.result;
        const array =
          arrayBuffer instanceof ArrayBuffer ? new Uint8Array(arrayBuffer) : new Uint8Array();
        proxy.uploadSVOConfig(file.name, array);
      };
    }
  };

  const children: NavigationItemType[] = [
    {
      id: 'saveConfig',
      content: intl.messages['topNavigation.saveConfig'],
      icon: saveIcon,
      onClick: () => dispatch(saveClientChunk(protobuf, currentState)),
      enabled: unsavedChanges ? canSaveConfig(myPermissions) : false,
    },
    {
      id: 'permissions',
      content: intl.messages['topNavigation.permissions'],
      icon: permissionsIcon,
      onClick: () => history.push('/permissions'),
      enabled: canReadUserManagement(myPermissions) || canEditUserManagement(myPermissions),
    },
    {
      id: 'download',
      content: intl.messages['topNavigation.download'],
      icon: downloadIcon,
      onClick: downloadConfig,
      enabled: canReadSVOConfiguration(myPermissions),
    },
    {
      id: 'upload',
      content: (
        <>
          {intl.messages['topNavigation.upload']}
          <input
            type="file"
            id="file"
            ref={inputFile}
            style={{ display: 'none' }}
            onChange={onFileAdded}
          />
        </>
      ),
      icon: uploadIcon,
      onClick: async () => inputFile.current?.click?.(),
      enabled: canWriteSVOConfiguration(myPermissions),
    },
    {
      id: 'help',
      icon: helpIcon,
      content: (
        <>
          <span>{intl.messages['topNavigation.help']}</span>
          <Portal container={document.body}>
            <HelpView open={isHelpViewOpen} onClose={() => setIsHelpViewOpen(false)} />
          </Portal>
        </>
      ),
      onClick: () => !isHelpViewOpen && setIsHelpViewOpen(true),
      enabled: true,
    },
    {
      id: 'shortcutHelp',
      icon: shortcutHelpIcon,
      content: (
        <>
          <span>{intl.messages['topNavigation.shortcut']}</span>
          <Portal container={document.body}>
            <ShortcutView open={shortcutViewOpen} onClose={() => setShortcutViewOpen(false)} />
          </Portal>
        </>
      ),
      onClick: () => !shortcutViewOpen && setShortcutViewOpen(true),
      enabled: true,
    },
    {
      id: 'about',
      icon: aboutIcon,
      content: (
        <>
          <span>{intl.messages['topNavigation.about']}</span>
          <Portal container={document.body}>
            <AboutView open={isAboutViewOpen} onClose={() => setIsAboutViewOpen(false)} />
          </Portal>
        </>
      ),
      onClick: () => !isAboutViewOpen && setIsAboutViewOpen(true),
      enabled: true,
    },
    {
      id: 'logout',
      icon: logoutIcon,
      content: intl.messages['topNavigation.logout'],
      onClick: async () => {
        proxy.unsubscribeFromLockAcquisitionStream();
        setImmediate(() => (window.location.href = `${window.location.origin}/logout`));
      },
      enabled: true,
    },
  ];

  return {
    id: 'user-info',
    icon: userIcon,
    content: user.fullname,
    enabled: true,
    children,
  };
};
