export default class ConfigurationLock {
  constructor(lock) {
    this.lockId = undefined;
    this.takeoverId = undefined;
    this.owner = undefined;
    this.takeoverUser = undefined;
    this.host = '';
    this.lockDescription = '';
    this.takeoverMessage = '';
    this.status = 0;

    // Workaround for checking if config lock is already owned by the same logged user in Proxy.js.
    // In this file (Proxy.js) we don't have an access to redux state.app.user,
    // but we know that it is the same logged user only when LockAcquiredNotification comes
    // from SVOGateway. In result this member is set only in this one place
    // and reset while releasing or passing the lock to other user.
    this.lockedInSameAppInstance = false;

    // Flags to control alert dialogs
    this.isTakeoverAttempt = false;
    this.configAlreadyLocked = false;
    this.isWaitingForResponse = false;
    this.ownerRejectedTakeover = false;

    if (Object.keys(lock).length !== 0) {
      Object.assign(this, lock);
    }
  }

  isLocked() {
    return this.status === 1;
  }

  isUnlocked() {
    return this.status === 0;
  }

  isSameUserAsOwner(user) {
    return user.username === this.owner;
  }

  isLockedBySameUser(user) {
    return this.isLocked() && this.isSameUserAsOwner(user);
  }

  isLockedByOtherUser(user) {
    return this.isLocked() && !this.isSameUserAsOwner(user);
  }

  isLockedInDifferentAppInstance() {
    return this.isLocked() && !this.lockedInSameAppInstance;
  }

  isTakeoverAttemptFromOtherUser() {
    return this.isTakeoverAttempt;
  }

  isConfigAlreadyLocked() {
    return this.configAlreadyLocked;
  }

  isWaitingForTakeoverResponse() {
    return this.isWaitingForResponse;
  }

  isOwnerRejectedTakeover() {
    return this.ownerRejectedTakeover;
  }

  resetTakeoverAttempt() {
    this.isTakeoverAttempt = false;
  }

  resetAlertDialogFlags() {
    this.configAlreadyLocked = false;
    this.ownerRejectedTakeover = false;
  }

  handleLockAcquiredNotification(notification) {
    this.lockId = notification.lockId;
    this.owner = notification.user;
    this.host = notification.host;
    this.lockDescription = notification.description;
    this.status = 1; // Locked
    this.lockedInSameAppInstance = true;

    this.isWaitingForResponse = false;
  }

  handleLockTakeoverNotification(notification) {
    this.takeoverId = notification.takeoverId;
    this.takeoverMessage = notification.message;
    this.takeoverUser = notification.user;

    this.isTakeoverAttempt = true;
  }

  handleLockReleasedNotification() {
    this.lockId = undefined;
    this.owner = undefined;
    this.host = '0.0.0.0';
    this.lockDescription = '';
    this.status = 0; // Unlocked
  }

  handleLockAlreadyLockedNotification(notification) {
    this.lockId = notification.lockId;
    this.owner = notification.user;
    this.host = notification.host;
    this.lockDescription = notification.description;
    this.status = 1; // Locked

    this.configAlreadyLocked = true;
  }

  handleLockTakeoverRejectedNotification() {
    this.isWaitingForResponse = false;
    this.ownerRejectedTakeover = true;
  }

  waitForTakeoverResponse() {
    this.isWaitingForResponse = true;
  }
}
