export interface OverlaySchema {
  id: number;
  color: string;
  colorVariable?: string;
  isActive: boolean;
  label: string;
  lineStyle: string;
  maxHeightFallback?: number;
  maxWidthFallback?: number;
  minHeightFallback?: number;
  minWidthFallback?: number;
  offsetLeft: number | null;
  offsetLeftVariable?: string;
  offsetTop: number | null;
  offsetTopVariable?: string;
  width: number;
  widthVariable?: string;
  height: number;
  heightVariable?: string;
  x: number;
  xVariable?: string;
  y: number;
  yVariable?: string;
}

export const updateOverlayList = (updatedOverlay: OverlaySchema, overlayList: OverlaySchema[]) =>
  overlayList.map((overlay) => (overlay.id === updatedOverlay.id ? updatedOverlay : overlay));
