import { memo, useEffect, useMemo, useState } from 'react';
import { Button, CircularProgress } from '@material-ui/core';
import { useIntl } from 'react-intl';
import { useSelector } from 'react-redux';
import { ReactComponent as ArrowIcon } from '../../../assets/icons/back.svg';
import iconList from '../../../assets/icons/overlay-list.svg';
import ActionBar from '../../../components/ActionBar';
import { TreeNodeSchema } from '../../../components/ObjectSelector';
import Configuration from '../../../protobuf/Configuration';
import Product from '../../../protobuf/Product';
import Protobuf from '../../../protobuf/Protobuf';
import Proxy from '../../../protobuf/Proxy';
import { TOGGLE_BUTTON } from '../../../types/ActionBarType';
import { VARENUM } from '../../ToolsetView/common';
import { AngleEditor } from './AngleEditor';
import { BooleanEditor } from './BooleanEditor';
import { ColorEditor } from './ColorEditor';
import { NumberEditor } from './NumberEditor';
import { RawEditor } from './RawEditor';
import { Tabs } from './Tabs';
import { CommonEditorProps } from './common';

interface ValueEditorProps {
  inspectionId: string;
  data: TreeNodeSchema;
  onExit?: () => void;
}

const Editor = memo(({ type, value, onChange }: CommonEditorProps & { type: string }) => {
  const intl = useIntl();

  //BOOL 
  //INTEGER32 
  //EXTENT WIDTH 
  //EXTENT HEIGHT 
  //EXTENT ANGLE 
  //EXTENT X 
  //EXTENT Y 
  //DECIMAL 
  //ENUMERATION
  //VARIANT
  //Integer32Hex
  switch (type.toUpperCase()) {
    default:
      return <RawEditor value={value} onChange={onChange} />;
    case 'EXTENT WIDTH':
    case 'EXTENT HEIGHT':
      return <NumberEditor value={parseFloat(value)} onChange={onChange} min={0} max={512} />; //TODO: consider removing max value
    case 'EXTENT X':
    case 'EXTENT Y':
    case 'DECIMAL':
      return <NumberEditor value={parseFloat(value)} onChange={onChange} />;
    case 'EXTENT ANGLE':
      return <AngleEditor value={parseFloat(value) || 0} onChange={onChange} />;
    case 'INTEGER32':
      return (
        <Tabs
          options={[
            {
              value: 'number',
              label: intl.messages['inputToolView.numberEditor'],
              component: <NumberEditor value={parseInt(value)} onChange={onChange} onlyIntegers />,
            },
            {
              value: 'color',
              label: intl.messages['inputToolView.colorEditor'],
              component: <ColorEditor value={parseInt(value)} onChange={onChange} />,
            },
          ]}
        />
      );
    case 'BOOL':
      return (
        <BooleanEditor
          value={typeof value === 'string' ? value === 'true' : value}
          onChange={onChange}
        />
      );
  }
});

export const ValueEditor = memo(({ inspectionId, data, onExit }: ValueEditorProps) => {
  const proxy = useSelector<any>((state) => state?.app?.proxy) as Proxy;
  const configuration = useSelector<any>((state) => state?.app?.configuration) as Configuration;

  const intl = useIntl();

  const [product, setProduct] = useState<Product | null>(null);
  const [internalValue, setInternalValue] = useState<any>(null);
  const [errors, setErrors] = useState<string[]>([]);
  const [showSuccessMessage, setShowSuccessMessage] = useState(false);
  const [updateCount, setUpdateCount] = useState(0);
  const [waitingForEditResult, setWaitingForEditResult] = useState(false);

  const value = useMemo(
    () =>
      product?.getValuesAsString([data.id])?.find((entry: any) => entry.id === data.id)?.value ??
      null,
    [data.id, product],
  );

  const name = useMemo(
    () => configuration.getValueNameForId(inspectionId, data.id),
    [configuration, data.id, inspectionId],
  );

  useEffect(() => {
    const onProduct = ({ product }: { product: Product }) => {
      setProduct(product);
      return Promise.resolve();
    };

    const startAtTriggerCount = -1;
    const desc = {
      inspectionId,
      valueIds: [data.id],
      startAtTriggerCount,
    };
    const productSub = proxy.subscribeToProductStream(desc, onProduct);

    return () => {
      if (productSub) {
        proxy.unsubscribeFromProductStream(productSub);
      }
    };
  }, [data.id, inspectionId, proxy, updateCount]);

  const editValue = () => {
    setWaitingForEditResult(true);
    const request = Protobuf.SvPb.SetItemsRequest.create({
      valueList: [
        {
          item: {
            type: VARENUM.VT_BSTR,
            strVal: String(internalValue),
          },
          name: name ?? data.name,
        },
      ],
    });
    proxy.protobuf
      .request(request)
      .then((res: any) => {
        const errors = res.errorList.map((e: any) => e.$type?.comment);
        setErrors(errors);

        if (!errors.length) {
          setShowSuccessMessage(true);
          setInternalValue((value: typeof internalValue) => String(value));
          setUpdateCount((count) => count + 1);
        }
      })
      .finally(() => setWaitingForEditResult(false));
  };

  const handleValueChange = (value: any) => {
    setErrors([]);
    setShowSuccessMessage(false);
    setInternalValue(value);
  };

  return (
    <div>
      <div className="value-editor">
        <header>
          <span>
            {intl.messages['inputToolView.type']}:&nbsp;{data.type}
          </span>
          <span>{name ?? data.name}</span>
        </header>
        {product ? (
          <div className="editor-container">
            <Editor
              type={data.type ?? 'unknown'}
              value={internalValue ?? value}
              onChange={handleValueChange}
            />
          </div>
        ) : (
          <div style={{ textAlign: 'center', padding: '24px 0' }}>
            <CircularProgress />
          </div>
        )}
        <div className="options">
          <Button variant="contained" color="primary" onClick={onExit}>
            <ArrowIcon fill="#fff" className="image-item" />
            {intl.messages['inputToolView.backToList']}
          </Button>
          <Button
            disabled={!product || internalValue === null || value === internalValue}
            variant="contained"
            color="primary"
            onClick={editValue}
          >
            {intl.messages['inputToolView.setValue']}
          </Button>
        </div>
        {errors.length > 0 && (
          <div className="errors">
            {errors.map((error, index) => (
              <div key={index}>{error}</div>
            ))}
          </div>
        )}
        {waitingForEditResult && <CircularProgress />}
        {showSuccessMessage && (
          <div className="success">
            <div>{intl.messages['inputToolView.valueSetSuccess']}</div>
          </div>
        )}
      </div>
      <ActionBar
        isViewAction
        actions={[
          {
            id: 0,
            intlId: 'inputToolView.showList',
            onClickHandler: onExit ?? (() => void 0),
            iconUrl: iconList,
            type: TOGGLE_BUTTON,
          },
        ]}
      />
    </div>
  );
});
