import { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { Button, makeStyles } from '@material-ui/core';
import { useIntl } from 'react-intl';
import { useSelector } from 'react-redux';
import Dialog from '../../../components/Dialog';
import Protobuf from '../../../protobuf/Protobuf';
import type Proxy from '../../../protobuf/Proxy';
import { bytesToString } from '../common';
import { FormulaEditor, FormulaEditorRef } from './FormulaEditor';
import { LinkedValueSelectDialog } from './LinkedValueSelectDialog';
import { ReadonlyFormula } from './ReadonlyFormula';
import { useFormulaValidate } from './hooks/useFormulaValidate';

import './FormulaEditPanel.scss';

const useStyles = makeStyles({
  root: {
    padding: '0 6px',
    minWidth: 26,
    minHeight: 'auto',
  },
});

interface FormulaEditPanelTitleProps extends ReturnType<typeof useFormulaValidate> {
  conditional?: boolean;
  short?: boolean;
  internalEquationText: string;
}

const FormulaEditPanelTitle = ({
  conditional,
  short,
  internalEquationText,
  validating,
  validatedFormula,
  validationSuccessResult,
  validationErrorMessages,
  errorPosition,
}: FormulaEditPanelTitleProps) => {
  const intl = useIntl();

  const validatedFragment = validatedFormula !== internalEquationText;

  const validatedFormulaCut = useMemo(() => {
    if (validatedFormula.includes('\n')) {
      return validatedFormula.split('\n')[0] + '...';
    } else {
      return validatedFormula;
    }
  }, [validatedFormula]);

  return (
    <div className="formula-edit-panel-title">
      {!short && <div>{intl.messages['toolsetView.formula']}</div>}
      {validating && <div>{intl.messages['toolsetView.validating']}...</div>}
      {validatedFormula && (
        <div className="result">
          {validationSuccessResult !== null && (
            <div>
              <span className="validation-success-info">
                {intl.messages['toolsetView.validationSuccess']}.
              </span>
              &ensp;
              <span>{intl.messages['toolsetView.result']}:</span>&ensp;
              <strong>
                {conditional && !validatedFragment
                  ? validationSuccessResult
                    ? 'TRUE'
                    : 'FALSE'
                  : validationSuccessResult}
              </strong>
            </div>
          )}
          {validationErrorMessages?.length > 0 && (
            <div>
              <span>{intl.messages['toolsetView.validationError']}.</span>&ensp;
              <span className="validation-error-info">{validationErrorMessages.join('. ')}</span>
            </div>
          )}
          {validatedFragment && (
            <div className="validated-formula-info">
              <span>{intl.messages['toolsetView.validatedFormula']}:</span>
              <ReadonlyFormula
                style={{
                  whiteSpace: 'nowrap',
                  display: 'inline-flex',
                  overflow: 'hidden',
                  textOverflow: 'ellipsis',
                }}
                errorPosition={errorPosition ?? undefined}
              >
                {validatedFormulaCut}
              </ReadonlyFormula>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

interface FormulaEditPanelBaseProps
  extends ReturnType<typeof useFormulaValidate>,
    Omit<FormulaEditPanelProps, 'showInDialog' | 'equationId'> {
  internalEquationText: string;
  setInternalEquationText: React.Dispatch<React.SetStateAction<string>>;
  undoAll: () => void;
}

const FormulaEditPanelBase = ({
  inspectionID,
  internalEquationText,
  equationText,
  setInternalEquationText,
  onSave,
  onClose,
  undoAll,
  validate,
  validating,
  reset: resetValidation,
  validatedFormula,
  validationErrorMessages,
}: FormulaEditPanelBaseProps) => {
  const proxy = useSelector<any>((state) => state?.app?.proxy) as Proxy;
  const intl = useIntl();
  const panelButtonClasses = useStyles();

  const formulaEditorRef = useRef<FormulaEditorRef>(null);

  const [selection, setSelection] = useState<{ start: number; end: number }>({
    start: internalEquationText.length,
    end: internalEquationText.length,
  });
  const [openLinkedValueSelectDialog, setOpenLinkedValueSelectDialog] = useState(false);

  const validateAndReturn = useCallback(async () => {
    const result = await validate(internalEquationText);
    if (result.success) {
      onSave(internalEquationText.trim(), result.resultValue);
    }
    return result;
  }, [validate, internalEquationText, onSave]);

  const appendToInternalEquation = useCallback(
    (value: string, options: { cursorOffset?: number } = {}) => {
      const editorSelection = formulaEditorRef.current?.getSelectionPosition() ?? {
        start: internalEquationText.length,
        end: internalEquationText.length,
      };

      const shouldAppendSpace = () => {
        if (['\n'].includes(value)) {
          return false;
        }

        const lines = internalEquationText.split('\n');
        let startLi = 0,
          endLi = 0;
        let startPivot = editorSelection.start;
        let endPivot = editorSelection.end;
        for (; startLi < lines.length; endLi++) {
          startPivot -= lines[startLi].length + 1;
          endPivot -= lines[startLi].length + 1;
          if (startPivot >= 0) {
            startLi++;
          }
          if (endPivot < 0) {
            break;
          }
        }
        const equationPart = lines.slice(startLi, Math.min(lines.length, endLi + 1)).join('\n');

        if (equationPart.length === 0) {
          return false;
        }
        const lastChar = equationPart.charAt(equationPart.length - 1);
        const digits = '1234567890'.split('');
        const numeric = [...digits, ...['.', ',', '(', ')']];
        if (
          (numeric.includes(lastChar) && numeric.includes(value)) ||
          (digits.includes(lastChar) && ['b', 'h'].includes(value))
        ) {
          return false;
        }
        return !equationPart.endsWith(' ');
      };

      if (editorSelection.start === editorSelection.end) {
        setInternalEquationText(
          internalEquationText.slice(0, editorSelection.start) +
            (shouldAppendSpace() ? ' ' : '') +
            value +
            internalEquationText.slice(editorSelection.end),
        );
        formulaEditorRef.current?.focus(
          editorSelection.start +
            (options.cursorOffset ?? 0) +
            (shouldAppendSpace() ? 1 : 0) +
            value.length,
        );
      } else {
        setInternalEquationText(
          internalEquationText.slice(0, editorSelection.start) +
            value +
            internalEquationText.slice(editorSelection.end),
        );
        formulaEditorRef.current?.focus(
          editorSelection.start +
            (typeof options.cursorOffset === 'number' ? options.cursorOffset : value.length),
        );
      }
    },
    [internalEquationText, setInternalEquationText],
  );

  const deleteLeft = useCallback(() => {
    const editorSelection = formulaEditorRef.current?.getSelectionPosition() ?? {
      start: internalEquationText.length,
      end: internalEquationText.length,
    };
    const start = Math.max(
      0,
      editorSelection.start - (editorSelection.start === editorSelection.end ? 1 : 0),
    );

    setInternalEquationText(
      internalEquationText.slice(0, start) + internalEquationText.slice(editorSelection.end),
    );
    formulaEditorRef.current?.focus(start);
  }, [internalEquationText, setInternalEquationText]);

  const buttons = useMemo<{ label?: string; onClick?: () => void; separator?: boolean }[][]>(() => {
    const createSimpleAppendButtons = <OptionsType extends Record<string, unknown>>(
      labels: string[],
      options?: OptionsType,
    ) => {
      return labels.map((symbol) => ({
        label: symbol,
        onClick: () => appendToInternalEquation(symbol),
        ...(options ?? {}),
      }));
    };

    return [
      // External and numeric system letters
      [
        {
          label: 'Add linked value',
          onClick: () => setOpenLinkedValueSelectDialog(true),
        },
        {
          separator: true,
        },
        {
          label: '',
          onClick: () => formulaEditorRef.current?.moveCursorTo(0),
        },
        {
          label: '',
          onClick: () => formulaEditorRef.current?.moveLeft(),
        },
        {
          label: '',
          onClick: () => formulaEditorRef.current?.moveRight(),
        },
        {
          label: '',
          onClick: () => formulaEditorRef.current?.moveCursorTo(Number.MAX_SAFE_INTEGER),
        },
        {
          separator: true,
        },
        ...createSimpleAppendButtons(['b', 'h']),
      ],

      // Digits and dot
      createSimpleAppendButtons('0123456789.'.split('')),

      // Logical and basic math operators
      [
        ...createSimpleAppendButtons(['AND', 'OR', 'NOT', 'XOR']),
        {
          label: 'IF',
          onClick: () => appendToInternalEquation('IF(  )\nTHEN {}\nELSE {}', { cursorOffset: 4 }),
        },
        ...createSimpleAppendButtons(['+', '-']),
        {
          label: '',
          onClick: () => appendToInternalEquation('*'),
        },
        {
          label: '',
          onClick: () => appendToInternalEquation('/'),
        },
      ],

      // Comparators, parenthesis and coma
      [
        ...createSimpleAppendButtons(['<', '>']),
        {
          label: '',
          onClick: () => appendToInternalEquation('<='),
        },
        {
          label: '',
          onClick: () => appendToInternalEquation('>='),
        },
        {
          label: '=',
          onClick: () => appendToInternalEquation('='),
        },
        {
          label: '',
          onClick: () => appendToInternalEquation('<>'),
        },
        ...createSimpleAppendButtons(['(', ')', '{', '}', ',']),
      ],

      // Advanced
      [
        {
          label: 'Sin',
          onClick: () => appendToInternalEquation('SIN('),
        },
        {
          label: 'Cos',
          onClick: () => appendToInternalEquation('COS('),
        },
        {
          label: 'Tan',
          onClick: () => appendToInternalEquation('TAN('),
        },
        {
          label: 'Sin',
          onClick: () => appendToInternalEquation('ASIN('),
        },
        {
          label: 'Cos',
          onClick: () => appendToInternalEquation('ACOS('),
        },
        {
          label: 'Tan',
          onClick: () => appendToInternalEquation('ATAN('),
        },
        {
          label: 'Min',
          onClick: () => appendToInternalEquation('MIN(,', { cursorOffset: 4 }),
        },
        {
          label: 'Max',
          onClick: () => appendToInternalEquation('MAX(,', { cursorOffset: 4 }),
        },
        {
          label: 'Avg',
          onClick: () => appendToInternalEquation('AVG(,', { cursorOffset: 4 }),
        },
        {
          label: 'Med',
          onClick: () => appendToInternalEquation('MED(,', { cursorOffset: 4 }),
        },
        {
          label: 'Sum',
          onClick: () => appendToInternalEquation('SUM(,', { cursorOffset: 4 }),
        },
        {
          label: 'Std dev',
          onClick: () => appendToInternalEquation('STDDEV(,', { cursorOffset: 7 }),
        },
        {
          label: 'Mod',
          onClick: () => appendToInternalEquation('MOD(,', { cursorOffset: 4 }),
        },
        {
          label: 'Abs',
          onClick: () => appendToInternalEquation('ABS('),
        },
        {
          label: 'Sqr',
          onClick: () => appendToInternalEquation('SQR('),
        },
        {
          label: 'Sqrt',
          onClick: () => appendToInternalEquation('SQRT('),
        },
        {
          label: 'Trunc',
          onClick: () => appendToInternalEquation('TRUNC('),
        },
        {
          label: 'Pow',
          onClick: () => appendToInternalEquation('POW(,', { cursorOffset: 4 }),
        },
      ],

      // Overall options
      [
        {
          label: 'Delete',
          onClick: deleteLeft,
        },
        {
          label: '',
          onClick: () => appendToInternalEquation(' '),
        },
        {
          label: '',
          onClick: () => appendToInternalEquation('\n'),
        },
      ],
    ];
  }, [appendToInternalEquation, deleteLeft]);

  return (
    <>
      <div className="formula-edit-panel">
        <div className="panel-container">
          <div className="panel">
            <div className="panel-buttons">
              {buttons.map((row, index1) => (
                <div key={index1}>
                  {row.map((btn, index2) =>
                    btn.separator ? (
                      <span
                        key={index1.toString() + (btn.label ?? '_') + index2.toString()}
                        style={{ flexGrow: 1 }}
                      />
                    ) : (
                      <Button
                        key={index1.toString() + (btn.label ?? '_') + index2.toString()}
                        variant="outlined"
                        color="primary"
                        onClick={btn.onClick}
                        classes={panelButtonClasses}
                        style={{
                          textTransform: 'none',
                        }}
                      >
                        {btn.label}
                      </Button>
                    ),
                  )}
                </div>
              ))}
            </div>
            <div className="equation-textfield-container">
              <FormulaEditor
                ref={formulaEditorRef}
                value={internalEquationText}
                error={validationErrorMessages?.length > 0}
                onSelectionChange={setSelection}
                onChange={(value) => {
                  resetValidation();
                  setInternalEquationText(value);
                }}
              />
            </div>
          </div>
        </div>
        <div className="options">
          <Button
            variant="outlined"
            color="primary"
            style={{ minWidth: 96 }}
            disabled={!internalEquationText || validating}
            onClick={() => {
              const validateFragment =
                selection.start !== selection.end && selection.end - selection.start > 0;
              const validateString = validateFragment
                ? internalEquationText.substring(selection.start, selection.end)
                : internalEquationText;
              validate(validateString).then((result) => {
                if (result.success) {
                  formulaEditorRef.current?.focus(selection.start, selection.end);
                } else if (result.errorPosition) {
                  formulaEditorRef.current?.focus(
                    result.errorPosition + (validateFragment ? selection.start : 0),
                  );
                }
              });
            }}
          >
            {selection.start !== selection.end && selection.end - selection.start > 0
              ? intl.messages['toolsetView.validateSelection']
              : intl.messages['toolsetView.validate']}
          </Button>
          <Button
            variant="outlined"
            color="primary"
            style={{ minWidth: 96 }}
            onClick={() => {
              resetValidation();
              undoAll();
            }}
            disabled={internalEquationText === equationText || validating}
          >
            {intl.messages['toolsetView.undoAll']}
          </Button>
          <span style={{ marginLeft: 16 }} />
          <Button variant="outlined" color="primary" style={{ minWidth: 96 }} onClick={onClose}>
            {intl.messages['cancel']}
          </Button>
          <Button
            variant="outlined"
            color="primary"
            style={{ minWidth: 96 }}
            disabled={
              !internalEquationText ||
              validating ||
              internalEquationText === equationText ||
              (!!validatedFormula && validationErrorMessages?.length > 0)
            }
            onClick={() => {
              validateAndReturn().then((result) => {
                if (result.success) {
                  formulaEditorRef.current?.focus(selection.start, selection.end);
                } else if (result.errorPosition) {
                  formulaEditorRef.current?.focus(result.errorPosition);
                }
              });
            }}
          >
            {intl.messages['ok']}
          </Button>
        </div>
      </div>
      {openLinkedValueSelectDialog && (
        <LinkedValueSelectDialog
          inspectionID={inspectionID}
          onClose={() => setOpenLinkedValueSelectDialog(false)}
          onSelect={(indirectObjectId) => {
            proxy.protobuf
              .request(
                Protobuf.SvPb.InspectionCmdRequest.create({
                  inspectionID,
                  getObjectNameRequest: {
                    BeforeType: 4354, //SVObjectTypeEnum.BlobDrawObjectType
                    objectId: indirectObjectId,
                  },
                }),
              )
              .then((res: any) => {
                const name = bytesToString(res?.getObjectNameResponse?.name);

                // Removes inspection name and surrounds with parenthesis
                const parsedName = `"${name.replace(/^[^.]+\./, '')}"`;
                appendToInternalEquation(parsedName);
              })
              .finally(() => {
                setOpenLinkedValueSelectDialog(false);
              });
          }}
        />
      )}
    </>
  );
};

interface FormulaEditPanelProps {
  showInDialog?: boolean;
  inspectionID: number;
  equationText: string | undefined;
  equationId: number;
  onClose: () => void;
  onSave: (equationText: string, validationResultValue: number | null) => void;
  conditional?: boolean;
}

export const FormulaEditPanel = ({
  showInDialog,
  inspectionID,
  equationText,
  equationId,
  onClose,
  onSave,
  conditional,
}: FormulaEditPanelProps) => {
  const [internalEquationText, setInternalEquationText] = useState('');

  const formulaValidation = useFormulaValidate(inspectionID, equationId);

  const equationTextUnix = (equationText ?? '').replace(/\r\n/g, '\n');

  useEffect(() => {
    setInternalEquationText(equationTextUnix);
  }, [equationTextUnix]);

  const undoAll = useCallback(() => {
    setInternalEquationText(equationTextUnix);
  }, [equationTextUnix]);

  const titleProps: FormulaEditPanelTitleProps = {
    ...formulaValidation,
    conditional,
    short: !showInDialog,
    internalEquationText,
  };

  const panelProps: FormulaEditPanelBaseProps = {
    ...formulaValidation,
    onSave,
    onClose,
    inspectionID,
    equationText: equationTextUnix,
    internalEquationText,
    setInternalEquationText,
    undoAll,
  };

  if (showInDialog) {
    return (
      <Dialog
        title={<FormulaEditPanelTitle {...titleProps} />}
        onClose={onClose}
        open
        isPositionAbsolute
      >
        <FormulaEditPanelBase {...panelProps} />
      </Dialog>
    );
  } else {
    return (
      <div>
        <FormulaEditPanelTitle {...titleProps} />
        <FormulaEditPanelBase {...panelProps} />
      </div>
    );
  }
};
