import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import Dialog from '../../../../components/Dialog';
import ObjectSelector, { TreeNodeSchema } from '../../../../components/ObjectSelector';
import Configuration from '../../../../protobuf/Configuration';
import type Proxy from '../../../../protobuf/Proxy';

interface VariableSelectionProps {
  inspectionId: string;
  open: boolean;
  onClose?: (selectedId?: React.Key | React.Key[]) => void;
  title?: string;
}

export const VariableSelection = ({
  inspectionId,
  open,
  onClose,
  title = '',
}: VariableSelectionProps) => {
  const proxy = useSelector<any>((state) => state.app?.proxy) as Proxy;
  const configuration = useSelector<any>((state) => state.app?.configuration) as Configuration;

  const [tree, setTree] = useState<TreeNodeSchema | null>(null);
  const [filterIgnore, setFilterIgnore] = useState<string[]>([]);

  useEffect(() => {
    if (configuration.objectSelectorItems?.size === 0) {
      configuration.setObjectSelectorItems(proxy.protobuf).then(() => {
        proxy.dispatchCurrentConfiguration(configuration);
      });
    }
  }, [configuration, proxy]);

  useEffect(() => {
    const items = configuration.getObjectSelectorItemsForInspection(inspectionId);
    if (!items) {
      return;
    }

    let tree: TreeNodeSchema = items.tree;
    tree = configuration.filterArrayValuesFromTree(tree);

    setTree(tree);

    setFilterIgnore(() => {
      const filterIgnore: string[] = [];
      const name = configuration.getInspectionName(inspectionId);

      filterIgnore.push(`Inspections.${name}`);
      filterIgnore.push(`Inspections${name}`);
      filterIgnore.push(name);

      return filterIgnore;
    });
  }, [configuration, inspectionId]);

  if (!configuration || !inspectionId || !tree) {
    return null;
  }

  return (
    <Dialog isInView isPopup title={title} onClose={onClose} open={open}>
      <div className="objectselector-in-dialog-wrapper">
        <ObjectSelector
          singleSelect
          data={tree}
          onChange={(selectedId) => onClose?.(selectedId)}
          filterIgnore={filterIgnore}
        />
      </div>
    </Dialog>
  );
};
