import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage } from 'react-intl';
import { connect } from 'react-redux';

import BackIcon from '../../../../assets/icons/back.svg';
import editIcon from '../../../../assets/icons/edit.svg';
import ImageStripViewIcon from '../../../../assets/icons/filmstrip.svg';
import ImageViewIcon from '../../../../assets/icons/image.svg';
import ListViewIcon from '../../../../assets/icons/list.svg';
import ParameterViewIcon from '../../../../assets/icons/parameter.svg';
import toolsetIcon from '../../../../assets/icons/toolset.svg';
import ListSelection from '../../../../components/ListSelection';
import {
  canAddImageView,
  canAddListView,
  canAddImageStripView,
  canAddParameterView,
  canAddToolSetView,
  canAddInputToolView,
} from '../../../../utils/permissions';
import {
  PARAMETER_VIEW,
  IMAGE_VIEW,
  IMAGE_STRIP_VIEW,
  LIST_VIEW,
  TOOLSET_VIEW,
  INPUT_TOOL_VIEW,
} from '../../../ViewSetFactory';

class ViewTypeSelection extends Component {
  render() {
    const { onSelection, goBack, myPermissions, showCancel } = this.props;
    const inspectionTypes = [
      {
        id: IMAGE_VIEW,
        text: <FormattedMessage id="viewTypeSelection.imageView" />,
        icon: ImageViewIcon,
        enabled: canAddImageView(myPermissions),
      },
      {
        id: PARAMETER_VIEW,
        text: <FormattedMessage id="viewTypeSelection.parameterView" />,
        icon: ParameterViewIcon,
        enabled: canAddParameterView(myPermissions),
      },
      {
        id: IMAGE_STRIP_VIEW,
        text: <FormattedMessage id="viewTypeSelection.imageStripView" />,
        icon: ImageStripViewIcon,
        enabled: canAddImageStripView(myPermissions),
      },
      {
        id: LIST_VIEW,
        text: <FormattedMessage id="viewTypeSelection.ListView" />,
        icon: ListViewIcon,
        enabled: canAddListView(myPermissions),
      },
      {
        id: TOOLSET_VIEW,
        text: <FormattedMessage id="viewTypeSelection.toolset" />,
        icon: toolsetIcon,
        enabled: canAddToolSetView(myPermissions),
      },
      {
        id: INPUT_TOOL_VIEW,
        text: <FormattedMessage id="viewTypeSelection.inputTool" />,
        icon: editIcon,
        enabled: canAddInputToolView(myPermissions),
      },
    ];
    const cancelButtonProps = {
      text: <FormattedMessage id="viewTypeSelection.back" />,
      icon: BackIcon,
      goBack,
    };

    return (
      <div>
        <ListSelection
          headline={<FormattedMessage id="viewTypeSelection.selectViewType" />}
          onSelection={onSelection}
          cancelButtonProps={cancelButtonProps}
          showCancel={showCancel}
          selectionItems={inspectionTypes}
        />
      </div>
    );
  }
}

ViewTypeSelection.defaultProps = {};

ViewTypeSelection.propTypes = {
  onSelection: PropTypes.func.isRequired,
  goBack: PropTypes.func,
  showCancel: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  myPermissions: state.app.myPermissions,
});

export default connect(mapStateToProps)(ViewTypeSelection);
