import { Key, memo, useCallback, useMemo } from 'react';
import { useIntl } from 'react-intl';
import { useSelector } from 'react-redux';
import EditIcon from '../../../assets/icons/edit.svg';
import ObjectSelector, { TreeNodeSchema } from '../../../components/ObjectSelector';
import IconButton from '../../../components/buttons/IconButton';
import type Configuration from '../../../protobuf/Configuration';

interface ToolListSelectorProps {
  inspectionId: string;
  onSelect: (node: TreeNodeSchema) => void;
  expandedKeys?: Key[];
  setExpandedKeys?: (keys: Key[]) => void;
}

export const ToolListSelector = memo(
  ({ inspectionId, expandedKeys, setExpandedKeys, onSelect }: ToolListSelectorProps) => {
    const intl = useIntl();
    const configuration = useSelector<any>((state) => state?.app?.configuration) as Configuration;

    const makeTreeEditable = useCallback(
      (node: TreeNodeSchema): TreeNodeSchema => {
        if (node.children?.length > 0) {
          return {
            ...node,
            children: node.children.map(makeTreeEditable),
          };
        }
        return {
          ...node,
          styledText: (
            <div className="tool-entry" onClick={() => onSelect(node)}>
              <span>{node.name}</span>{' '}
              <IconButton
                tooltip={intl.messages['inputToolView.edit']}
                onClick={() => void 0}
                style={{ height: 20, width: 20 }}
                iconUrl={EditIcon}
              />
            </div>
          ),
        };
      },
      [intl.messages, onSelect],
    );

    const tree = useMemo(() => {
      if (!configuration) {
        return { children: [] };
      }

      const tree =
        configuration.getRemotelySettableObjectSelectorItemsForInspection(inspectionId)?.tree;
      const filteredTree: TreeNodeSchema = tree
        ? configuration.filterArrayValuesFromTree(tree)
        : {};

      return {
        ...filteredTree,
        children: (filteredTree?.children ?? []).map(makeTreeEditable),
      };
    }, [configuration, inspectionId, makeTreeEditable]);

    return (
      <ObjectSelector
        data={tree}
        checkable={false}
        onExpand={setExpandedKeys}
        expandedKeys={expandedKeys}
      />
    );
  },
);
