import { forwardRef, useEffect, useImperativeHandle, useRef } from 'react';
import clsx from 'clsx';
import Prism from 'prismjs';
import { useIntl } from 'react-intl';
import Editor from 'react-simple-code-editor';
import 'prismjs/components/prism-sql';
import 'prismjs/plugins/match-braces/prism-match-braces.js';

import 'prismjs/themes/prism-solarizedlight.css';
import 'prismjs/plugins/match-braces/prism-match-braces.css';
import './FormulaEditor.scss';

const language = 'sql';

export interface FormulaEditorRef {
  getSelectionPosition: () => { start: number; end: number };
  focus: (selectionStart?: number, selectionEnd?: number) => void;
  moveLeft: () => void;
  moveRight: () => void;
  moveCursorTo: (position: number) => void;
}

interface FormulaEditorProps {
  value: string;
  onChange: (value: string) => void;
  onSelectionChange?: (selection: { start: number; end: number }) => void;
  error?: boolean;
}

const Code = ({ children }: { children: string }) => {
  const ref = useRef<HTMLElement>(null);

  useEffect(() => {
    const pre = ref.current?.parentElement;
    if (pre) {
      Prism.highlightAllUnder(pre, false);
    }
  }, [children]);

  return <code ref={ref}>{children}</code>;
};

export const FormulaEditor = forwardRef<FormulaEditorRef, FormulaEditorProps>(
  ({ value, onChange, onSelectionChange, error }, forwardedRef) => {
    const intl = useIntl();
    const ref = useRef<any>(null);

    const moveCursorTo = (position: number) => {
      const textarea: HTMLTextAreaElement = ref.current?._input;
      if (!textarea) {
        return;
      }
      textarea.focus();
      const pos = Math.min(value.length, Math.max(0, position));
      setTimeout(() => textarea.setSelectionRange(pos, pos), 0);
    };

    useEffect(() => {
      if (!onSelectionChange) {
        return;
      }
      const handleSelection = () => {
        if (!ref.current?._input || ref.current._input !== document.activeElement) {
          return;
        }
        const textarea: HTMLTextAreaElement = ref.current._input;
        onSelectionChange({
          start: textarea.selectionStart,
          end: textarea.selectionEnd,
        });
      };

      document.addEventListener('selectionchange', handleSelection);

      return () => {
        document.removeEventListener('selectionchange', handleSelection);
      };
    }, [onSelectionChange]);

    useImperativeHandle(forwardedRef, () => ({
      getSelectionPosition: () => {
        return {
          start: ref.current?._input?.selectionStart ?? 0,
          end: ref.current?._input?.selectionEnd ?? 0,
        };
      },
      focus: (selectionStart, selectionEnd) => {
        const textarea: HTMLTextAreaElement = ref.current?._input;
        if (!textarea) {
          return;
        }
        textarea.focus();
        if (typeof selectionStart === 'number') {
          setTimeout(
            () => textarea.setSelectionRange(selectionStart, selectionEnd ?? selectionStart),
            0,
          );
        }
      },
      moveCursorTo,
      moveLeft: () => moveCursorTo((ref.current?._input?.selectionStart ?? 0) - 1),
      moveRight: () => moveCursorTo((ref.current?._input?.selectionStart ?? 0) + 1),
    }));

    return (
      <Editor
        className={clsx(
          'formula-editor-field',
          error && 'markError',
          `language-${language}`,
          'match-braces',
          'rainbow-braces',
        )}
        ref={ref}
        value={value ?? ''}
        placeholder={intl.messages['toolsetView.formula']}
        onValueChange={onChange}
        highlight={(code) => <Code>{code || ''}</Code>}
        padding={8}
        style={{
          fontSize: 14,
        }}
        autoFocus
      />
    );
  },
);
