import { useState } from 'react';
import { TextField } from '@material-ui/core';
import { useIntl } from 'react-intl';
import EditIcon from '../../../assets/icons/edit.svg';
import IconButton from '../../../components/buttons/IconButton';
import { CommonEditorProps } from './common';

import './RawEditor.scss';

interface RawEditorProps {
  numeric?: boolean;
  onBlur?: () => void;
  exitEditModeOnBlur?: boolean;
}

export const RawEditor = ({
  numeric,
  value,
  onChange,
  onBlur,
  exitEditModeOnBlur,
}: CommonEditorProps<string> & RawEditorProps) => {
  const intl = useIntl();

  const [editMode, setEditMode] = useState(false);

  return (
    <div className="raw-editor">
      {editMode ? (
        <TextField
          type={numeric ? 'number' : 'text'}
          fullWidth
          value={value}
          onChange={(event) => onChange(event.target.value ?? '')}
          onBlur={() => {
            if (exitEditModeOnBlur) {
              setEditMode(false);
            }
            onBlur?.();
          }}
          autoFocus
        />
      ) : (
        <div className="view-mode">
          <span>{value}</span>
          <IconButton
            tooltip={intl.messages['inputToolView.edit']}
            onClick={() => setEditMode(true)}
            style={{ height: 20, width: 20 }}
            iconUrl={EditIcon}
          />
        </div>
      )}
    </div>
  );
};
