import React, { useState } from 'react';
import { Box } from '@material-ui/core';
import deleteIcon from '../../../../assets/icons/delete.svg';
import AutoComplete from '../../../AutoComplete';
import ObjectSelector from '../../../ObjectSelector';
import { OutlinedSelection } from '../../../OutlinedSelection';
import IconButton from '../../../buttons/IconButton';
import { treeEntityNameGenerator } from '../ImageForm';
import InformationBox from '../InformationBox';
import { COMPONENT_TYPE_AUTOCOMPLETE } from '../TriggerSettings';

const PropertyForm = ({
  id,
  object,
  propertyName,
  allowedVariables,
  onDelete,
  inspection,
  excludedFields = [],
  valueFieldType = COMPONENT_TYPE_AUTOCOMPLETE,
  informationText = <></>,
}) => {
  const [treeNodeId, setTreeNodeId] = useState(object[propertyName].treeNodeId || 0);

  const [name, setName] = useState(
    typeof object[propertyName] === 'object' ? object[propertyName].name : '',
  );
  const [value, setValue] = useState(
    typeof object[propertyName] === 'object' ? object[propertyName].value : object[propertyName],
  );
  const [enabled, setEnabled] = useState(
    (typeof object[propertyName] === 'object' && object[propertyName].enabled) || true,
  );

  const getPropertyInputs = () => {
    return (
      <div className="field-name-type">
        <Box sx={{ mr: 1 }}>
          {!excludedFields.includes('name') && (
            <AutoComplete
              label="Name"
              fullWidth
              value={name}
              onChange={(value) => {
                setName(value);
                object[propertyName].name = value;
              }}
              variant="outlined"
              size="small"
              className="item"
              suggestionList={allowedVariables}
            />
          )}
        </Box>
        <Box sx={{ mr: 1 }}>
          {!excludedFields.includes('value') &&
            allowedVariables &&
            (valueFieldType === COMPONENT_TYPE_AUTOCOMPLETE ? (
              <AutoComplete
                label="Value"
                fullWidth
                value={value}
                onChange={(value) => {
                  setValue(value);
                  object[propertyName].value = value;
                }}
                variant="outlined"
                size="small"
                className="item"
                suggestionList={allowedVariables}
              />
            ) : (
              ('node' in allowedVariables || 'children' in allowedVariables) && (
                <ObjectSelector
                  singleSelect
                  selection={[treeNodeId]}
                  expandedKeys={[treeNodeId]}
                  data={allowedVariables}
                  onChange={(selectedId) => {
                    object[propertyName].treeNodeId = selectedId;
                    const fullLocation = treeEntityNameGenerator(
                      selectedId,
                      allowedVariables,
                      inspection,
                    );
                    object[propertyName].value = '${VALUE:' + fullLocation + '}';
                    setTreeNodeId(selectedId);
                  }}
                  treeStyle={{ overflow: 'scroll', height: '250px' }}
                />
              )
            ))}
        </Box>
        {!excludedFields.includes('enabled') && (
          <div>
            <OutlinedSelection
              name="Enabled"
              selectedValue={enabled.toString()}
              values={['true', 'false']}
              onSelectionChanged={(e) => {
                setEnabled(e.enabled === 'true');
                object[propertyName].enabled = e.enabled === 'true';
              }}
              id="enabled"
            />
          </div>
        )}
        <div>
          <IconButton tooltip="Add property" onClick={() => onDelete()} iconUrl={deleteIcon} />
        </div>
        {informationText && <InformationBox informationText={informationText} />}
      </div>
    );
  };

  return (
    <div className="field-name-type">
      <div className="item">
        <div className="property-id">{id}</div>
      </div>
      <div className="inputs">{getPropertyInputs(object[propertyName])}</div>
    </div>
  );
};

export default PropertyForm;
