import { useState, useCallback } from 'react';
import { useSelector } from 'react-redux';
import Protobuf from '../../../../protobuf/Protobuf';
import type Proxy from '../../../../protobuf/Proxy';
import logger from '../../../../utils/logger';

export const useFormulaValidate = (inspectionID: number, equationId: number) => {
  const proxy = useSelector<any>((state) => state?.app?.proxy) as Proxy;

  const [validating, setValidating] = useState(false);
  const [validatedFormula, setValidatedFormula] = useState('');
  const [validationSuccessResult, setValidationSuccessResult] = useState<number | null>(null);
  const [validationErrorMessages, setValidationErrorMessages] = useState<string[]>([]);
  const [errorPosition, setErrorPosition] = useState<number | null>(null);

  const reset = useCallback(() => {
    setValidatedFormula('');
    setValidationSuccessResult(null);
    setValidationErrorMessages([]);
    setErrorPosition(null);
  }, []);

  const sendValidateAndSetEquationRequest = useCallback(
    (equationText: string) =>
      proxy.protobuf.request(
        Protobuf.SvPb.InspectionCmdRequest.create({
          inspectionID,
          validateAndSetEquationRequest: {
            objectId: equationId,
            equationText: equationText.replace(/([^\r])\n/g, '$1\r\n'),
            bSetValue: false,
          },
        }),
      ),
    [equationId, inspectionID, proxy.protobuf],
  );

  const validate = useCallback(
    async (equationText: string) => {
      try {
        setValidating(true);
        const res = await sendValidateAndSetEquationRequest(equationText);
        setValidating(false);

        setValidatedFormula(equationText);

        const status = res?.validateAndSetEquationResponse?.validateStatus; //it is also position of the error

        if (status === -1) {
          setValidationSuccessResult(res?.validateAndSetEquationResponse?.result ?? null);
          setValidationErrorMessages([]);
          return {
            success: true,
            resultValue: res?.validateAndSetEquationResponse?.result ?? null,
          };
        } else {
          const errors = (res?.validateAndSetEquationResponse?.Messages?.messages ?? []).map(
            (m: any) => m.messageText,
          );
          if (!errors.length) {
            errors.push('Incorrect formula');
          }
          setValidationErrorMessages(errors);
          setValidationSuccessResult(null);
          setErrorPosition(status);
          return { success: false, errorPosition: status };
        }
      } catch (e) {
        logger.error(e instanceof Error ? e.message : String(e));
        setValidationErrorMessages([]);
      }
      setValidationSuccessResult(null);
      return { success: false };
    },
    [sendValidateAndSetEquationRequest],
  );

  return {
    validate,
    validating,
    validatedFormula,
    reset,
    validationSuccessResult,
    validationErrorMessages,
    errorPosition,
  };
};
