import { CircularProgress, TextField } from '@material-ui/core';
import { CommonMathToolProps } from '../common';

export const CommentTab = ({ value, onChange, loading }: CommonMathToolProps<string>) => {
  const handleChange: React.ChangeEventHandler<HTMLTextAreaElement | HTMLInputElement> = (
    event,
  ) => {
    onChange(event.target.value);
  };

  return (
    <div className="tab-body">
      {loading ? (
        <CircularProgress />
      ) : (
        <TextField
          autoFocus
          multiline
          minRows={4}
          value={value}
          onChange={handleChange}
          variant="outlined"
          fullWidth
        />
      )}
    </div>
  );
};
