import { useEffect, useMemo, useState } from 'react';
import { Button, CircularProgress } from '@material-ui/core';
import { useIntl } from 'react-intl';
import { ToolsetTreeNode } from '..';
import logger from '../../../utils/logger';
import { CommonHookProps, CommonMathToolProps } from '../common';
import { CommentTab } from './CommentTab';
import { FormulaTab } from './FormulaTab';
import { RangeTab } from './RangeTab';
import { useComment } from './hooks/useComment';
import { useFormula } from './hooks/useFormula';
import { useRange } from './hooks/useRange';

import './style.scss';

export function isMathTool(node?: ToolsetTreeNode) {
  if (!node) {
    return false;
  }
  return (
    (node.objectType === 'SVToolObjectType' && node.objectSubType === 'SVMathToolObjectType') ||
    (node.objectType === 0x7 && //SVObjectTypeEnum.SVToolObjectType
      node.objectSubType === 0x12) //SVObjectSubTypeEnum.SVMathToolObjectType
  );
}

interface MathToolProps extends CommonHookProps {
  view: string;
}

const noDisplayStyle: React.CSSProperties = { display: 'none' };

export const MathTool = ({ inspectionID, data, view }: MathToolProps) => {
  const intl = useIntl();

  const [saving, setSaving] = useState(false);
  const [errors, setErrors] = useState<string[]>([]);
  const [success, setSuccess] = useState(false);
  const [showOptions, setShowOptions] = useState(true);

  const commonProps = { inspectionID, data };
  const formula = useFormula(commonProps);
  const conditional = useFormula({ ...commonProps, conditional: true });
  const range = useRange(commonProps);
  const comment = useComment(commonProps);

  useEffect(() => {
    if (comment.modified || range.modified || formula.modified || conditional.modified) {
      setSuccess(false);
    }
  }, [comment.modified, conditional.modified, formula.modified, range.modified]);

  useEffect(() => {
    setSuccess(false);
    setErrors([]);
  }, [view]);

  const toggleOptions = (showOptions: boolean) => {
    setShowOptions(showOptions);
    if (!showOptions) {
      setSuccess(false);
      setErrors([]);
    }
  };

  const views = useMemo<
    {
      name: string;
      component: ({ value, onChange, loading }: CommonMathToolProps<any>) => JSX.Element;
      props: CommonMathToolProps<any>;
    }[]
  >(
    () => [
      {
        name: 'Formula',
        component: FormulaTab,
        props: {
          inspectionID,
          value: formula.value,
          onChange: formula.setValue,
          toggleOptions,
          loading: formula.loading,
        },
      },
      {
        name: 'Range',
        component: RangeTab,
        props: {
          inspectionID,
          value: range.value,
          onChange: range.setValue,
          toggleOptions,
          loading: range.loading,
        },
      },
      {
        name: 'Conditional',
        component: FormulaTab,
        props: {
          inspectionID,
          value: conditional.value,
          onChange: conditional.setValue,
          toggleOptions,
          loading: conditional.loading,
          conditional: true,
        },
      },
      {
        name: 'Comment',
        component: CommentTab,
        props: {
          inspectionID,
          value: comment.value,
          onChange: comment.setValue,
          toggleOptions,
          loading: comment.loading,
        },
      },
    ],
    [
      inspectionID,
      formula.value,
      formula.setValue,
      formula.loading,
      range.value,
      range.setValue,
      range.loading,
      conditional.value,
      conditional.setValue,
      conditional.loading,
      comment.value,
      comment.setValue,
      comment.loading,
    ],
  );

  const modified = useMemo(() => {
    switch (view) {
      case 'Comment':
        return comment.modified;
      case 'Range':
        return range.modified;
      case 'Formula':
        return formula.modified;
      case 'Conditional':
        return conditional.modified;
    }
    return false;
  }, [comment.modified, conditional.modified, formula.modified, range.modified, view]);

  const saveChanges = async () => {
    setSaving(true);
    setSuccess(false);
    try {
      const commentErrors = await comment.saveChanges();
      const rangeErrors = await range.saveChanges();
      const formulaErrors = await formula.saveChanges();
      const conditionalErrors = await conditional.saveChanges();

      const totalErrors = [
        ...commentErrors,
        ...rangeErrors,
        ...formulaErrors,
        ...conditionalErrors,
      ];
      setErrors(totalErrors);
      setSuccess(!totalErrors?.length);
    } catch (e) {
      logger.error(e instanceof Error ? e.message : String(e));
      setErrors([e instanceof Error ? e.message : String(e)]);
    }
    setSaving(false);
  };

  return (
    <div className="math-tool">
      <header>
        {data.name}&nbsp;|&nbsp;{view}
        {modified ? '*' : ''}
      </header>
      <div className="math-tool-body">
        <div className="content">
          {views.map((t) => (
            <div key={t.name} style={view === t.name ? {} : noDisplayStyle}>
              <t.component {...t.props} />
            </div>
          ))}
        </div>
      </div>
      {showOptions && (
        <div className="options">
          <Button
            variant="contained"
            color="primary"
            onClick={saveChanges}
            disabled={saving || !modified}
            style={{ minWidth: 128 }}
          >
            {saving ? <CircularProgress size={24} /> : intl.messages['save']}
          </Button>
          {!saving && errors?.length > 0 && (
            <div className="errors">
              {errors.map((err, index) => (
                <span key={index}>{err}</span>
              ))}
            </div>
          )}
          {!saving && success && (
            <div className="success">{intl.messages['toolsetView.savingSuccess']}</div>
          )}
        </div>
      )}
    </div>
  );
};
