import { Key } from 'react';
import { useIntl } from 'react-intl';
import shortId from 'shortid';

import './style.scss';
import { TOGGLE_BUTTON, SEPARATOR } from '../../types/ActionBarType';
import IconButton from '../buttons/IconButton';
import { ToggleButton } from '../buttons/ToggleButton';

export interface ActionBarType {
  id?: Key;
  intlId: string;
  onClickHandler: () => void;
  iconUrl: string;
  enabled?: boolean;
  type?: typeof TOGGLE_BUTTON | typeof SEPARATOR;
  showAlternative?: boolean;
  intlIdAlternative?: string;
  iconUrlAlternative?: string;
  onClickHandlerAlternative?: () => void;
}

interface ActionBarProps {
  actions: ActionBarType[];
  isViewAction?: boolean;
}

const ActionBar = ({ actions, isViewAction = false }: ActionBarProps) => {
  const intl = useIntl();

  return (
    <div
      className="action-bar-wrapper"
      style={{ position: isViewAction ? 'absolute' : 'relative' }}
    >
      {actions &&
        actions.map((action) => {
          if (action && action.type === SEPARATOR) {
            return (
              <div className="action-bar-separator" key={`separator_${shortId.generate()}`}>
                <div className="separator"></div>
              </div>
            );
          }
          if (action && action.type === TOGGLE_BUTTON) {
            return (
              <ToggleButton
                key={action.id}
                id={action.id}
                intlId={action.intlId}
                iconUrl={action.iconUrl}
                enabled={action.enabled}
                onClickHandler={action.onClickHandler}
                intlIdAlternative={action.intlIdAlternative}
                iconUrlAlternative={action.iconUrlAlternative}
                onClickHandlerAlternative={action.onClickHandlerAlternative}
                showAlternative={action.showAlternative}
              />
            );
          }

          return (
            <IconButton
              key={action.intlId}
              tooltip={intl.messages[`${action.intlId}` as never]}
              onClick={() => {
                action.onClickHandler();
              }}
              iconUrl={action.iconUrl}
              enabled={action.enabled}
            />
          );
        })}
    </div>
  );
};

export default ActionBar;
