import { useEffect, useState } from 'react';
import { Button, CircularProgress, MenuItem, Select, TextField } from '@material-ui/core';
import clsx from 'clsx';
import { useIntl } from 'react-intl';
import { useSelector } from 'react-redux';
import EditIcon from '../../../assets/icons/edit.svg';
import IconButton, { IconButtonProps } from '../../../components/buttons/IconButton';
import Product from '../../../protobuf/Product';
import type Proxy from '../../../protobuf/Proxy';
import { NumberEditor } from '../../InputToolView/components/NumberEditor';
import { CommonMathToolProps, EmbeddedIdEnum, LinkedSelectedType } from '../common';
import { FormulaEditPanel } from './FormulaEditPanel';
import { LinkedValueSelectDialog } from './LinkedValueSelectDialog';
import { ReadonlyFormula } from './ReadonlyFormula';
import { useIndirectValueObjectName } from './hooks/useIndirectValueObjectName';
import {
  clampRangeValue,
  compareRangeValues,
  MAX_RANGE_VALUE,
  MIN_RANGE_VALUE,
  Range,
  RangeValue,
} from './hooks/useRange';

import './RangeTab.scss';

function getRangeClassName(classEnum: RangeValue['rangeClass']) {
  switch (classEnum) {
    case EmbeddedIdEnum.RangeClassFailHighEId:
      return 'Fail high';
    case EmbeddedIdEnum.RangeClassFailLowEId:
      return 'Fail low';
    case EmbeddedIdEnum.RangeClassWarnHighEId:
      return 'Warn high';
    case EmbeddedIdEnum.RangeClassWarnLowEId:
      return 'Warn low';
  }
}

const RangeValuePreview = ({
  inspectionID,
  rangeValue,
  onOpenEditView,
}: {
  inspectionID: number;
  rangeValue?: RangeValue;
  onOpenEditView: (value: RangeValue) => void;
}) => {
  const intl = useIntl();

  if (!rangeValue) {
    return <span>-</span>;
  }

  const editButtonProps: IconButtonProps = {
    onClick: () => onOpenEditView(rangeValue),
    style: { height: 20, width: 20 },
    iconUrl: EditIcon,
    tooltip: intl.messages['inputToolView.edit'],
  };

  switch (rangeValue.type) {
    case LinkedSelectedType.DirectValue:
      return (
        <div className="range-value-preview">
          <span>{rangeValue.value}</span>
          <IconButton {...editButtonProps} />
        </div>
      );
    case LinkedSelectedType.IndirectValue:
      return (
        <div className="range-value-preview">
          <IndirectValue
            inspectionId={inspectionID}
            objectId={rangeValue.indirectObjectId}
            defaultValue={rangeValue.value}
          />
          <IconButton {...editButtonProps} />
          <span className="info">
            ({intl.messages['toolsetView.link']}:&ensp;
            <i>
              <IndirectValueObjectName
                inspectionID={inspectionID}
                value={rangeValue.indirectObjectId}
              />
            </i>
            )
          </span>
        </div>
      );
    case LinkedSelectedType.Formula:
      return (
        <div className="range-value-preview">
          <span>{rangeValue.value}</span>
          <IconButton {...editButtonProps} />
          <span className="info">
            (Formula:&ensp;
            <i>
              <ReadonlyFormula style={{ whiteSpace: 'nowrap' }}>
                {rangeValue.formula}
              </ReadonlyFormula>
            </i>
            )
          </span>
        </div>
      );
  }
};

const IndirectValueObjectName = ({
  inspectionID,
  value,
}: {
  inspectionID: number;
  value?: number;
}) => {
  const { name } = useIndirectValueObjectName(inspectionID, value);

  return <span>{name}</span>;
};

const IndirectValue = ({
  inspectionId,
  objectId,
  defaultValue,
}: {
  inspectionId: number;
  objectId?: number;
  defaultValue: number;
}) => {
  const proxy = useSelector<any>((state) => state?.app?.proxy) as Proxy;

  const [value, setValue] = useState(defaultValue);
  const [error, setError] = useState(false);

  useEffect(() => {
    const onProduct = ({ product }: { product: Product }) => {
      const productValue = product
        ?.getValuesAsString([objectId], '')
        ?.find((entry: any) => parseInt(entry.id) === objectId)?.value;

      setError(productValue === '');
      setValue(productValue || defaultValue);
      return Promise.resolve();
    };

    const startAtTriggerCount = -1;
    const desc = {
      inspectionId,
      valueIds: [objectId],
      startAtTriggerCount,
    };
    const productSub = proxy.subscribeToProductStream(desc, onProduct);

    return () => {
      if (productSub) {
        proxy.unsubscribeFromProductStream(productSub);
      }
    };
  }, [defaultValue, inspectionId, objectId, proxy]);

  return <span className={clsx(error && 'indirect-value-error')}>{value ?? '-'}</span>;
};

const RangeValueEditor = ({
  inspectionID,
  value,
  onExit,
  onChange,
}: Omit<CommonMathToolProps<RangeValue>, 'loading' | 'toggleOptions'> & { onExit: () => void }) => {
  const intl = useIntl();

  const [internalValue, setInternalValue] = useState(value);
  const [openLinkedValueSelectDialog, setOpenLinkedValueSelectDialog] = useState(false);
  const [openFormulaEditDialog, setOpenFormulaEditDialog] = useState(false);

  useEffect(() => {
    setInternalValue(value);
  }, [value]);

  const applyChanges = () => {
    onChange(internalValue);
    onExit();
  };

  const modified = !compareRangeValues(value, internalValue);
  const invalid =
    (internalValue.type === LinkedSelectedType.IndirectValue && !internalValue.indirectObjectId) ||
    (internalValue.type === LinkedSelectedType.Formula && !internalValue.formula);

  return (
    <div className="range-value-editor">
      <header>{getRangeClassName(internalValue.rangeClass)}</header>
      <div className="type-options">
        <label>{intl.messages['inputToolView.type']}:</label>
        <Select
          defaultValue={internalValue.type}
          value={internalValue.type}
          onChange={(event) =>
            setInternalValue({ ...internalValue, type: event.target.value as LinkedSelectedType })
          }
          style={{ minWidth: 128, width: 'auto' }}
        >
          <MenuItem value={LinkedSelectedType.DirectValue}>
            {intl.messages['toolsetView.value']}
          </MenuItem>
          <MenuItem value={LinkedSelectedType.IndirectValue}>
            {intl.messages['toolsetView.link']}
          </MenuItem>
          <MenuItem value={LinkedSelectedType.Formula}>
            {intl.messages['toolsetView.formula']}
          </MenuItem>
        </Select>
      </div>
      {internalValue.type === LinkedSelectedType.DirectValue && (
        <div className="direct-editor-container">
          <div className="direct-editor">
            <label>{intl.messages['toolsetView.value']}:</label>
            <TextField
              fullWidth
              type="number"
              value={internalValue.value}
              onChange={(event) =>
                setInternalValue({
                  ...internalValue,
                  value: clampRangeValue(parseFloat(event.target.value ?? 0)),
                })
              }
              autoFocus
            />
          </div>
          <NumberEditor
            value={internalValue.value}
            onChange={(value) =>
              setInternalValue({
                ...internalValue,
                value: clampRangeValue(value),
              })
            }
            min={MIN_RANGE_VALUE}
            max={MAX_RANGE_VALUE}
            hideRawEditor
          />
        </div>
      )}
      {internalValue.type === LinkedSelectedType.IndirectValue && (
        <div className="indirect-editor">
          <label>{intl.messages['toolsetView.value']}:</label>
          <IndirectValue
            inspectionId={inspectionID}
            objectId={internalValue.indirectObjectId}
            defaultValue={internalValue.value}
          />
          <label>{intl.messages['toolsetView.link']}:</label>
          <span style={{ display: 'flex', alignItems: 'center' }}>
            <IndirectValueObjectName
              inspectionID={inspectionID}
              value={internalValue.indirectObjectId}
            />
            <IconButton
              onClick={() => setOpenLinkedValueSelectDialog(true)}
              style={{ height: 20, width: 20, marginLeft: 8 }}
              iconUrl={EditIcon}
              tooltip={intl.messages['inputToolView.edit']}
            />
          </span>
          {openLinkedValueSelectDialog && (
            <LinkedValueSelectDialog
              inspectionID={internalValue.inspectionID}
              value={internalValue.indirectObjectId}
              onClose={() => setOpenLinkedValueSelectDialog(false)}
              onSelect={(objectId) => {
                setInternalValue({
                  ...internalValue,
                  indirectObjectId: objectId,
                });
                setOpenLinkedValueSelectDialog(false);
              }}
            />
          )}
        </div>
      )}
      {internalValue.type === LinkedSelectedType.Formula && (
        <div className="formula-editor">
          <label>{intl.messages['toolsetView.value']}:</label>
          <span>{internalValue.value}</span>
          <label>{intl.messages['toolsetView.formula']}:</label>
          <span style={{ display: 'flex', alignItems: 'center' }}>
            <ReadonlyFormula>{internalValue.formula}</ReadonlyFormula>
            <IconButton
              onClick={() => setOpenFormulaEditDialog(true)}
              style={{ height: 20, width: 20, marginLeft: 8 }}
              iconUrl={EditIcon}
              tooltip={intl.messages['inputToolView.edit']}
            />
          </span>
          {openFormulaEditDialog && (
            <FormulaEditPanel
              showInDialog
              inspectionID={internalValue.inspectionID}
              equationId={internalValue.equationId}
              equationText={internalValue.formula}
              onClose={() => setOpenFormulaEditDialog(false)}
              onSave={(equationText, validationResultValue) => {
                setInternalValue({
                  ...internalValue,
                  formula: equationText,
                  value: validationResultValue ?? internalValue.value,
                });
                setOpenFormulaEditDialog(false);
              }}
            />
          )}
        </div>
      )}
      <div className="options">
        <Button variant="outlined" color="primary" onClick={onExit} style={{ minWidth: 128 }}>
          {intl.messages['cancel']}
        </Button>
        <Button
          variant="contained"
          color="primary"
          onClick={applyChanges}
          style={{ minWidth: 128 }}
          disabled={!modified || invalid}
        >
          {intl.messages['ok']}
        </Button>
      </div>
    </div>
  );
};

export const RangeTab = ({
  inspectionID,
  value,
  onChange,
  toggleOptions,
  loading,
}: CommonMathToolProps<Range>) => {
  const [editingValue, setEditingValue] = useState<RangeValue | null>(null);

  useEffect(() => {
    toggleOptions(!editingValue);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [!!editingValue]);

  const handleChange = (rangeValue: RangeValue) => {
    setEditingValue(rangeValue);

    switch (rangeValue.rangeClass) {
      case EmbeddedIdEnum.RangeClassFailHighEId:
        onChange({ ...value, failHigh: rangeValue });
        break;
      case EmbeddedIdEnum.RangeClassFailLowEId:
        onChange({ ...value, failLow: rangeValue });
        break;
      case EmbeddedIdEnum.RangeClassWarnHighEId:
        onChange({ ...value, warnHigh: rangeValue });
        break;
      case EmbeddedIdEnum.RangeClassWarnLowEId:
        onChange({ ...value, warnLow: rangeValue });
        break;
    }
  };

  return (
    <div className="tab-body range-tab">
      {loading ? (
        <CircularProgress />
      ) : editingValue ? (
        <RangeValueEditor
          inspectionID={inspectionID}
          value={editingValue}
          onExit={() => setEditingValue(null)}
          onChange={handleChange}
        />
      ) : (
        <div className="values-list">
          <label>{getRangeClassName(EmbeddedIdEnum.RangeClassFailHighEId)}:</label>
          <RangeValuePreview
            inspectionID={inspectionID}
            rangeValue={value.failHigh}
            onOpenEditView={setEditingValue}
          />
          <label>{getRangeClassName(EmbeddedIdEnum.RangeClassWarnHighEId)}:</label>
          <RangeValuePreview
            inspectionID={inspectionID}
            rangeValue={value.warnHigh}
            onOpenEditView={setEditingValue}
          />
          <label>{getRangeClassName(EmbeddedIdEnum.RangeClassWarnLowEId)}:</label>
          <RangeValuePreview
            inspectionID={inspectionID}
            rangeValue={value.warnLow}
            onOpenEditView={setEditingValue}
          />
          <label>{getRangeClassName(EmbeddedIdEnum.RangeClassFailLowEId)}:</label>
          <RangeValuePreview
            inspectionID={inspectionID}
            rangeValue={value.failLow}
            onOpenEditView={setEditingValue}
          />
        </div>
      )}
    </div>
  );
};
