import { useMemo } from 'react';
import { FormattedMessage } from 'react-intl';
import { useSelector } from 'react-redux';
import iconPause from '../../../assets/icons/pause.svg';
import iconPlayMultiple from '../../../assets/icons/play-multiple.svg';
import iconPlay from '../../../assets/icons/play.svg';
import Configuration from '../../../protobuf/Configuration';
import type Proxy from '../../../protobuf/Proxy';
import { canEditInspectionState, canReadInspectionState } from '../../../utils/permissions';
import { NavigationItemType } from '../NavigationItem';

export const useInspectionStateNavigation = (): NavigationItemType | undefined => {
  const isSVOConfigLoaded = useSelector<any>((state) => state.viewSet.isSVOConfigLoaded);
  const { proxy, configuration, pausedInspections, myPermissions } = useSelector<any>(
    (state) => state.app,
  ) as {
    proxy: Proxy;
    configuration: Configuration;
    pausedInspections: string[];
    myPermissions: Set<string>;
  };
  const inspections = useMemo(
    () => (configuration && configuration.getInspections()) || [],
    [configuration],
  );
  const allRunning = pausedInspections.length === 0;
  const canRead = canReadInspectionState(myPermissions);
  const canEdit = canEditInspectionState(myPermissions);

  const children = useMemo(() => {
    const items = inspections.map<NavigationItemType>((inspection: any) => {
      const isPaused = (pausedInspections || []).includes(inspection.id);

      return {
        id: inspection.id,
        onClick: () => {
          isPaused
            ? proxy.resumeRejectStream(inspection.id)
            : proxy.pauseRejectStream(inspection.id);
        },
        content: inspection.name,
        icon: isPaused ? iconPlay : iconPause,
        className: isPaused ? 'paused-inspection' : undefined,
        enabled: true,
      };
    });

    items.unshift({
      id: 'globalResumePause',
      onClick: () => {
        allRunning ? proxy.pauseRejectStream() : proxy.resumeRejectStream();
      },
      content: allRunning ? 'Pause all inspections' : 'Resume all inspections',
      icon: allRunning ? iconPause : iconPlay,
      className: 'all-inspections',
      enabled: true,
    });

    return items;
  }, [allRunning, inspections, pausedInspections, proxy]);

  if (!isSVOConfigLoaded || (!canRead && !canEdit)) {
    return undefined;
  }

  return {
    id: 'inspection-state-navigation',
    icon: iconPlayMultiple,
    content: (
      <FormattedMessage
        id="topNavigation.inspectionState"
        values={{
          runningInspections: inspections.length - pausedInspections.length,
          numberOfInspections: inspections.length,
        }}
      />
    ),
    enabled: true,
    children: canEdit ? children : undefined,
  };
};
