import { Key, useState } from 'react';
import { ToggleButtonGroup, ToggleButton } from '@material-ui/lab';
import './Tabs.scss';

interface TabsProps {
  options: { value: Key; label: React.ReactNode; component: React.ReactNode }[];
}

export const Tabs = ({ options }: TabsProps) => {
  const [activeTab, setActiveTab] = useState(options[0]?.value);

  return (
    <div className="tabs">
      <nav>
        <ToggleButtonGroup
          color="primary"
          value={activeTab}
          exclusive
          onChange={(_, value) => value && setActiveTab(value)}
        >
          {options.map(({ value, label }) => (
            <ToggleButton key={value} value={value} size="small">
              {label}
            </ToggleButton>
          ))}
        </ToggleButtonGroup>
      </nav>
      <div>{options.find((option) => option.value === activeTab)?.component}</div>
    </div>
  );
};
