import { useCallback, useEffect, useMemo, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import iconDeleteDustbin from '../../../../assets/icons/delete-dustbin.svg';
import iconPlus from '../../../../assets/icons/plus.svg';
import ActionBar, { ActionBarType } from '../../../../components/ActionBar/index';
import { OverlayList } from '../../../../components/OverlayList';
import { initViewState, setSharedStateData, setSaveFlag } from '../../../../redux/actions';

import logger from '../../../../utils/logger';
import { canEditParameterView } from '../../../../utils/permissions';
import { OverlaySchema, updateOverlayList } from '../../../../utils/reduxHelper';
import { OverlayPropertyList } from './OverlayPropertyList';
import { VariableSelection } from './VariableSelection';

import './style.scss';

interface OverlayProps {
  id: string;
  inspectionId: string;
}

export const Overlay = ({ id: viewId, inspectionId }: OverlayProps) => {
  const myPermissions = useSelector<any>((state) => state.app?.myPermissions) as Set<string>;
  const inspections = useSelector<any>((state) => state.inspections) as any;
  const dispatch = useDispatch();

  const [currentOverlay, setCurrentOverlay] = useState<OverlaySchema | undefined>(undefined);
  const [isObjectSelectorOpen, setIsObjectSelectorOpen] = useState(false);
  const [variableName, setVariableName] = useState('Variables');
  const [variableId, setVariableId] = useState<keyof OverlaySchema | ''>('');
  const [selectedVariables, setSelectedVariables] = useState<(React.Key | React.Key[])[]>([]);

  useEffect(() => {
    dispatch(initViewState(inspectionId, viewId, {}, {}));
  }, [dispatch, inspectionId, viewId]);

  const [editingViewId, overlays, imageDimensions] = useMemo(() => {
    const editingViewId = inspections[inspectionId]?.sharedState?.editingViewId;
    const overlays: OverlaySchema[] =
      inspections[inspectionId]?.sharedState?.views[editingViewId]?.overlays;
    const imageDimensions: { width: number; height: number } =
      inspections[inspectionId]?.views[editingViewId]?.imageDimensions;

    return [editingViewId, overlays, imageDimensions];
  }, [inspectionId, inspections]);

  useEffect(() => {
    if (overlays && currentOverlay) {
      const overlay = overlays.find((item) => item.id === currentOverlay.id);
      if (overlay && overlay !== currentOverlay) {
        setCurrentOverlay(overlay);
      }
    }

    if (!currentOverlay && overlays?.length > 0) {
      setCurrentOverlay(overlays[0]);
    }
  }, [currentOverlay, overlays, editingViewId]);

  useEffect(() => {
    setCurrentOverlay(overlays?.[0]);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [editingViewId]);

  const overlayActions = useMemo(() => {
    const addNewOverlay = () => {
      const newOverlay: OverlaySchema = {
        color: '#ebfc02',
        height: 100,
        id: Math.floor(Math.random() * 10000),
        isActive: true,
        label: 'Overlay',
        lineStyle: 'solid',
        width: 100,
        x: 10,
        y: 20,
        offsetLeft: null,
        offsetTop: null,
      };
      try {
        dispatch(setSharedStateData(inspectionId, 'overlays', [...overlays, newOverlay]));
        setCurrentOverlay(newOverlay);
      } catch (e) {
        logger.error(e instanceof Error ? e.message : String(e));
      }
    };

    const getNeighborOverlayId = (id: number, overlays: OverlaySchema[]) => {
      for (let i = 0; i < overlays.length; i++) {
        if (overlays[i]['id'] === id) {
          if (overlays[i - 1]) {
            return overlays[i - 1]['id'];
          } else if (overlays[i + 1]) {
            return overlays[i + 1]['id'];
          }
        }
      }
      return -1;
    };

    const deleteCurrentOverlay = () => {
      if (!currentOverlay) {
        return;
      }

      try {
        const updatedOverlays = overlays.filter((overlay) => overlay.id !== currentOverlay.id);
        dispatch(setSharedStateData(inspectionId, 'overlays', updatedOverlays));

        const newCurrentOverlay = getNeighborOverlayId(currentOverlay.id, overlays);
        setCurrentOverlay(overlays.filter((item) => item.id === newCurrentOverlay)[0]);
      } catch (e) {
        logger.error(e instanceof Error ? e.message : String(e));
      }
    };

    const canEdit = canEditParameterView(myPermissions);
    const actions: ActionBarType[] = [
      {
        intlId: 'parameterview.overlay.add',
        iconUrl: iconPlus,
        onClickHandler: () => {
          dispatch(setSaveFlag(true));
          addNewOverlay();
        },
        enabled: canEdit,
      },
      {
        intlId: 'parameterview.overlay.delete',
        iconUrl: iconDeleteDustbin,
        onClickHandler: () => {
          dispatch(setSaveFlag(true));
          deleteCurrentOverlay();
        },
        enabled: canEdit,
      },
    ];

    return actions;
  }, [currentOverlay, dispatch, inspectionId, myPermissions, overlays]);

  const handleUpdateOverlayList = (updatedOverlayList: OverlaySchema[]) => {
    dispatch(setSharedStateData(inspectionId, 'overlays', updatedOverlayList));
    dispatch(setSaveFlag(true));
  };

  const setVariableSelection = useCallback(
    (selectedVariable?: React.Key | React.Key[]) => {
      if (!selectedVariable || variableId === '' || !currentOverlay) {
        setIsObjectSelectorOpen(false);
        return;
      }

      if (selectedVariable && !selectedVariables.includes(selectedVariable)) {
        setSelectedVariables([...selectedVariables, selectedVariable]);
      }

      const updatedOverlay: OverlaySchema = {
        ...currentOverlay,
        [variableId]: selectedVariable,
      };

      const updatedOverlayList = updateOverlayList(updatedOverlay, overlays);
      dispatch(setSharedStateData(inspectionId, 'overlays', updatedOverlayList));
      setIsObjectSelectorOpen(false);
      dispatch(setSaveFlag(true));
    },
    [currentOverlay, dispatch, inspectionId, overlays, selectedVariables, variableId],
  );

  if (!overlays) {
    return (
      <div className="parameter-view-overlay-placeholder">
        Please select a source for this parameter view.
      </div>
    );
  }

  return (
    <div className="parameter-view-overlay-wrapper">
      <div className="parameter-view-overlay-table-wrapper">
        <OverlayList
          overlays={overlays}
          updateCurrentOverlay={setCurrentOverlay}
          updateOverlayList={handleUpdateOverlayList}
          canSelectRow={true}
          onSelectAll={(isActive) => {
            try {
              const updatedOverlays = overlays.map((overlay) => ({
                ...overlay,
                isActive,
              }));
              dispatch(setSharedStateData(inspectionId, 'overlays', updatedOverlays));
              dispatch(setSaveFlag(true));
            } catch (e) {
              logger.error(e instanceof Error ? e.message : String(e));
            }
          }}
        />
        <ActionBar actions={overlayActions} />
      </div>
      {currentOverlay && (
        <OverlayPropertyList
          inspectionId={inspectionId}
          viewId={viewId}
          currentOverlay={currentOverlay}
          imageDimensions={imageDimensions}
          onChange={(changedValue) => {
            const updatedOverlay = { ...currentOverlay, ...changedValue };
            const updatedOverlayList = updateOverlayList(updatedOverlay, overlays);
            dispatch(setSharedStateData(inspectionId, 'overlays', updatedOverlayList));
            setCurrentOverlay(updatedOverlay);
          }}
          openSelection={(variableName, variableId) => {
            setIsObjectSelectorOpen(true);
            setVariableName(variableName);
            setVariableId(variableId);
          }}
        />
      )}
      <VariableSelection
        title={variableName}
        onClose={setVariableSelection}
        open={isObjectSelectorOpen}
        inspectionId={inspectionId}
      />
    </div>
  );
};
