import { useSelector } from 'react-redux';
import shortId from 'shortid';
import logIcon from '../../../assets/icons/description.svg';
import { canReadLogs } from '../../../utils/permissions';
import { NavigationItemType } from '../NavigationItem';

export const useStatusReportNavigation = (): NavigationItemType | undefined => {
  const { notifications, myPermissions } = useSelector<any>((state) => state.app) as {
    notifications: any[];
    myPermissions: Set<string>;
  };

  if (!canReadLogs(myPermissions)) {
    return undefined;
  }

  return {
    id: 'status-report',
    icon: logIcon,
    enabled: true,
    content: notifications[0]?.message,
    children: notifications.map<NavigationItemType>((notification: any) => ({
      id: notification.id ?? notification.timestamp ?? shortId.generate(),
      content: (
        <p key={shortId.generate()} className={`${notification.type} status`}>
          {notification.timestamp}: <b>{notification.message}</b>{' '}
          {notification.count > 1 ? `(${notification.count})` : ''}
        </p>
      ),
      enabled: true,
    })),
  };
};
