import { Key } from 'react';
import { useIntl } from 'react-intl';
import logger from '../../../utils/logger';
import IconButton from '../IconButton';

import './style.scss';

interface ToggleButtonProps {
  id?: Key;
  intlId?: string;
  iconUrl: string;
  enabled: boolean | any;
  onClickHandler?: () => void;
  showAlternative?: boolean;
  intlIdAlternative?: string;
  iconUrlAlternative?: string;
  onClickHandlerAlternative?: () => void;
}

export const ToggleButton = ({
  id,
  intlId,
  iconUrl,
  enabled,
  onClickHandler,
  intlIdAlternative,
  iconUrlAlternative,
  onClickHandlerAlternative,
  showAlternative,
}: ToggleButtonProps) => {
  if (
    !intlId ||
    !iconUrl ||
    !onClickHandler ||
    (showAlternative && (!intlIdAlternative || !iconUrlAlternative || !onClickHandlerAlternative))
  ) {
    logger.error('ToggleButton component did not receive required parameters');
  }

  const intl = useIntl();

  return showAlternative ? (
    <IconButton
      key={id}
      tooltip={intl.messages[`${intlIdAlternative}` as never]}
      onClick={() => {
        onClickHandlerAlternative?.();
      }}
      iconUrl={iconUrlAlternative ?? ''}
      enabled={enabled}
    />
  ) : (
    <IconButton
      key={intlId}
      tooltip={intl.messages[`${intlId}` as never]}
      onClick={() => onClickHandler?.()}
      iconUrl={iconUrl}
      enabled={enabled}
    />
  );
};
