import React, { useState } from 'react';
import { Typography } from '@material-ui/core';
import addIcon from '../../../../assets/icons/plus.svg';
import { OutlinedSelection } from '../../../OutlinedSelection';
import IconButton from '../../../buttons/IconButton';
import PropertyForm from '../PropertyForm';

const batchPropertiesRequiresName = [
  'data1',
  'data2',
  'data3',
  'data4',
  'vdata1',
  'vdata2',
  'vdata3',
  'vdata4',
  'vdata5',
  'vdata6',
  'vdata7',
  'vdata8',
  'vdata9',
  'vdata10',
];

const batchProperties = [
  ...[
    '',
    'batch_inspectionid',
    'timestamp',
    'svimname',
    'inspectionname',
    'batchid',
    'batchname',
    'batchstart',
    'comment',
    'username',
    'machinenum',
    'machinetype',
    'sectionid',
    'sectionname',
    'sectioncomment',
    'productionspeed',
    'headerid',
  ],
  ...batchPropertiesRequiresName,
];

const informationToFieldsMap = {};

batchProperties.forEach((key) => {
  informationToFieldsMap[key] = (
    <>
      <Typography gutterBottom>
        Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in,
        egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.
      </Typography>
      <Typography gutterBottom>
        Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus
        vel augue laoreet rutrum faucibus dolor auctor.
      </Typography>
      <Typography gutterBottom>
        Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel
        scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus
        auctor fringilla.
      </Typography>
    </>
  );
});
const BatchSettings = ({ subscription, allowedVariables }) => {
  const [newBatchProperty, setNewBatchProperty] = useState('');
  const [update, setUpdate] = useState(false);

  const addNewBatchProperty = (subscription) => {
    if (!subscription.batch || newBatchProperty === '') {
      subscription.batch = {};
    }

    subscription.batch[newBatchProperty] = { name: '', value: '', enabled: true };
    setNewBatchProperty('');
  };

  const { batch, inspection } = subscription;
  const batchResult = [];

  Object.keys(batch).map((key) => {
    if (typeof batch[key] === 'string') {
      batch[key] = {
        name: '',
        value: batch[key],
        enabled: true,
      };
    }
    return batch[key];
  });

  const remainingBatchProperties = batchProperties.filter((x) => !Object.keys(batch).includes(x));

  Object.keys(batch).forEach((key) => {
    batchResult.push(
      <PropertyForm
        key={`batch_${inspection}_${key}`}
        id={key}
        object={batch}
        propertyName={key}
        allowedVariables={allowedVariables}
        excludedFields={!batchPropertiesRequiresName.includes(key) ? ['name'] : []}
        onDelete={() => {
          delete batch[key];
          setUpdate(!update);
        }}
        informationText={informationToFieldsMap[key]}
      />,
    );
  });

  return (
    <div className="properties content">
      {batchResult}
      <div className="field-name-type">
        <div className="item">Add new property:</div>
        <div className="inputs">
          <div className="field-name-type">
            <div style={{ width: '200px', padding: '5px 0' }}>
              <OutlinedSelection
                selectedValue={newBatchProperty}
                values={remainingBatchProperties}
                onSelectionChanged={(e) => setNewBatchProperty(e.addbatchProp)}
                name="Property"
                id="addbatchProp"
              />
            </div>
            <div style={{ margin: 'auto 0' }}>
              <IconButton
                tooltip="Add property"
                onClick={() => addNewBatchProperty(subscription)}
                iconUrl={addIcon}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BatchSettings;
