import { Key } from 'react';
import { useIntl } from 'react-intl';
import shortId from 'shortid';

import './style.scss';
import IconButton from '../../../../components/buttons/IconButton';
import { ToggleButton } from '../../../../components/buttons/ToggleButton';
import { TOGGLE_BUTTON, SEPARATOR } from '../../../../types/ActionBarType';

export interface ToolSetBarProps {
  tools: {
    id?: Key;
    intlId: string;
    onClickHandler: () => void;
    iconUrl: string;
    enabled: boolean;
    type?: typeof TOGGLE_BUTTON | typeof SEPARATOR;
    intlIdAlternative?: string;
    iconUrlAlternative?: string;
    onClickHandlerAlternative?: () => void;
    showAlternative?: boolean;
  }[];
}

const ToolSetBar = ({ tools }: ToolSetBarProps) => {
  const intl = useIntl();

  return (
    <div className="toolset-bar-wrapper">
      {tools &&
        tools.map((tool) => {
          if (tool && tool.type === SEPARATOR) {
            return (
              <div className="toolset-bar-separator" key={`separator_${shortId.generate()}`}>
                <div className="separator"></div>
              </div>
            );
          }
          if (tool && tool.type === TOGGLE_BUTTON) {
            return (
              <ToggleButton
                key={tool.id}
                id={tool.id}
                intlId={tool.intlId}
                iconUrl={tool.iconUrl}
                enabled={tool.enabled}
                onClickHandler={tool.onClickHandler}
                intlIdAlternative={tool.intlIdAlternative}
                iconUrlAlternative={tool.iconUrlAlternative}
                onClickHandlerAlternative={tool.onClickHandlerAlternative}
                showAlternative={tool.showAlternative}
              />
            );
          }

          return (
            <IconButton
              key={tool.intlId}
              tooltip={intl.messages[tool.intlId as keyof typeof intl.messages]}
              onClick={() => tool.onClickHandler()}
              iconUrl={tool.iconUrl}
              enabled={tool.enabled}
            />
          );
        })}
    </div>
  );
};

export default ToolSetBar;
