import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import Protobuf from '../../../../protobuf/Protobuf';
import type Proxy from '../../../../protobuf/Proxy';
import { bytesToString } from '../../common';

export const useIndirectValueObjectName = (inspectionID: number, objectId?: number) => {
  const proxy = useSelector<any>((state) => state?.app?.proxy) as Proxy;

  const [name, setName] = useState('');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!objectId) {
      return;
    }
    setLoading(true);
    proxy.protobuf
      .request(
        Protobuf.SvPb.InspectionCmdRequest.create({
          inspectionID,
          getObjectNameRequest: {
            BeforeType: 4354, //SVObjectTypeEnum.BlobDrawObjectType
            objectId,
          },
        }),
      )
      .then((res: any) => {
        setName(bytesToString(res?.getObjectNameResponse?.name));
      })
      .finally(() => setLoading(false));
  }, [inspectionID, objectId, proxy.protobuf]);

  return { name, loading };
};
