import { CSSProperties, useEffect, useRef } from 'react';
import Prism from 'prismjs';
import 'prismjs/components/prism-sql';
import 'prismjs/plugins/match-braces/prism-match-braces.js';

import 'prismjs/themes/prism-solarizedlight.css';
import 'prismjs/plugins/match-braces/prism-match-braces.css';
import './ReadonlyFormula.scss';

const language = 'sql';

export const ReadonlyFormula = ({
  children,
  style = {},
}: {
  children?: string;
  style?: CSSProperties;
  errorPosition?: number;
}) => {
  const codeRef = useRef<HTMLPreElement>(null);

  useEffect(() => {
    if (codeRef.current && children) {
      Prism.highlightAllUnder(codeRef.current, false);
    }
  }, [children]);

  return (
    <pre
      ref={codeRef}
      style={{
        display: 'inline',
        whiteSpace: 'break-spaces',
        margin: 0,
        position: 'relative',
        background: 'none',
        padding: 0,
        ...style,
      }}
      className={`language-${language} match-braces rainbow-braces`}
    >
      <code style={{ fontFamily: 'inherit', overflow: 'hidden', textOverflow: 'ellipsis' }}>
        {children}
      </code>
      {/* {typeof errorPosition === 'number' && (
        <span className="error-marker" style={{ left: `${7.8 * errorPosition}px` }}>
          _
        </span>
      )} */}
    </pre>
  );
};
