import React, { useState, useRef } from 'react';
import { TextField } from '@material-ui/core';

import SuggestionList from '../SuggestionList';

const Autocomplete = ({ label, fullWidth, value, onChange, variant, size, suggestionList }) => {
  const inputRef = useRef();
  const [show, setShow] = useState(false);
  const [filter, setFilter] = useState('');
  const [currentKey, setCurrentKey] = useState();

  const getFilterText = (value, position) => {
    let filterText = '';
    let currentPosition = position - 1;

    while (currentPosition >= 0 && !filterText.startsWith('$')) {
      if (value.charAt(currentPosition) === ' ') {
        return '';
      }

      filterText = value.charAt(currentPosition) + filterText;
      currentPosition--;
    }

    if (!filterText.startsWith('$')) {
      filterText = '';
    }

    return filterText;
  };

  const onChangeAutocompleteText = (event) => {
    const filterText = getFilterText(event.target.value, inputRef.current.selectionEnd);
    setFilter(filterText);
    setShow(filterText.length > 0);
    onChange(event.target.value);
  };

  const cancelSuggestionList = () => {
    setShow(false);
  };

  const onSelectSuggestion = (name) => {
    setShow(false);
    onChange(
      `${value.slice(0, inputRef.current.selectionEnd - filter.length)}${name}${value.slice(
        inputRef.current.selectionEnd,
        value.length,
      )}`,
    );
  };

  const onKeyDown = (event) => {
    if (event.key === 'ArrowDown' || event.key === 'ArrowUp') {
      event.preventDefault();
    }
    setCurrentKey({ currentKey: event.key });
  };

  return (
    <div className="autocomplete-wrapper">
      <TextField
        inputRef={inputRef}
        label={label}
        fullWidth={fullWidth}
        value={value}
        onChange={(e) => onChangeAutocompleteText(e)}
        onKeyDown={(e) => onKeyDown(e)}
        variant={variant}
        size={size}
        inputProps={{
          style: {
            height: 37,
          },
        }}
      />
      <SuggestionList
        show={show}
        currentKey={currentKey}
        filter={filter}
        items={suggestionList}
        onSelect={(name) => onSelectSuggestion(name)}
        onCancel={() => cancelSuggestionList()}
      />
    </div>
  );
};

export default Autocomplete;
