import React from 'react';
import classNames from 'classnames';

import { useIntl } from 'react-intl';

import closeIcon from '../../assets/icons/close.svg';

import ActionBar, { ActionBarType } from '../ActionBar';
import IconButton from '../buttons/IconButton';

import './style.scss';

interface DialogProps {
  open?: boolean;
  isInView?: boolean;
  dialogActions?: ActionBarType[];
  title?: React.ReactNode;
  onClose?: () => void;
  isFullscreen?: boolean;
  isPositionAbsolute?: boolean;
  isPopup?: boolean;
}

const Dialog: React.FC<React.PropsWithChildren<DialogProps>> = ({
  children,
  open = false,
  isInView = false,
  isFullscreen = false,
  isPopup = false,
  isPositionAbsolute,
  title,
  dialogActions,
  onClose,
}) => {
  const intl = useIntl();

  const dialogClass = classNames({
    dialog: true,
    'dialog--open': open,
    'is-in-view': isInView,
    'is-position-absolute': isPositionAbsolute,
  });
  const dialogWrapperClass = classNames({
    'dialog-wrapper': true,
    'is-height-100': isInView || isFullscreen,
    'is-width-100': isFullscreen,
    isPopup: isPopup,
  });

  const dialogContentWrapperClass = classNames({
    'dialog-content-wrapper': true,
    'is-padding-0': isInView,
    isPopup: isPopup,
  });

  if (!open) {
    return null;
  }
  return (
    <div className={dialogClass}>
      <div className={dialogWrapperClass}>
        <div className="dialog-toolbar">
          <div className="dialog-title">{title}</div>
          <div className="dialog-controll-wrapper">
            <IconButton
              tooltip={intl.messages['viewSetManager.close']}
              iconUrl={closeIcon}
              onClick={onClose}
            />
          </div>
        </div>
        <div className={dialogContentWrapperClass}>{children}</div>
        <div className="dialog-action-wrapper">
          {dialogActions && <ActionBar isViewAction actions={dialogActions} />}
        </div>
      </div>
    </div>
  );
};

export default Dialog;
