import React, { useState } from 'react';
import { Typography } from '@material-ui/core';
import addIcon from '../../../../assets/icons/plus.svg';
import { OutlinedSelection } from '../../../OutlinedSelection';
import IconButton from '../../../buttons/IconButton';
import ImageForm from '../ImageForm';
import PropertyForm from '../PropertyForm';

export const COMPONENT_TYPE_TREE = 'tree';
export const COMPONENT_TYPE_AUTOCOMPLETE = 'autocomplete';

const fieldsForTreeForValueSelection = [
  'data1',
  'data2',
  'data3',
  'data4',
  'data5',
  'data6',
  'data7',
  'data8',
  'data9',
  'data10',
  'data11',
  'data12',
  'data13',
  'data14',
  'data15',
  'data16',
  'data17',
  'data18',
  'data19',
];
const triggerPropertiesRequiresName = [
  ...fieldsForTreeForValueSelection,
  ...['image1', 'image2', 'image3', 'image4'],
];
const triggerProperties = [
  ...['', 'storeCondition', 'revolution'],
  ...triggerPropertiesRequiresName,
];

const informationToFieldsMap = {};

triggerProperties.forEach((key) => {
  informationToFieldsMap[key] = (
    <>
      <Typography gutterBottom>
        Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in,
        egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.
      </Typography>
      <Typography gutterBottom>
        Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus
        vel augue laoreet rutrum faucibus dolor auctor.
      </Typography>
      <Typography gutterBottom>
        Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel
        scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus
        auctor fringilla.
      </Typography>
    </>
  );
});

const TriggerSettings = ({
  subscription,
  allowedVariables,
  allowedAutoCompleteVariables,
  allowedImageVariables,
  allowedImagePathVariables,
}) => {
  const [newTriggerProperty, setNewTriggerProperty] = useState('');
  const [update, setUpdate] = useState(false);

  const addNewTriggerProperty = (subscription) => {
    if (!subscription.trigger || newTriggerProperty === '') {
      subscription.trigger = {};
    }

    subscription.trigger[newTriggerProperty] = { name: '', value: '', enabled: true };
    setNewTriggerProperty('');
  };

  const { trigger, inspection } = subscription;
  const triggerResult = [];

  const deleteProperty = (key) => {
    delete trigger[key];
    setUpdate(!update);
  };

  const remainingTriggerProperties = triggerProperties.filter(
    (x) => !Object.keys(trigger).includes(x),
  );

  Object.keys(trigger).forEach((key) => {
    if (key.startsWith('image')) {
      triggerResult.push(
        <ImageForm
          key={`trigger_${inspection}_${key}`}
          inspection={inspection}
          id={key}
          object={trigger}
          propertyName={key}
          allowedImageVariables={allowedImageVariables}
          allowedVariables={allowedImagePathVariables}
          onDelete={() => deleteProperty(key)}
          informationText={informationToFieldsMap[key]}
          treeStyle={{ overflow: 'scroll', height: '250px' }}
        />,
      );
    } else {
      triggerResult.push(
        <PropertyForm
          inspection={inspection}
          key={`trigger_${inspection}_${key}`}
          informationText={informationToFieldsMap[key]}
          id={key}
          object={trigger}
          propertyName={key}
          allowedVariables={
            fieldsForTreeForValueSelection.includes(key)
              ? allowedVariables
              : allowedAutoCompleteVariables
          }
          onDelete={() => deleteProperty(key)}
          excludedFields={!triggerPropertiesRequiresName.includes(key) ? ['name'] : []}
          valueFieldType={
            fieldsForTreeForValueSelection.includes(key)
              ? COMPONENT_TYPE_TREE
              : COMPONENT_TYPE_AUTOCOMPLETE
          }
        />,
      );
    }
  });

  return (
    <div className="properties content">
      {triggerResult}
      <div className="field-name-type">
        <div className="item">Add new property:</div>
        <div className="inputs">
          <div className="field-name-type">
            <div style={{ width: '200px', padding: '5px 0' }}>
              <OutlinedSelection
                selectedValue={newTriggerProperty}
                values={remainingTriggerProperties}
                onSelectionChanged={(e) => setNewTriggerProperty(e.addtriggerProp)}
                name="Property"
                id="addtriggerProp"
              />
            </div>
            <div className="item" style={{ margin: 'auto' }}>
              <IconButton
                tooltip="Add property"
                onClick={() => addNewTriggerProperty(subscription)}
                iconUrl={addIcon}
                style={{ margin: '0' }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TriggerSettings;
