import { useEffect, useState } from 'react';
import { CircularProgress } from '@material-ui/core';
import { useIntl } from 'react-intl';
import EditIcon from '../../../assets/icons/edit.svg';
import IconButton from '../../../components/buttons/IconButton';
import { CommonMathToolProps } from '../common';
import { FormulaEditPanel } from './FormulaEditPanel';
import { ReadonlyFormula } from './ReadonlyFormula';
import { EquationValue } from './hooks/useFormula';
import { useFormulaValidate } from './hooks/useFormulaValidate';

import './FormulaTab.scss';

export const FormulaTab = ({
  inspectionID,
  value,
  onChange,
  toggleOptions,
  loading,
  conditional,
}: CommonMathToolProps<EquationValue | null> & { conditional?: boolean }) => {
  const intl = useIntl();

  const [openFormulaEditPanel, setOpenFormulaEditPanel] = useState(false);

  useEffect(() => {
    toggleOptions(!openFormulaEditPanel);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [openFormulaEditPanel]);

  const {
    validate,
    validating,
    validatedFormula,
    validationErrorMessages,
    validationSuccessResult,
  } = useFormulaValidate(inspectionID, value?.equationObjectID ?? 0);

  useEffect(() => {
    if (value?.equationText) {
      validate(value.equationText);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [value?.equationText]);

  return (
    <div className="tab-body">
      {loading || !value ? (
        <CircularProgress />
      ) : openFormulaEditPanel ? (
        <FormulaEditPanel
          conditional={conditional}
          inspectionID={inspectionID}
          equationId={value.equationObjectID}
          equationText={value.equationText}
          onClose={() => setOpenFormulaEditPanel(false)}
          onSave={(equationText) => {
            onChange({
              ...value,
              equationText,
            });
            setOpenFormulaEditPanel(false);
          }}
        />
      ) : (
        <div className="formula-tab">
          <label>{intl.messages['toolsetView.value']}:</label>
          <span>
            {validating
              ? intl.messages['toolsetView.validating']
              : validatedFormula && (
                  <>
                    {validationSuccessResult !== null &&
                      (conditional
                        ? validationSuccessResult
                          ? 'TRUE'
                          : 'FALSE'
                        : validationSuccessResult)}
                    {validationErrorMessages?.length > 0 && (
                      <span>
                        <span>{intl.messages['toolsetView.validationError']}.</span>&ensp;
                        <span className="validation-error-info">
                          {validationErrorMessages.join('. ')}
                        </span>
                      </span>
                    )}
                  </>
                )}
          </span>
          <label>{intl.messages['toolsetView.formula']}:</label>
          <span
            style={{
              display: 'flex',
              alignItems: 'flex-start',
              flexWrap: 'wrap',
              overflow: 'hidden',
            }}
          >
            <ReadonlyFormula style={{ overflow: 'hidden', textOverflow: 'ellipsis' }}>
              {value.equationText}
            </ReadonlyFormula>
            <IconButton
              onClick={() => setOpenFormulaEditPanel(true)}
              style={{
                height: 20,
                width: 20,
                marginLeft: 8,
              }}
              iconUrl={EditIcon}
              tooltip={intl.messages['inputToolView.edit']}
            />
          </span>
        </div>
      )}
    </div>
  );
};
