import { useMemo } from 'react';
import { Input, Slider } from '@material-ui/core';
import clsx from 'clsx';
import { CommonEditorProps } from './common';
import './ColorEditor.scss';

interface RGBA {
  r: number;
  g: number;
  b: number;
  a: number;
}

function int2rgba(value: number): RGBA {
  return {
    r: (value >> 24) & 0xff,
    g: (value >> 16) & 0xff,
    b: (value >> 8) & 0xff,
    a: value & 0xff,
  };
}

function rgba2int(color: RGBA): number {
  return (
    ((color.r & 0xff) << 24) | ((color.g & 0xff) << 16) | ((color.b & 0xff) << 8) | (color.a & 0xff)
  );
}

function rgbToHex(color: RGBA) {
  return (
    '#' +
    `0${color.r.toString(16)}`.slice(-2) +
    `0${color.g.toString(16)}`.slice(-2) +
    `0${color.b.toString(16)}`.slice(-2)
  );
}

function hexToRgb(hex: string) {
  return {
    r: parseInt(hex[1] + hex[2], 16),
    g: parseInt(hex[3] + hex[4], 16),
    b: parseInt(hex[5] + hex[6], 16),
  };
}

interface ChannelValueProps {
  label: string;
  value: number;
  onChange: (value: number) => void;
}

const ChannelValue = ({ label, value, onChange }: ChannelValueProps) => {
  return (
    <div className="channel-value">
      <label>{label}:</label>
      <div className="input-container">
        <Input
          type="number"
          value={value}
          onChange={(event) => {
            const parsedValue = parseInt(event.target.value);
            if (!isNaN(parsedValue)) {
              onChange(parsedValue);
            }
          }}
        />
      </div>
      <div className="slider-widget-container">
        <Slider
          color="primary"
          value={value || 0}
          onChange={(_, value) => onChange((Array.isArray(value) ? value[0] : value) || 0)}
          valueLabelDisplay="auto"
          min={0}
          max={255}
          step={1}
        />
      </div>
    </div>
  );
};

export const ColorEditor = ({ value, onChange }: CommonEditorProps<number>) => {
  const rgba = useMemo(() => int2rgba(value), [value]);

  const handleUpdate = (color: RGBA) => {
    onChange(rgba2int(color));
  };

  return (
    <div className={clsx('color-editor')}>
      <div>
        <ChannelValue
          label="Red"
          value={rgba.r}
          onChange={(value) => handleUpdate({ ...rgba, r: value })}
        />
        <ChannelValue
          label="Green"
          value={rgba.g}
          onChange={(value) => handleUpdate({ ...rgba, g: value })}
        />
        <ChannelValue
          label="Blue"
          value={rgba.b}
          onChange={(value) => handleUpdate({ ...rgba, b: value })}
        />
        <ChannelValue
          label="Alpha"
          value={rgba.a}
          onChange={(value) => handleUpdate({ ...rgba, a: value })}
        />
      </div>
      <Input
        className="color-picker"
        type="color"
        onChange={(event) => handleUpdate({ ...rgba, ...hexToRgb(event.target.value) })}
        value={rgbToHex(rgba)}
      />
    </div>
  );
};
