import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import Protobuf from '../../../../protobuf/Protobuf';
import type Proxy from '../../../../protobuf/Proxy';
import logger from '../../../../utils/logger';
import { CommonHookProps, EmbeddedIdEnum, EmbeddedValue, VARENUM } from '../../common';

export const useComment = ({ inspectionID, data }: CommonHookProps) => {
  const proxy = useSelector<any>((state) => state?.app?.proxy) as Proxy;

  const [loading, setLoading] = useState(false);
  const [initialValue, setInitialValue] = useState('');
  const [value, setValue] = useState('');
  const [embeddedValueObjectID, setEmbeddedValueObjectID] = useState<number | undefined>(undefined);

  const modified = value !== initialValue;

  useEffect(() => {
    const request = Protobuf.SvPb.InspectionCmdRequest.create({
      inspectionID,
      getEmbeddedValuesRequest: {
        objectId: parseInt(data.objectID ?? ''),
      },
    });
    setLoading(true);
    proxy.protobuf
      .request(request)
      .then((res: any) => {
        const embeddedValues: EmbeddedValue[] = res?.getEmbeddedValuesResponse?.list ?? [];
        const value = embeddedValues.find(
          (entry) => entry.embeddedId === EmbeddedIdEnum.ToolCommentTypeEId,
        );
        setEmbeddedValueObjectID(value?.objectId);
        const commentValue =
          value?.value?.value?.strVal ?? value?.value?.defaultValue?.strVal ?? '';
        setInitialValue(commentValue);
        setValue(commentValue);
      })
      .catch(logger.error)
      .finally(() => setLoading(false));
  }, [data.objectID, inspectionID, proxy.protobuf]);

  const saveChanges = useCallback(async () => {
    if (!modified) {
      return [];
    }
    const res = await proxy.protobuf.request(
      Protobuf.SvPb.InspectionCmdRequest.create({
        inspectionID,
        setEmbeddedValuesRequest: {
          objectId: parseInt(data.objectID ?? ''),
          list: [
            {
              values: {
                objectId: embeddedValueObjectID,
                embeddedId: EmbeddedIdEnum.ToolCommentTypeEId,
                value: {
                  defaultValue: { type: VARENUM.VT_BSTR, strVal: '' },
                  value: { type: VARENUM.VT_BSTR, strVal: value.trim() },
                },
              } as EmbeddedValue,
              isModified: true,
            },
          ],
        },
      }),
    );
    const errors: string[] = res?.standardResponse?.errorMessages?.messages?.map(
      (msg: any) => msg.messageText,
    );
    if (!errors?.length) {
      setInitialValue(value);
    }
    return errors ?? [];
  }, [data.objectID, embeddedValueObjectID, inspectionID, modified, proxy.protobuf, value]);

  return { value, setValue, modified, loading, saveChanges };
};
