import { Component } from 'react';
import { Skeleton } from '@material-ui/lab';
import PropTypes from 'prop-types';
import { FormattedMessage, injectIntl } from 'react-intl';
import { connect } from 'react-redux';

import KoerberLogo from '../../assets/logo/logo.svg';
import Dialog from '../../components/Dialog';
import Logo from '../../components/layout/Logo';

import './style.scss';

class AboutView extends Component {
  render() {
    const { onClose, open, intl, configuration } = this.props;

    if (!configuration) {
      return <div />;
    }

    return (
      <Dialog title={<FormattedMessage id="aboutView" />} onClose={onClose} open={open}>
        <div className="appname">{process.env.REACT_APP_NAME}</div>
        <div className="table">
          <div className="row">
            <div className="cell">{intl.messages['aboutView.applicationVersion']}</div>
            <div className="cell">
              {configuration.getWebAppVersion() || <Skeleton width="155px" />}
            </div>
          </div>
          <div className="row">
            <div className="cell">{intl.messages['aboutView.svoVersion']}</div>
            <div className="cell">
              {configuration.getSvObserverVersion() || <Skeleton width="155px" />}
            </div>
          </div>
          <div className="row">
            <div className="cell">{intl.messages['aboutView.gatewayVersion']}</div>
            <div className="cell">
              {configuration.getGatewayVersion() || <Skeleton width="155px" />}
            </div>
          </div>
        </div>
        <div className="logo">
          <Logo src={KoerberLogo} title="logo" />
        </div>
      </Dialog>
    );
  }
}

AboutView.defaultProps = {
  open: false,
};

AboutView.propTypes = {
  open: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
  try {
    const { configuration } = state.app;
    return { configuration };
  } catch (error) {
    return {};
  }
};

export default connect(mapStateToProps)(injectIntl(AboutView));
