import React, { useRef, useEffect } from 'react';
import { Paper } from '@material-ui/core';

import './style.scss';

const usePrevious = (value) => {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
};

let selectedItem = 0;

const SuggestionList = ({ items, onSelect, show, currentKey, filter, onCancel }) => {
  const prevFilter = usePrevious(filter);

  if (!show) {
    return <div></div>;
  }

  if (filter !== prevFilter) {
    selectedItem = 0;
  }

  const consumeChars = (str, chars, positions) => {
    str = str.toLowerCase();
    let prevIdx = 0;
    while (chars.length) {
      const idx = str.indexOf(chars[0], prevIdx);
      if (idx === -1) {
        break;
      }
      positions.push(idx);
      chars = chars.slice(1);
      prevIdx = idx + 1;
    }
    return chars;
  };

  const highlightAtPositions = (word, positions) => {
    let result = [];
    for (let i = 0; i < word.length; i++) {
      if (positions.includes(i)) {
        result.push([
          <span className="filteredText" key={`suggestionlist_highlight_span_${word}_${i}`}>
            {word.charAt(i)}
          </span>,
        ]);
      } else {
        result.push([word.charAt(i)]);
      }
    }
    return <div key={`suggestionlist_highlight_div_${word}`}>{result}</div>;
  };

  const filteredItems = [];
  (items || []).forEach((item) => {
    const pos = [];
    const remainingChars = consumeChars(item, filter.toLowerCase().split(''), pos);

    if (remainingChars.length === 0) {
      filteredItems.push({
        text: item,
        textWithHighlight: highlightAtPositions(item, pos),
      });
    }
  });

  if (filteredItems.length === 0) {
    return <div></div>;
  }

  if (currentKey.currentKey === 'ArrowUp' && selectedItem > 0) {
    selectedItem--;
  }

  if (currentKey.currentKey === 'ArrowDown' && selectedItem < filteredItems.length - 1) {
    selectedItem++;
  }

  const menuitems = [];
  let currentText = '';
  (filteredItems || []).forEach((item, idx) => {
    const isSelected = selectedItem === idx;
    if (isSelected) {
      currentText = item.text;
    }

    menuitems.push(
      <li
        key={`suggestionlist_li_${item.text}`}
        onClick={() => onSelect(item.text)}
        className={isSelected ? 'suggestionlist-selected' : ''}
      >
        {item.textWithHighlight}
      </li>,
    );
  });

  if (currentKey.currentKey === 'Enter') {
    onSelect(currentText);
  }

  if (currentKey.currentKey === 'Escape') {
    onCancel();
  }

  return (
    <Paper
      className="suggestionlist-wrapper"
      style={show ? { display: 'block' } : { display: 'none' }}
    >
      <ul className="suggestionlist-list">{menuitems}</ul>
    </Paper>
  );
};

export default SuggestionList;
