import { Key, memo, useState } from 'react';
import { TreeNodeSchema } from '../../components/ObjectSelector';
import { ToolListSelector } from './components/ToolListSelector';
import { ValueEditor } from './components/ValueEditor';

import './style.scss';

interface InputToolViewProps {
  inspectionId: string;
}

const InputToolView = memo(({ inspectionId }: InputToolViewProps) => {
  const [selectedNode, setSelectedNode] = useState<TreeNodeSchema | null>(null);
  const [expandedKeys, setExpandedKeys] = useState<Key[]>(['Tool Set']);

  return (
    <div className="input-tool">
      {selectedNode ? (
        <ValueEditor
          inspectionId={inspectionId}
          data={selectedNode}
          onExit={() => setSelectedNode(null)}
        />
      ) : (
        <ToolListSelector
          {...{ expandedKeys, setExpandedKeys, inspectionId }}
          onSelect={setSelectedNode}
        />
      )}
    </div>
  );
});

export default InputToolView;
