import { useCallback, useEffect, useMemo, useState } from 'react';
import Checkbox from '@material-ui/core/Checkbox';

import Table, { AutoResizer, CallOrReturn, ColumnShape } from 'react-base-table';

import { OverlaySchema, updateOverlayList } from '../../utils/reduxHelper';

import 'react-base-table/styles.css';
import './style.scss';

interface OverlayListProps {
  overlays: OverlaySchema[];
  updateCurrentOverlay: (overlay: OverlaySchema) => void;
  updateOverlayList: (overlays: OverlaySchema[]) => void;
  onSelectAll: (selectAll: boolean) => void;
  hasActionBar?: boolean;
  canSelectRow?: boolean;
}

export const OverlayList = ({
  overlays,
  onSelectAll,
  updateCurrentOverlay,
  updateOverlayList: externalUpdateOverlayList,
  canSelectRow,
  hasActionBar = true,
}: OverlayListProps) => {
  const [selectAll, setSelectAll] = useState(false);
  const [selectedRowIndex, setSelectedRowIndex] = useState(-1);

  useEffect(() => {
    setSelectedRowIndex(canSelectRow ? 0 : -1);
  }, [canSelectRow]);

  useEffect(() => {
    setSelectAll(() => {
      for (const overlay of overlays || []) {
        if (!overlay.isActive) {
          return false;
        }
      }
      return true;
    });
  }, [overlays]);

  const headerRenderer: CallOrReturn<
    React.ReactNode,
    {
      cells: React.ReactNode[];
      columns: ColumnShape<unknown>;
      headerIndex: number;
    }
  > = useCallback(
    ({ columns }) => {
      const handleSelectAll = (checked: boolean) => {
        setSelectAll(checked);
        onSelectAll(checked);
      };

      const columnHeaders: JSX.Element[] = [];
      columns.forEach((_: never, columnIndex: number) => {
        if (columnIndex === 0) {
          columnHeaders.push(
            <div
              className="base-table-header-column"
              style={{ width: columns[columnIndex].width }}
              key={`header_${columnIndex}`}
            >
              <Checkbox
                checked={selectAll}
                onChange={(e) => handleSelectAll(e.target.checked)}
                style={{
                  padding: 0,
                }}
                color="primary"
                key="select-all-checkbox"
              />
            </div>,
          );
        } else {
          columnHeaders.push(
            <div
              className="base-table-header-column"
              key={`header_${columnIndex}`}
              style={{ padding: ' 0 7.5px' }}
            >
              {columns[columnIndex].title}
            </div>,
          );
        }
      });

      return columnHeaders;
    },
    [onSelectAll, selectAll],
  );

  const tableDataFromOverlays = useMemo(() => {
    const handleSingleSelect = (checked: boolean, overlay: OverlaySchema) => {
      const updatedOverlay = { ...overlay, isActive: checked };
      const updatedOverlayList = updateOverlayList(updatedOverlay, overlays);

      externalUpdateOverlayList(updatedOverlayList);
    };

    const getCheckboxDefinition4Cell = (currentId: number) => {
      const overlay = overlays.filter((item) => item.id === currentId)[0];
      const isActive = overlay ? overlay.isActive : false;
      return (
        <Checkbox
          checked={isActive}
          onChange={(e) => handleSingleSelect(e.target.checked, overlay)}
          style={{
            padding: 0,
          }}
          color="primary"
          key={`checkbox_${currentId}`}
        />
      );
    };

    const data =
      overlays &&
      overlays.map((overlay) => {
        return {
          checkbox: getCheckboxDefinition4Cell(overlay.id),
          name: overlay.label,
          id: overlay.id,
        };
      });
    return data;
  }, [externalUpdateOverlayList, overlays]);

  const columnDefinition4OverlayTable = useMemo(() => {
    return [
      {
        key: 'checkbox',
        dataKey: 'checkbox',
        title: '',
        width: 40,
      },
      {
        key: 'name',
        dataKey: 'name',
        title: 'Overlays',
        width: 0,
        flexGrow: 1,
      },
    ];
  }, []);

  if (!overlays) return null;

  return (
    <AutoResizer key="autoresizer">
      {({ width, height }) => (
        <Table
          headerRenderer={headerRenderer}
          columns={columnDefinition4OverlayTable}
          data={tableDataFromOverlays}
          width={width}
          height={hasActionBar ? height - 30 : height}
          headerHeight={30}
          rowHeight={30}
          key="overlay-table"
          rowEventHandlers={
            canSelectRow
              ? {
                  onClick: ({ rowIndex }) => {
                    setSelectedRowIndex(rowIndex);
                    updateCurrentOverlay(
                      overlays.filter((item) => item.id === overlays[rowIndex].id)[0],
                    );
                  },
                }
              : undefined
          }
          rowClassName={({ rowIndex }) =>
            selectedRowIndex === rowIndex ? 'overlay-table-selected-row' : ''
          }
        />
      )}
    </AutoResizer>
  );
};
