import { ToolsetTreeNode } from '.';

export function bytesToString(bytes: Uint8Array | undefined, fallback = '') {
  if (!(bytes instanceof Uint8Array)) {
    return fallback;
  }
  return String.fromCharCode(...Array.from(bytes));
}

export function stringToBytes(str = '') {
  return new Uint8Array(str.split('').map((a) => a.charCodeAt(0)));
}

interface Variant {
  type: number;
  count?: number;

  bVal?: boolean;
  lVal?: number;
  llVal?: number;
  ulVal?: number;
  ullVal?: number;
  dblVal?: number;
  fltVal?: number;
  strVal?: string;
  // bytes bytesVal = 11;
}

interface Value {
  value: Variant;
  defaultValue: Variant;
}

export interface LinkedValue extends Value {
  type: LinkedSelectedType;
  directValue?: Variant;
  indirectIDString?: Uint8Array;
  formula?: Uint8Array;
  readonly equationId: number;
}

export interface EmbeddedValue {
  readonly objectId: number;
  embeddedId: EmbeddedIdEnum;
  value?: Value;
  linkedValue?: LinkedValue;
}

export interface CommonHookProps {
  inspectionID: number;
  data: ToolsetTreeNode;
}

export interface CommonMathToolProps<ValueType> {
  inspectionID: number;
  value: ValueType;
  onChange: (value: ValueType) => void;
  loading: boolean;
  toggleOptions: (show: boolean) => void;
}

export enum LinkedSelectedType {
  // None = 0, // not used
  DirectValue = 1,
  IndirectValue = 2,
  Formula = 3,
}

