import React, { Key } from 'react';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import { ClassNameMap } from '@material-ui/styles';
import union from 'lodash/union';
import Tree, { TreeNode } from 'rc-tree';
import { TreeProps } from 'rc-tree/lib/Tree';
import { TreeNodeProps } from 'rc-tree/lib/TreeNode';
import shortId from 'shortid';

import { isObjectValid } from '../../utils/objectValidator';

import './styles.scss';

const styles = () => ({
  label: {
    fontSize: '1.4rem',
    transform: 'translate(10px, 12px) scale(1)',
  },
});

const NUMBER_FILTER_RESULTS = 100;
const NUMBER_OF_SELECTABLE_VALUES = 100;

export interface TreeNodeSchema {
  id?: Key;
  name?: string;
  styledText?: JSX.Element;
  children: TreeNodeSchema[];
  location?: string;
  title?: string;
  halfChecked?: boolean[];
  type?: string;
}

type CheckInfo = { title?: string; node: TreeNodeSchema };

interface TreeContextSchema {
  abortRecursion: boolean;
  expandedFilteredKeys: Key[];
  filteredLocations: string[];
  filteredKeys: Key[];
  countSelection: number;
}

interface ObjectSelectorProps {
  data: TreeNodeSchema;
  selection?: Key[];
  singleSelect?: boolean;
  checkable?: boolean;
  expandedKeys?: Key[];
  onChange?: (ids: Key | Key[], checkedLocation?: string) => void;
  onExpand?: (keys: Key[]) => void;
  classes: Partial<ClassNameMap>;
  filterIgnore?: string[];
}

class ObjectSelector extends React.Component<ObjectSelectorProps> {
  state = {
    expandedKeys: [isObjectValid(() => this.props.data.children?.[0].location)].filter(
      Boolean,
    ) as Key[],
    expandedFilteredKeys: [] as Key[],
    checkedKeys: [] as Key[],
    filteredCheckedKeys: [] as Key[],
    inputValue: '',
    filteredLocations: [] as string[],
    filteredTree: undefined as TreeNodeSchema | undefined,
    countedVisibleValues: 0,
    countedPossibleValues: 0,
    selectionLimitReached: false,
    halfChecked: [] as string[],
  };

  componentDidMount() {
    this.setSelection();
    this.setExpandedKeys();
  }

  componentDidUpdate(prevProps: ObjectSelectorProps) {
    const { selection, expandedKeys } = this.props;

    if (selection && prevProps.selection !== selection && selection.length > 0) {
      this.setSelection();
    }

    if (expandedKeys && prevProps.expandedKeys !== expandedKeys && expandedKeys.length > 0) {
      this.setExpandedKeys();
    }
  }

  setSelection = () => {
    const { selection } = this.props;

    this.setState({
      checkedKeys: selection,
    });
  };

  setExpandedKeys = () => {
    const { expandedKeys } = this.props;
    if (expandedKeys) {
      this.setState({
        expandedKeys: expandedKeys,
      });
    }
  };

  onExpand = (expandedKeys: Key[]) => {
    if (this.state.inputValue) {
      this.setState({
        expandedFilteredKeys: expandedKeys,
      });
    } else {
      this.props.onExpand?.(expandedKeys);
      this.setState({
        expandedKeys,
      });
    }
  };

  onCheck: TreeProps['onCheck'] = (checkedKeys, checkProps) => {
    let limitedCheckedKeys = this.limitCheckedKeys(
      checkedKeys as Key[],
      NUMBER_OF_SELECTABLE_VALUES,
    );
    limitedCheckedKeys = this.checkUnselectingAll(limitedCheckedKeys, checkProps.node as never);

    const filteredCheckedKeys = limitedCheckedKeys.slice();
    if (this.state.inputValue) {
      limitedCheckedKeys = this.updateCheckedKeys(limitedCheckedKeys);
    }

    limitedCheckedKeys = this.ensureSingleSelection(limitedCheckedKeys);

    this.setChangedIds(limitedCheckedKeys, checkProps as never);
    this.setState({
      checkedKeys: limitedCheckedKeys,
      filteredCheckedKeys,
      selectionLimitReached: limitedCheckedKeys.length === NUMBER_OF_SELECTABLE_VALUES,
    });
  };

  limitCheckedKeys = (keys: Key[], limit: number) => {
    const halfChecked = [];
    if (!limit || limit > keys.length) {
      limit = keys.length;
    }
    const result = [];
    let condition = Math.abs(limit);
    let i = 0;
    while (condition !== 0 && i < keys.length) {
      if (parseInt(keys[i] as string, 10)) {
        result.push(keys[i]);
        condition--;
      } else {
        halfChecked.push(keys[i]);
      }

      i++;
    }

    this.setState({ halfChecked });

    return result;
  };

  checkUnselectingAll = (keys: Key[], checkedNode: TreeNodeSchema) => {
    const halfChecked = this.state.halfChecked.slice();
    if (checkedNode.halfChecked) {
      if (halfChecked.includes(checkedNode.title as string)) {
        this.setState({ halfChecked: [] });
        keys = [];
      } else {
        halfChecked.push(checkedNode.title as string);
        this.setState({ halfChecked });
      }
    }
    return keys;
  };

  updateCheckedKeys = (checkedKeys: Key[]) => {
    const newSelectedKeys = checkedKeys.filter(
      (key) => !this.state.filteredCheckedKeys.includes(key),
    );
    const newDeselectedKeys = this.state.filteredCheckedKeys.filter(
      (key) => !checkedKeys.includes(key),
    );
    checkedKeys = union(newSelectedKeys, this.state.checkedKeys);
    checkedKeys = checkedKeys.filter((key) => !newDeselectedKeys.includes(key));

    return checkedKeys;
  };

  ensureSingleSelection = (checkedKeys: Key[]) => {
    if (this.props.singleSelect) {
      return [checkedKeys[checkedKeys.length - 1]] || [];
    } else {
      return checkedKeys;
    }
  };

  setChangedIds = (ids: Key[], checkProps: CheckInfo) => {
    const filteredIds = (ids || []).filter((id) => parseInt(id as string, 10) > 0);
    if (this.props.singleSelect) {
      const checkedLocation = this.getSelectedLocation(checkProps);
      this.props.onChange?.(filteredIds[filteredIds.length - 1] || 0, checkedLocation);
    } else {
      this.props.onChange?.(filteredIds);
    }
  };

  onChangeFilter: React.ChangeEventHandler<HTMLTextAreaElement | HTMLInputElement> = (event) => {
    const { data } = this.props;
    let filteredTree: TreeNodeSchema | undefined = undefined;

    const inputValue = event.target.value || '';
    const filteredInputValue = this.cutFilterIgnoreWords(inputValue.toLowerCase()) || '';
    const initialChars = filteredInputValue.split('');
    const ctx: TreeContextSchema = {
      abortRecursion: false,
      expandedFilteredKeys: [] as Key[],
      filteredLocations: [] as string[],
      filteredKeys: [] as Key[],
      countSelection: 0,
    };
    const countCtx = {
      countMatches: 0,
    };

    if (initialChars.length) {
      const copy = initialChars.slice();
      const children = [] as ReturnType<this['createFilteredShadowNode']>[];
      this.filterNodesRecursively(data, initialChars, children, ctx);
      this.countMatchingNodesRecursively(data, copy, countCtx);
      filteredTree = children.length > 0 ? children[0] : { children: [] as TreeNodeSchema[] };
    }

    const filteredCheckedKeys = (this.state.checkedKeys ?? []).filter((key) =>
      ctx.filteredKeys.includes(key),
    );

    this.setState({
      filteredLocations: ctx.filteredLocations,
      filteredTree,
      filteredCheckedKeys,
      expandedFilteredKeys: ctx.expandedFilteredKeys,
      inputValue,
      countedVisibleValues: ctx.countSelection,
      countedPossibleValues: countCtx.countMatches,
    });
  };

  cutFilterIgnoreWords = (filteredInput: string) => {
    const { filterIgnore } = this.props;

    if (filterIgnore) {
      filterIgnore.forEach((filter) => {
        filter = filter.toLowerCase();
        if (filteredInput.startsWith(filter)) {
          filteredInput = filteredInput.replace(filter, '');

          if (filteredInput.startsWith('.')) {
            filteredInput = filteredInput.slice(1);
          }
        }
      });
    }

    return filteredInput;
  };

  filterNodesRecursively = (
    node: TreeNodeSchema,
    chars: string[],
    shadowChildren: TreeNodeSchema[],
    ctx: TreeContextSchema,
  ) => {
    if (!node) {
      return false;
    }
    const positions: number[] = [];
    let remainingChars: string[] = [];
    if (chars.length > 0) {
      remainingChars = this.consumeChars(node.name as string, chars, positions);
    }

    if (remainingChars.length === 0) {
      ctx.filteredLocations.push(node.location as string);
      if (node.id && node.children.length === 0) {
        ctx.filteredKeys.push(node.id);
      }
    }

    if (ctx.filteredKeys.length >= NUMBER_FILTER_RESULTS) {
      ctx.abortRecursion = true;
    }

    const children: ReturnType<this['createFilteredShadowNode']>[] = [];
    let childMatches = false;
    if (node.children && !ctx.abortRecursion) {
      node.children.forEach((child) => {
        if (
          !ctx.abortRecursion &&
          this.filterNodesRecursively(child, remainingChars, children, ctx)
        ) {
          childMatches = true;
        }
      });
    }

    if (childMatches) {
      ctx.expandedFilteredKeys.push(node.location as string);
    }

    const isMatchingSubtree = remainingChars.length === 0 || childMatches;
    if (isMatchingSubtree) {
      if (node.children.length === 0) {
        ctx.countSelection++;
      }
      const shadowNode = this.createFilteredShadowNode(node, positions, children);
      shadowChildren.push(shadowNode);
    }
    return isMatchingSubtree;
  };

  countMatchingNodesRecursively = (
    node: TreeNodeSchema,
    chars: string[],
    countCtx: { countMatches: number },
  ) => {
    if (!node) {
      return false;
    }
    const positions: number[] = [];
    let remainingChars: string[] = [];
    if (chars.length > 0) {
      remainingChars = this.consumeChars(node.name as string, chars, positions);
    }

    let childMatches = false;
    if (node.children) {
      node.children.forEach((child) => {
        if (this.countMatchingNodesRecursively(child, remainingChars, countCtx)) {
          childMatches = true;
        }
      });
    }

    const isMatchingSubtree = remainingChars.length === 0 || childMatches;
    if (isMatchingSubtree && node.children.length === 0) {
      countCtx.countMatches++;
    }

    return isMatchingSubtree;
  };

  consumeChars = (str: string, chars: string[], positions: number[]) => {
    str = str?.toLowerCase() ?? '';
    let prevIdx = 0;
    while (chars.length) {
      if (chars[0] === '.') {
        chars = chars.slice(1);
        break;
      }

      const idx = str.indexOf(chars[0], prevIdx);
      if (idx === -1) {
        break;
      }
      positions.push(idx);
      chars = chars.slice(1);
      prevIdx = idx + 1;
    }
    return chars;
  };

  createFilteredShadowNode = (
    node: TreeNodeSchema,
    positions: number[],
    children: TreeNodeSchema[],
  ) => {
    const styledText = this.highlightAtPositions(node.name as string, positions);
    const shadowNode: TreeNodeSchema = {
      ...node,
      children,
      styledText,
    };
    return shadowNode;
  };

  highlightAtPositions = (word: string, positions: number[]) => {
    const result = [];
    for (let i = 0; i < word.length; i++) {
      if (positions.includes(i)) {
        result.push([
          <span className="filteredText" key={`strong${shortId.generate()}`}>
            {word.charAt(i)}
          </span>,
        ]);
      } else {
        result.push([word.charAt(i)]);
      }
    }
    return <div key={`div${shortId.generate()}`}>{result}</div>;
  };

  createTreeNodes = (node: TreeNodeSchema, loop: (nodeSchema: TreeNodeSchema) => JSX.Element[]) => {
    const defaultProps: TreeNodeProps = {
      style: {},
      onSelect: () => void 0,
      icon: undefined,
      switcherIcon: undefined,
    };
    if (node.children?.length > 0) {
      return (
        <TreeNode
          // since nodes don't contain a value to subscribe and also don't have a id, we use the location as key
          // we'll need to filter invalid ids from checkedKeys
          {...defaultProps}
          key={node.location}
          title={node.styledText || node.name}
          disableCheckbox={this.props.singleSelect}
        >
          {node.children.map(loop)}
        </TreeNode>
      );
    }
    //TODO: insect this missing location property
    return (
      <TreeNode
        {...defaultProps}
        key={node.id}
        title={node.styledText || node.name} /*location={node.location}*/
      />
    );
  };

  getSelectedLocation = (childProps: CheckInfo) => {
    if (isObjectValid(() => childProps.node.location)) {
      return childProps.node.location as string;
    }
    return '';
  };

  render() {
    const {
      checkedKeys,
      expandedKeys,
      expandedFilteredKeys,
      filteredTree,
      filteredCheckedKeys,
      inputValue,
      countedVisibleValues,
      countedPossibleValues,
      selectionLimitReached,
    } = this.state;
    const loop = (node: TreeNodeSchema) => {
      if (!node) {
        return null;
      }
      return this.createTreeNodes(node, loop);
    };

    const className = inputValue ? 'unfilteredText' : '';
    const expanded = inputValue ? expandedFilteredKeys : expandedKeys;
    const rootNode = inputValue ? filteredTree : this.props.data;
    const visibleCheckedKeys = inputValue ? filteredCheckedKeys : checkedKeys;
    const treeNodes = rootNode?.name
      ? (loop(rootNode) as unknown as JSX.Element[])
      : (rootNode?.children ?? []).map(loop);

    return (
      <div className="object-selector">
        <div className="topmenu-wrapper">
          <div className="w100 text">
            <div className="infotext">
              <div className="pr2">Select objects:</div>
              {selectionLimitReached ? (
                <div className="warning pr2">
                  {`(Selection limit of ${NUMBER_OF_SELECTABLE_VALUES} items reached)`}
                </div>
              ) : undefined}
              {inputValue && treeNodes.length > 0 ? (
                <div className="warning">
                  {`(${countedVisibleValues} of ${countedPossibleValues} matches are shown)`}
                </div>
              ) : undefined}
            </div>
          </div>
          <div className="w100">
            <TextField
              label="Filter"
              type="text"
              variant="outlined"
              style={{ width: '100%' }}
              onChange={this.onChangeFilter}
              value={this.state.inputValue}
              InputLabelProps={{ classes: { root: this.props.classes.label } }}
            />
          </div>
        </div>
        <div>
          {treeNodes.length > 0 ? (
            //@ts-expect-error many missing properties are required and need to be set
            <Tree
              checkable={this.props.checkable ?? true}
              showLine
              autoExpandParent={true}
              defaultExpandParent={true}
              showIcon={false}
              selectable={false}
              onExpand={this.onExpand}
              expandedKeys={expanded}
              checkedKeys={visibleCheckedKeys}
              onCheck={this.onCheck}
              className={className}
            >
              {treeNodes}
            </Tree>
          ) : (
            <div className="text">No filter results</div>
          )}
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(ObjectSelector);
