import React, { useState } from 'react';
import { Box } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import deleteIcon from '../../../../assets/icons/delete.svg';
import AutoComplete from '../../../AutoComplete';
import ObjectSelector from '../../../ObjectSelector';
import { OutlinedSelection } from '../../../OutlinedSelection';
import IconButton from '../../../buttons/IconButton';
import InformationBox from '../InformationBox';

const DEFAULT_SAMPLING_RATE = 0.1;
const MAX_SAMPLING_RATE = 1.0;
export const treeEntityNameGenerator = (searchedNodeId, tree, inspectionName) => {
  const queue = (tree && tree.children && tree.children.slice(0)) || [];
  while (queue.length > 0) {
    const head = queue.shift();
    const { id, location, children } = head;
    if (id && searchedNodeId === id.toString()) {
      return 'Inspections.' + inspectionName + '.' + location;
    }
    if (children && children.length) {
      for (let i = 0; i < children.length; ++i) {
        queue.push(children[i]);
      }
    }
  }
};

const ImageForm = ({
  id,
  object,
  propertyName,
  allowedImageVariables,
  allowedPathVariables,
  onDelete,
  inspection,
  informationText = <></>,
  treeStyle = {},
}) => {
  const [treeNodeId, setTreeNodeId] = useState(object[propertyName].treeNodeId || 0);
  const [path, setPath] = useState(object[propertyName].path || '');
  const [samplingRate, setSamplingRate] = useState(
    object[propertyName].samplingRate || DEFAULT_SAMPLING_RATE,
  );
  const [enabled, setEnabled] = useState(
    (typeof object[propertyName] === 'object' && object[propertyName].enabled) || true,
  );

  return (
    <div className="field-name-type">
      <div className="item">
        <div className="property-id">{id}</div>
      </div>
      <div className="inputs">
        <div className="field-name-type">
          <Box sx={{ mr: 1 }}>
            {allowedImageVariables && (
              <ObjectSelector
                singleSelect
                selection={[treeNodeId]}
                expandedKeys={[treeNodeId]}
                data={allowedImageVariables}
                onChange={(selectedId) => {
                  // keep id of this node in JSON to find it easily when we load this JSON back
                  object[propertyName].treeNodeId = selectedId;
                  const fullLocation = treeEntityNameGenerator(
                    selectedId,
                    allowedImageVariables,
                    inspection,
                  );
                  object[propertyName].image = '${IMAGE:' + fullLocation + '}';
                  setTreeNodeId(selectedId);
                }}
                treeStyle={treeStyle}
              />
            )}
          </Box>
          <Box sx={{ mr: 1 }}>
            <AutoComplete
              label="Path"
              fullWidth
              value={path}
              onChange={(value) => {
                setPath(value);
                object[propertyName].value = value;
              }}
              variant="outlined"
              size="small"
              suggestionList={allowedPathVariables}
            />
          </Box>
          <Box sx={{ mr: 1 }}>
            <OutlinedSelection
              selectedValue={enabled.toString()}
              values={['true', 'false']}
              onSelectionChanged={(e) => setEnabled(e.enabled === 'true')}
              id="enabled"
              name="Enabled"
            />
          </Box>
          <Box>
            <TextField
              fullWidth
              variant="outlined"
              label="Sampling rate"
              InputProps={{
                inputProps: {
                  type: 'number',
                  min: 0.1,
                  max: MAX_SAMPLING_RATE,
                  step: 0.1,
                },
                style: {
                  height: 29,
                },
              }}
              className="item"
              type="number"
              required={true}
              value={samplingRate || DEFAULT_SAMPLING_RATE}
              onChange={(e) => setSamplingRate(e.target.value)}
              onBlur={(e) => (object[propertyName].value = e.target.value)}
            />
          </Box>
          <div>
            <IconButton tooltip="Add property" onClick={() => onDelete()} iconUrl={deleteIcon} />
          </div>
          {informationText && <InformationBox informationText={informationText} />}
        </div>
      </div>
    </div>
  );
};

export default ImageForm;
