import { useEffect, useRef, useState } from 'react';
import { Select, OutlinedInput, InputLabel, FormControl, makeStyles } from '@material-ui/core';

const useStyles = makeStyles(() => ({
  input: {
    height: 37,
    fontSize: '1.4rem',
  },
  select: {
    paddingTop: 0,
    paddingBottom: 0,
    fontSize: '1.4rem',
  },
  label: {
    transform: 'translate(14px, 11px) scale(1)',
    fontSize: '1.4rem',
  },
}));

interface OutlinedSelectionProps {
  id?: string;
  name?: string;
  labels?: string[];
  values: string[];
  selectedValue: any;
  onSelectionChanged?: (selection: Record<string, unknown>) => void;
  disabled?: boolean;
}

export const OutlinedSelection = ({
  values,
  labels,
  selectedValue,
  name = '',
  id = '',
  onSelectionChanged,
  disabled,
}: OutlinedSelectionProps) => {
  const classes = useStyles();
  const inputLabelRef = useRef<HTMLLabelElement>(null);

  const [labelWidth, setLabelWidth] = useState(0);

  useEffect(() => {
    if (inputLabelRef.current) {
      setLabelWidth(inputLabelRef.current.offsetWidth);
    }
  }, []);

  return (
    <FormControl variant="outlined" style={{ width: '100%' }} disabled={disabled}>
      <InputLabel htmlFor={id} ref={inputLabelRef} classes={{ outlined: classes.label }}>
        {name}
      </InputLabel>
      <Select
        native
        value={selectedValue || ''}
        onChange={(e) => onSelectionChanged?.({ [id]: e.target.value })}
        classes={{ root: classes.input }}
        input={
          <OutlinedInput
            name={name}
            labelWidth={labelWidth}
            id={id}
            classes={{ input: classes.select }}
          />
        }
      >
        {!disabled &&
          values.map((name, counter) => {
            return (
              <option value={name} key={id + counter}>
                {(labels || values)[counter]}
              </option>
            );
          })}
      </Select>
    </FormControl>
  );
};
